import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { useWeb3React } from 'web3-react-core'
import { BigNumber } from '@ethersproject/bignumber'
import { getContract, getSigner, isSupportedChainId } from 'utils/web3React'
import contracts from 'config/constants/contracts'
import tokens from 'config/constants/tokens'
import { tokensSymbol } from 'config/constants/types'
import { useGetTokenBalance } from 'hooks/useManager'
import oracleHub from '../utils/oracleHub'

const SystemParamsContext = React.createContext(null)

const { blockUpdater, manager, matrix, helper, pair, actionMintSell, mintSellHelper } = contracts

function SystemParamsProvider({ children }) {
  const { library, account, chainId, active } = useWeb3React()
  const [signer, setSigner] = useState(null)
  const [refreshParams, setRefreshParams] = useState(false)
  const [accountContract, setAccountContract] = useState(null)
  const [actionMintSellContract, setActionMintSellContract] = useState(null)
  const [matrixContract, setMatrixContract] = useState(null)
  const [mintSellHelpContract, setMintSellHelpContract] = useState(null)
  const [helperContract, setHelperContract] = useState(null)
  const [params, setParams] = useState({})
  const getTokenBalance = useGetTokenBalance(library, active, chainId)
  // eslint-disable-next-line consistent-return
  const estimateDailyReward = useCallback(
    // eslint-disable-next-line consistent-return
    async (balance) => {
      if (signer) {
        const updater = getContract(signer, blockUpdater.abi, blockUpdater.address[chainId])
        if (updater) {
          return updater.estimatedDailyReward(balance)
        }
      }
    },
    [chainId, signer],
  )
  const getHashpowerParams = useCallback(async () => {
    if (account) {
      const dailyObligation = await estimateDailyReward(
        BigNumber.from(10).mul(BigNumber.from(10).pow(tokens[tokensSymbol.FBTC20].decimals)),
      )
      setParams({
        dailyObligation,
      })
    }
  }, [account, estimateDailyReward])
  const [_pairWBTCAndUSDT, setPairWBTCAndUSDT] = useState<any>('{}')
  const [_pairWBTCAndFBTC, setPairWBTCAndFBTC] = useState<any>('{}')

  const pairWBTCAndUSDT = useMemo(() => JSON.parse(_pairWBTCAndUSDT), [_pairWBTCAndUSDT])
  const pairWBTCAndFBTC = useMemo(() => JSON.parse(_pairWBTCAndFBTC), [_pairWBTCAndFBTC])

  // eslint-disable-next-line consistent-return
  const getWBTC = useCallback(
    (data: { decimals: number | string; amount: any; symbol: string }) => {
      if (data.symbol === 'F(BTC,20)') {
        const { output } = oracleHub({
          inputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
          outputBalance: pairWBTCAndFBTC.WBTC,
          inputAmount: data.amount,
          inputDecimals: data.decimals,
          outputDecimals: tokens.WBTC.decimals,
        })
        return output
      }
      if (data.symbol === 'USDT') {
        return (
          oracleHub({
            inputBalance: pairWBTCAndUSDT.USDT,
            outputBalance: pairWBTCAndUSDT.WBTC,
            inputAmount: data.amount,
            inputDecimals: data.decimals,
            outputDecimals: tokens.WBTC.decimals,
          }).output * 0.9
        )
      }
      return data.amount
    },
    [pairWBTCAndFBTC, pairWBTCAndUSDT],
  )

  // eslint-disable-next-line consistent-return
  const getPairBalance = useCallback(async () => {
    if (getTokenBalance) {
      const data = []
      try {
        const pairBalance = await Promise.all(Object.values(pair).map(async (item) => getTokenBalance(item[chainId])))
        data.push(...pairBalance)
      } catch (error) {
        console.error(error)
      }
      setPairWBTCAndFBTC((prev: any) => {
        if (prev === data[0]) {
          return prev
        }
        return data[0] ? JSON.stringify(data[0]) : '{}'
      })
      setPairWBTCAndUSDT((prev: any) => {
        if (prev === data[1]) {
          return prev
        }
        return data[1] ? JSON.stringify(data[1]) : '{}'
      })
      return data
    }
  }, [getTokenBalance, chainId])

  useEffect(() => {
    let timer
    if (isSupportedChainId(chainId)) {
      getHashpowerParams()
      getPairBalance()
      timer = setInterval(() => {
        getPairBalance()
      }, 5000)
    }
    return () => {
      clearInterval(timer)
    }
  }, [chainId, getHashpowerParams, getPairBalance, refreshParams])

  useEffect(() => {
    if (active && account && chainId && isSupportedChainId(chainId)) {
      const Signer = getSigner(library, account)
      const accContract = getContract(Signer, manager.accountRegister.abi, manager.accountRegister.address[chainId])
      const _matrixContract = getContract(Signer, matrix.abi, matrix.address[chainId])
      const _helper = getContract(Signer, helper.abi, helper.address[chainId])
      const _actionMintSell = getContract(Signer, actionMintSell.abi, actionMintSell.address[chainId])
      const _mintSellHelpContract = getContract(Signer, mintSellHelper.abi, mintSellHelper.address[chainId])
      setSigner(Signer)
      setActionMintSellContract(_actionMintSell)
      setMintSellHelpContract(_mintSellHelpContract)
      setAccountContract(accContract)
      setMatrixContract(_matrixContract)
      setHelperContract(_helper)
    }
  }, [account, active, chainId, library])

  return (
    <SystemParamsContext.Provider
      value={{
        ...params,
        setRefreshParams,
        accountContract,
        matrixContract,
        helperContract,
        actionMintSellContract,
        mintSellHelpContract,
        signer,
        getWBTC,
        pairWBTCAndUSDT,
        pairWBTCAndFBTC,
        setPairWBTCAndUSDT,
        setPairWBTCAndFBTC,
        getPairBalance,
      }}
    >
      {children}
    </SystemParamsContext.Provider>
  )
}

export { SystemParamsProvider, SystemParamsContext }
