import React, { useEffect, useState } from 'react'
import { styled } from '@mui/material/styles'
import Card from 'components/Card'
import { BigNumber } from '@ethersproject/bignumber'
import { formatWBTC } from 'utils/formatTheValue'
import { updataShortRateInfo } from 'state/Short/action'
import { useAppDispatch, useAppSelector } from 'state'
import useHashPowerToken from '../../../hooks/useHashPowerToken'
import LinearIndeterminate from './progress'

const TopCard = styled(Card)`
  margin-bottom: 20px;
  display: flex;
  height: 175px;
  width: 100%;
  box-shadow: 0 4px 27px rgba(0, 0, 0, 0.03);
  backdrop-filter: unset;
`
const CtOne = styled('div')`
  width: 30%;
`
const CtTwo = styled('div')`
  width: 70%;
`
const CtLabel = styled('div')`
  color: rgba(0, 0, 0, 0.5);
`
const CtLabeltwo = styled('div')`
  margin-top: 10px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
`
const CtValue = styled('div')`
  margin-bottom: 14px;
  font-family: PingFangSC-Semibold, sans-serif;
  font-weight: 550;
  font-size: 35px;
  margin-top: 24px;
  line-height: 35px;
  color: #000000;
`
const PtCt = styled('div')`
  display: flex;
`
const PtLabel = styled('div')`
  width: 25%;
`
const PtValue = styled('div')`
  width: 75%;
`
function TopContent() {
  const rateInfo = useAppSelector((state) => state.short.rateInfo)

  const dispatch = useAppDispatch()

  const [linevalue, setLine] = useState({
    marks1: [
      {
        value: 0,
        label: '',
      },
      {
        value: 110,
        label: 'Liquidation',
      },
      {
        value: 120,
        label: 'Min. Mint',
      },
      {
        value: 300,
        label: '',
      },
    ],
    marks: [
      {
        value: 0,
        label: '',
      },
      {
        value: 110,
        label: '110%',
      },
      {
        value: 120,
        label: '120%',
      },
      {
        value: 300,
        label: '',
      },
    ],
  })
  const [linevaluep, setLinep] = useState({
    marks1: [],
    marks: [
      {
        value: 0,
        label: '',
      },
      {
        value: 110,
        label: '110',
      },

      {
        value: 120,
        label: '120',
      },
      {
        value: 300,
        label: '',
      },
    ],
  })

  const { hashPowerTokenList } = useHashPowerToken()

  const [hashPowerListInfoList, setHashPowerTokenListInfo] = useState<any>(hashPowerTokenList || [])
  const [load, setLoad] = useState(false)
  useEffect(() => {
    if (hashPowerTokenList && hashPowerTokenList[0]) {
      setHashPowerTokenListInfo(hashPowerTokenList)
      setLinep((value) => {
        return {
          ...value,
          defaultvalue: Number(coverage(hashPowerTokenList[0])),
          showValue: Number(coverage(hashPowerTokenList[0])),
        }
      })

      setLine((value) => ({
        ...value,
        defaultvalue: Number(roatComp(hashPowerTokenList[0])),
        showValue: Number(roatComp(hashPowerTokenList[0])),
      }))

      dispatch(
        updataShortRateInfo({
          rateInfo: {
            rate: Number(roatComp(hashPowerTokenList[0])),
            coverage: Number(coverage(hashPowerTokenList[0])),
          },
        }),
      )
      setLoad(true)
    }
  }, [dispatch, hashPowerTokenList])

  const roatComp = (item: any): any => {
    if (item && item.positionInfoValues && Object.keys(item.positionInfoValues).length) {
      if (item.positionInfoValues.cRate.includes('NaN')) {
        return 0
      }

      if (item.positionInfoValues.cRate === 'Infinity') {
        return 500
      }
      return item.positionInfoValues.cRate
    }
    return 0
  }

  const coverage = (item: any) => {
    if (item.positionInfoValues && Object.keys(item.positionInfoValues).length && item.positionInfoValues.gDays) {
      if (item.positionInfoValues.gDays === 'Infinity') {
        return 50
      }

      return item.positionInfoValues ? item.positionInfoValues.gDays : 0
    }
    return item.positionInfoValues ? item.positionInfoValues.gDays : 0
  }

  return (
    <>
      {load && rateInfo && (
        <TopCard>
          <CtOne>
            <CtLabel>Outstanding Debt</CtLabel>
            <CtValue>
              {hashPowerListInfoList[0] && hashPowerListInfoList[0].balance
                ? (BigNumber.from(hashPowerListInfoList[0].balance) as any) /
                  (BigNumber.from(10) as any).pow(hashPowerListInfoList[0].decimals)
                : 0}
            </CtValue>
            <CtLabeltwo>
              Mark Price:{' '}
              {Number(hashPowerListInfoList[0].position)
                ? formatWBTC(hashPowerListInfoList[0].tokensValues[hashPowerListInfoList[0].token])
                : 0}{' '}
              WBTC
            </CtLabeltwo>
          </CtOne>
          {load && (
            <CtTwo>
              <PtCt>
                <PtLabel style={{ marginTop: '45px' }}>Collateral Ratio</PtLabel>
                <PtValue>
                  <LinearIndeterminate key={1} value={linevalue} />
                </PtValue>
              </PtCt>
              <PtCt>
                <PtLabel>Prepaid Interest Coverage</PtLabel>
                <PtValue>
                  <LinearIndeterminate key={2} value={linevaluep} />
                </PtValue>
              </PtCt>
            </CtTwo>
          )}
        </TopCard>
      )}
    </>
  )
}

export default TopContent
