import { BigNumber } from '@ethersproject/bignumber'
import tokens from 'config/constants/tokens'
import { utils } from 'ethers'

export function formatCurrency(val) {
  if (val >= 1000000) {
    return `${(val / 1000000).toFixed(1)}M`
  }
  if (val >= 10000) {
    return `${(val / 1000).toFixed(1)}K`
  }
  const resVal = val.toFixed(2)
  // eslint-disable-next-line eqeqeq
  if (resVal == 0) {
    return '0.00'
  }
  return resVal
}

export function formatSnNumber(number) {
  // eslint-disable-next-line no-restricted-globals
  if (isNaN(Number(number))) {
    return number
  }

  if (number.toString().includes('+')) {
    return Number(number).toLocaleString().replace(/,/g, '')
  }
  if (String(number).includes('-')) {
    const e = String(number)
    const rex = /^([0-9]).?([0-9]*)e-([0-9]*)/
    if (!rex.test(e)) return number
    const numArr = e.match(rex)
    const n = Number(`${numArr[1]}${numArr[2] || ''}`)
    // eslint-disable-next-line no-restricted-properties
    const num = `0.${String(Math.pow(10, Number(numArr[3]) - 1)).substr(1)}${n}`
    return num.replace(/0*$/, '')
  }
  return String(number)
}

export default function formatTheValue(val: number | BigNumber | any, unitName?: number) {
  return Number(utils.formatUnits(typeof val === 'number' ? val.toFixed(unitName) : val, unitName))
  // return 0
}

export function toFixed(val, mark?) {
  const value = String(formatSnNumber(val))
  if (mark && mark === 'WBTC' && Number(value) && value.includes('.')) {
    return formatSnNumber(Number(value.slice(0, value.indexOf('.') + 9)))
  }
  if (mark && mark === 'F(BTC,20)' && Number(value) && value.includes('.')) {
    return formatSnNumber(Number(value.slice(0, value.indexOf('.') + 7)))
  }
  if (typeof mark === 'number') {
    return formatSnNumber(Number(value.slice(0, value.indexOf('.') + (mark + 1))))
  }
  if (mark !== 'WBTC' || mark !== 'F(BTC,20)') {
    if (value.includes('.')) {
      return formatSnNumber(Number(value.slice(0, value.indexOf('.') + 3)))
    }
    return val
  }
  return val
}

export function formatWBTC(val) {
  if (val) {
    // @ts-ignore
    return toFixed(formatSnNumber(val / BigNumber.from(10).pow(tokens.WBTC.decimals)), 'WBTC')
  }
  return 0
}

export function toWBTC(val) {
  return formatSnNumber(val * 10 ** 8)
}
export function toHashToken(val) {
  return formatSnNumber(val * 10 ** 6)
}

export function formatInfinity(val) {
  const value = String(val)
  if (value && value.includes('Infinity') && value.includes('-')) {
    return '-'
  }
  if (value && value.includes('Infinity') && !value.includes('-')) {
    return '+'
  }
  return val
}
