/* eslint-disable */
import { useCallback } from 'react'
import useSystemParams from 'hooks/useSystemParams'
import oracleHub from 'utils/oracleHub'
import { tokensSymbol } from 'config/constants/types'
import tokens from 'config/constants/tokens'
import { toFixed } from '../utils/formatTheValue'

export default function useComputedPrice() {
  const { pairWBTCAndUSDT, pairWBTCAndFBTC } = useSystemParams()
  return useCallback(
    (data, value?) => {
      /**
       * data
       * {
       *   inputToken:{
       *     token:?,
       *     decimals:?,
       *
       *   }
       *   outputToken:{
       *     token:?,
       *     decimals:?,
       *   }
       * }
       *
       *
       *
       * */
      if (pairWBTCAndUSDT && pairWBTCAndFBTC) {
        if (
          data.inputToken &&
          data.outputToken &&
          (data.inputToken.token === 'USDT' || data.outputToken.token === 'USDT')
        ) {
          if (data.inputToken.token === 'USDT' && data.outputToken.token === 'WBTC') {
            const priceInfo = oracleHub({
              inputBalance: pairWBTCAndUSDT.USDT,
              outputBalance: pairWBTCAndUSDT.WBTC,
              inputAmount: value,
              inputDecimals: data.inputToken.decimals,
              outputDecimals: data.outputToken.decimals,
            })
            return priceInfo.output
          }
          if (data.inputToken.token === 'WBTC') {
            const priceInfo = oracleHub({
              inputBalance: pairWBTCAndUSDT.WBTC,
              outputBalance: pairWBTCAndUSDT.USDT,
              inputAmount: value,
              inputDecimals: data.inputToken.decimals,
              outputDecimals: data.outputToken.decimals,
            })
            return toFixed(priceInfo.output, 2)
          }
          if (data.inputToken.token === 'F(BTC,20)') {
            const priceInfo1 = oracleHub({
              inputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
              outputBalance: pairWBTCAndFBTC.WBTC,
              inputAmount: value,
              inputDecimals: data.inputToken.decimals,
              outputDecimals: tokens.WBTC.decimals,
            })
            const priceInfo2 = oracleHub({
              inputBalance: pairWBTCAndUSDT.WBTC,
              outputBalance: pairWBTCAndUSDT.USDT,
              inputAmount: priceInfo1.output,
              inputDecimals: tokens.WBTC.decimals,
              outputDecimals: data.outputToken.decimals,
            })
            return toFixed(priceInfo2.output, 2)
          }

          if (data.outputToken.token === 'F(BTC,20)') {
            const priceInfo1 = oracleHub({
              inputBalance: pairWBTCAndUSDT.USDT,
              outputBalance: pairWBTCAndUSDT.WBTC,
              inputAmount: value,
              inputDecimals: data.inputToken.decimals,
              outputDecimals: tokens.WBTC.decimals,
            })
            const priceInfo2 = oracleHub({
              inputBalance: pairWBTCAndFBTC.WBTC,
              outputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
              inputAmount: priceInfo1.output,
              inputDecimals: tokens.WBTC.decimals,
              outputDecimals: data.outputToken.decimals,
            })
            return priceInfo2.output
          }
        } else if (
          data.inputToken &&
          data.outputToken &&
          data.outputToken.token !== 'USDT' &&
          data.inputToken.token !== 'USDT'
        ) {
          if (data.inputToken.token === 'WBTC') {
            const priceInfo = oracleHub({
              inputBalance: pairWBTCAndFBTC.WBTC,
              outputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
              inputAmount: value,
              inputDecimals: data.inputToken.decimals,
              outputDecimals: data.outputToken.decimals,
            })
            return priceInfo.output
          }
          if (data.inputToken.token === tokensSymbol.FBTC20) {
            const priceInfo = oracleHub({
              inputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
              outputBalance: pairWBTCAndFBTC.WBTC,
              inputAmount: value,
              inputDecimals: data.inputToken.decimals,
              outputDecimals: data.outputToken.decimals,
            })
            return priceInfo.output
          }
        }
        // if(data.) {

        // }
      }
    },
    [pairWBTCAndFBTC, pairWBTCAndUSDT],
  )
}
