import React from 'react'
import { withRouter } from 'react-router'
import { Menu as UikitMenu } from 'components/uikit'
import { useWeb3React } from 'web3-react-core'
import { languageList } from 'config/localization/languages'
import { useTranslation } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import useAuth from 'hooks/useAuth'
import config from './config'
import useCurrentBalance from '../../hooks/useCurrentBalance'
import { HashPowerTokenProvider } from '../../contexts'

const whiteRouterList = ['/dashboard', '/faucet']

const Menu = (props) => {
  const { account } = useWeb3React()
  const { login, logout } = useAuth()
  const { isDark, toggleTheme } = useTheme()
  const { currentLanguage, setLanguage } = useTranslation()
  const balance = useCurrentBalance()
  const { location } = props
  return (
    <>
      {location && whiteRouterList.includes(location.pathname) ? (
        <UikitMenu
          account={account}
          login={login}
          logout={logout}
          isDark={isDark}
          toggleTheme={toggleTheme}
          currentLang={currentLanguage.code}
          langs={languageList}
          setLang={setLanguage}
          links={config}
          balance={balance}
          {...props}
        />
      ) : (
        <HashPowerTokenProvider>
          <UikitMenu
            account={account}
            login={login}
            logout={logout}
            isDark={isDark}
            toggleTheme={toggleTheme}
            currentLang={currentLanguage.code}
            langs={languageList}
            setLang={setLanguage}
            links={config}
            balance={balance}
            {...props}
          />
        </HashPowerTokenProvider>
      )}
    </>
  )
}

export default withRouter(Menu)
