import {
  Box,
  Typography,
  Button,
  TableCell,
  TableRow,
  Badge,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableContainer,
  TableHead,
} from '@mui/material'
import React, { useCallback, useEffect, useMemo, useState } from 'react'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { formatWBTC } from 'utils/formatTheValue'
import { useAppDispatch, useAppSelector } from 'state'
import { useCheckApprove } from 'hooks/useApprove'
import { useWeb3React } from 'web3-react-core'
import Wallet from 'components/uikit/widgets/Menu/icons/Wallet'
import tokens from 'config/constants/tokens'
import { AvatarIcon, Fbtc20Icon } from 'components/uikit'
import { useCurrentCpt, useShortActiveAction, useShortPendingAction } from 'state/hooks'
import { BigNumber, utils } from 'ethers'
import ClearIcon from '@mui/icons-material/Clear'
import usePosContract from 'hooks/usePosContract'
import contracts from 'config/constants/contracts'
import useToast from 'hooks/useToast'
import useSystemParams from 'hooks/useSystemParams'
import useComputedPrice from 'hooks/useComputedPrice'
import { TokenType } from 'state/Short/reducer'
import { updateActionInfo } from 'state/Short/action'
import IconMap from 'components/IconMap'
import NeutralizePendingActionsComp from './NeutralizePendingActionsComp'
import { ActionButton, InputBox, SelectTypeWrap, TipInfo, pendingStyle } from './styledComp'
import WarnRedIcon from './icon/WarnRedIcon'
import WarnGrayIcon from './icon/WarnGrayIcon'

const TableBox: React.FC<{
  setNeutralizeType: (val: string) => any
}> = ({ setNeutralizeType }) => {
  const [hoveredRow, setHoveredRow] = useState<number>(-1)

  const { token } = useCurrentCpt()

  const { setActionInfo, setActiveAmount, setActionIndex, clearActive } = useShortActiveAction()

  const { pendingActionList, clearAllPendingAction, deleteNeutralizePendingAction } = useShortPendingAction()
  const handleMouseEnter = (index: number) => {
    setHoveredRow(index)
  }

  const handleMouseLeave = () => {
    setHoveredRow(-1)
  }
  return (
    <TableContainer
      component={Paper}
      sx={{
        backgroundColor: 'transparent',
        border: 'none',
        boxShadow: 'none',
      }}
    >
      <Table aria-label="simple table">
        <TableHead>
          <TableRow sx={pendingStyle.tableHeader}>
            <TableCell sx={pendingStyle.tableHeadCell}>{pendingActionList.length} Pending Actions</TableCell>
            <TableCell align="right" sx={pendingStyle.tableHeadCell}>
              <Button
                size="small"
                sx={pendingStyle.clearAll}
                onClick={() => {
                  clearAllPendingAction()
                }}
              >
                Clear All
              </Button>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody sx={pendingStyle.tableBody}>
          {pendingActionList.map((row: any, index) => (
            <TableRow
              key={row.tokenType + row.token}
              sx={pendingStyle.tableBodyRow}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
              onClick={() => {
                setActionInfo({ token: row.token, tokenType: row.tokenType })
                setActiveAmount(row.amount)
                setActionIndex(index)
                setNeutralizeType(row.neutralizeType)
              }}
            >
              <TableCell sx={pendingStyle.tableBodyCell}>
                <Badge
                  color="error"
                  overlap="circular"
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  sx={{
                    '.MuiBadge-badge': {
                      padding: 0,
                      minWidth: 10,
                      minHeight: 10,
                      height: 10,
                      position: 'absolute',
                      bottom: '20%',
                      right: '24%',
                      backgroundColor: 'rgba(110, 214, 243, 1)',
                      borderWidth: 1,
                      borderColor: 'rgba(235, 247, 255, 1)',
                      borderStyle: 'solid',
                    },
                  }}
                >
                  {IconMap[row.neutralizeType]()}
                </Badge>
                <Typography sx={{ fontWeight: 600, marginLeft: '5px' }}>
                  {row.tokenType} {row.token}
                </Typography>
              </TableCell>
              <TableCell align="right" sx={{ p: 0, height: 66 }}>
                <Box sx={{ display: 'flex', justifyContent: 'end' }}>
                  <Box>
                    <Typography sx={{ fontSize: 14 }}>{row.amount}</Typography>
                    <Typography sx={{ fontSize: 12, color: 'rgba(0, 0, 0, 0.5)' }}>
                      {row.price} {token}
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      transition: 'all 0.3s',
                      width: hoveredRow === index ? 32 : 0,
                      display: 'flex',
                      alignItems: 'center',
                    }}
                  >
                    <IconButton
                      onClick={(e) => {
                        e.stopPropagation()
                        // clearAllPendingAction()
                        clearActive()
                        deleteNeutralizePendingAction(row.token, row.neutralizeType)
                        // setNeutralizeAmount('')
                        // dispatch(deleteShortPendingActionsList({ index }))
                      }}
                      sx={{
                        transition: 'all 0.3s',
                        opacity: hoveredRow === index ? 1 : 0,
                        color: 'rgba(0, 0, 0, 1)',
                      }}
                    >
                      <ClearIcon sx={{ width: 16, height: 16 }} />
                    </IconButton>
                  </Box>
                </Box>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

function NeutralizeRightBox() {
  const { account, chainId, active } = useWeb3React()
  const currentCpt = useAppSelector((state) => state.collateralManage.currentCPT)
  const { walletTokenBalance, setRefresh, computed } = useHashPowerToken()
  const { position, decimals, token, avatarAssetList, tokensValues, rewards, balance } = currentCpt
  const [approveStatus, setApproveStatus] = useState(false)
  const [neutralizeType, setNeutralizeType] = useState('wallet')
  const [neutralizeAmount, setNeutralizeAmount] = useState<any>('')
  const [params, setParams] = useState<any>()
  const { onCheckApprove } = useCheckApprove()

  const { addPendingAction, updateNeutralizePendingAction, clearAllPendingAction, pendingActionList } =
    useShortPendingAction()

  useEffect(() => {
    if (account && chainId && token) {
      onCheckApprove(account, tokens[token].address[chainId], position)
        .then((res) => {
          if (res && res._hex > 0) {
            setApproveStatus(true)
          } else {
            setApproveStatus(false)
          }
        })
        .catch(() => {
          setApproveStatus(false)
        })
    }
  }, [account, approveStatus, chainId, onCheckApprove, position, setApproveStatus, token])

  const { activeInfo, onApprove, setActiveTab, clearActive } = useShortActiveAction()

  const onMax = () => {
    const dec = BigNumber.from(10).pow(decimals)
    if (neutralizeType === 'avatar') {
      if (avatarAssetList && avatarAssetList[token]) {
        // @ts-ignore
        if (avatarAssetList[token] * dec > Number(balance)) {
          // @ts-ignore
          setNeutralizeAmount(balance / dec)
        } else {
          setNeutralizeAmount(avatarAssetList[token])
        }
        if (avatarAssetList[token] > 0) {
          setDisabledAction(false)
        } else {
          setNeutralizeAmount(0)
          setDisabledAction(true)
        }
      } else {
        setNeutralizeAmount(0)
        setDisabledAction(true)
      }
    }
    if (neutralizeType === 'wallet') {
      if (walletTokenBalance && walletTokenBalance[token]) {
        // @ts-ignore
        if (walletTokenBalance[token] * dec > Number(balance)) {
          // @ts-ignore
          setNeutralizeAmount(balance / dec)
        } else {
          setNeutralizeAmount(walletTokenBalance[token])
        }
        if (walletTokenBalance[token] > 0) {
          setDisabledAction(false)
        } else {
          setNeutralizeAmount(0)
          setDisabledAction(true)
        }
      } else {
        setNeutralizeAmount(0)
        setDisabledAction(true)
      }
    }
  }
  const dispatch = useAppDispatch()
  useEffect(() => {
    dispatch(
      updateActionInfo({
        activeInfo: {
          token,
          tokenType: TokenType.NEUTRALIZE,
        },
      }),
    )
  }, [dispatch, token])

  const [amountNeutralize, setAmountNeutralize] = useState(0)
  const changPendList = useMemo(() => {
    let amount = 0
    pendingActionList.forEach((item) => {
      amount += item.amount
    })
    setAmountNeutralize(amount)
  }, [pendingActionList])

  const computedParams = useCallback(() => {
    if (currentCpt) {
      computed(amountNeutralize - 2 * amountNeutralize, currentCpt, (ratio, gDays, mintAllAmount) => {
        setParams({
          gDays,
          cRate: ratio,
          outstandingDebt: mintAllAmount,
          estimateNeutralizeFee: formatWBTC(tokensValues[token] * amountNeutralize * 0.01),
          dailyObligationReference: rewards ? formatWBTC(rewards / 10) * mintAllAmount : 0,
        })
      })
    }
  }, [amountNeutralize, computed, currentCpt, rewards, token, tokensValues])

  useEffect(() => {
    computedParams()
  }, [computedParams, amountNeutralize])

  const { fbtc20mint } = contracts
  const { signer } = useSystemParams()
  const posContract = usePosContract(active, signer)
  const { toastSuccess, toastError, toastInfo } = useToast()
  const computedPrice = useComputedPrice()
  const [availableShowFlag, setAvailableShowFlag] = useState(false)

  const isUpdate = useMemo(() => activeInfo.activeIndex !== -1, [activeInfo.activeIndex])

  const haveTokenFlag = useMemo(() => {
    return pendingActionList.some((item) => {
      if (item && (item as any).neutralizeType) {
        return (item as any).neutralizeType === neutralizeType
      }
      return false
    })
  }, [neutralizeType, pendingActionList])

  const onNeutralize = useCallback(async () => {
    pendingActionList.forEach(async (item) => {
      if (active && Number(position)) {
        const interf = new utils.Interface(fbtc20mint.abi)
        // @ts-ignore
        // console.log(utils.parseUnits(neutralizeAmount.toString(), decimals))
        const resData = interf.encodeFunctionData('neutralizetBalance', [
          utils.parseUnits(item.amount.toString(), decimals) || '0',
          item.neutralizeType === 'wallet',
        ])

        if (posContract) {
          try {
            // @ts-ignore
            const info = await posContract.delegateCall(fbtc20mint.address[chainId], resData)

            if (info) {
              setNeutralizeAmount(0)
              toastInfo('Submitted')
              const result = await info.wait()
              if (result) {
                setRefresh((state) => !state)
                toastSuccess('Success')
              }
            }
          } catch (e: any) {
            toastError('Fail', e.message)
          }
        }
      }
    })
    // return
  }, [
    pendingActionList,
    active,
    position,
    fbtc20mint.abi,
    fbtc20mint.address,
    decimals,
    posContract,
    chainId,
    toastInfo,
    setRefresh,
    toastSuccess,
    toastError,
  ])

  const submitDisable = useMemo(() => {
    // if (neutralizeAmount < 0) {
    //   return true
    // }
    // if (neutralizeType === 'wallet' && walletTokenBalance && neutralizeAmount > walletTokenBalance[token]) {
    //   return true
    // }
    // if (neutralizeType === 'avatar' && avatarAssetList && neutralizeAmount > avatarAssetList[token]) {
    //   return true
    // }
    if (pendingActionList.length === 0) {
      return true
    }
    return false

    // return (
    //   !neutralizeAmount || neutralizeAmount > walletTokenBalance[activeInfo.token] || pendingActionList.length === 0
    // )
  }, [pendingActionList])

  const estimateNeutralizeFeeWarningFlag = useCallback(() => {
    if (neutralizeType === 'wallet') {
      return walletTokenBalance && params && params.estimateNeutralizeFee > walletTokenBalance.WBTC
    }
    if (neutralizeType === 'avatar') {
      return avatarAssetList && params && params.estimateNeutralizeFee > avatarAssetList.WBTC
    }
    return false
  }, [avatarAssetList, neutralizeType, params, walletTokenBalance])

  const tokenAmountBalanceWarningFlag = useCallback(
    (type: string) => {
      if (neutralizeType === 'wallet' && type === 'wallet') {
        return walletTokenBalance && neutralizeAmount > walletTokenBalance[token] ? 'red' : ''
      }
      if (neutralizeType === 'avatar' && type === 'avatar') {
        return avatarAssetList && neutralizeAmount > avatarAssetList[token] ? 'red' : ''
      }
      return ''
    },
    [avatarAssetList, neutralizeAmount, neutralizeType, token, walletTokenBalance],
  )

  const walletNeutralizeFeeWarningFlag = useCallback(
    (type: string) => {
      if (neutralizeType === type) {
        return walletTokenBalance && params && params.estimateNeutralizeFee > walletTokenBalance.WBTC ? 'red' : ''
      }
      if (neutralizeType === type) {
        return avatarAssetList && params && params.estimateNeutralizeFee > avatarAssetList.WBTC ? 'red' : ''
      }
      return ''
    },
    [avatarAssetList, neutralizeType, params, walletTokenBalance],
  )
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [disabledAction, setDisabledAction] = useState(true)

  const disableFlag = useMemo(() => {
    if (neutralizeAmount <= 0) {
      return true
    }
    if (neutralizeAmount > 0) {
      return neutralizeType === 'wallet'
        ? neutralizeAmount > walletTokenBalance[activeInfo.token]
        : neutralizeAmount > avatarAssetList[activeInfo.token]
    }
    return false
  }, [activeInfo.token, avatarAssetList, neutralizeAmount, neutralizeType, walletTokenBalance])

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography sx={{ fontSize: 16, fontWeight: 500 }}>Neutralize F(BTC,20)</Typography>
      </Box>

      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 1.6 }}>
        <Box
          sx={{
            height: activeInfo?.token && availableShowFlag ? '52px' : '45px',
            transition: '300ms all',
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1.2, width: '100%' }}>
            <Fbtc20Icon style={{ width: 42, height: 42 }} />
            <div>
              <InputBox>
                <input
                  type="number"
                  // placeholder={walletTokenBalance[token] }
                  placeholder="0"
                  value={neutralizeAmount ?? ''}
                  onBlur={() => {
                    if (activeInfo?.amount.toString() === '' || activeInfo?.amount < 0) {
                      setAvailableShowFlag(false)
                    }
                  }}
                  onFocus={() => setAvailableShowFlag(true)}
                  onChange={(event) => {
                    const inputValue = event.target.value
                    let formattedValue = ''
                    const cleanedValue = inputValue.replace(/[^\d.]/g, '')
                    if (cleanedValue.includes('.')) {
                      const parts = cleanedValue.split('.')
                      parts[1] = parts[1]?.slice(0, tokens[token].decimals)
                      formattedValue = parts.join('.')
                    } else {
                      formattedValue = cleanedValue
                    }

                    setNeutralizeAmount(formattedValue || '')
                  }}
                />
              </InputBox>
              {activeInfo?.token && availableShowFlag ? (
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1,
                    width: '100%',
                    color: 'rgba(0,0,0,.5)',
                    paddingLeft: '4px',
                  }}
                >
                  {neutralizeType === 'wallet'
                    ? walletTokenBalance[activeInfo?.token]
                    : avatarAssetList[activeInfo?.token]}{' '}
                  Available
                </Box>
              ) : (
                <></>
              )}
            </div>
          </Box>
        </Box>
        <Box>
          <Button
            size="small"
            sx={{
              textTransform: 'none',
              borderRadius: 1.8,
              color: 'rgba(110, 214, 243, 1)',
            }}
            onClick={() => onMax()}
          >
            Max
          </Button>
        </Box>
      </Box>

      <SelectTypeWrap>
        <p className="title-neutralize">Neutralize the asset from</p>
        <div className="content">
          {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
          <div
            className={`item_wrap ${neutralizeType === 'wallet' ? 'active' : ''}`}
            onClick={() => {
              setNeutralizeType('wallet')
            }}
          >
            <div className="item wallet">
              <h4>
                <Wallet /> My Wallet
              </h4>
              <p>
                <span>{token}</span>
                <span
                  style={{
                    color: tokenAmountBalanceWarningFlag('wallet'),
                  }}
                >
                  {(walletTokenBalance && walletTokenBalance[token]) || 0}
                </span>
              </p>
              <p>
                <span>WBTC</span>
                <span
                  style={{
                    color: walletNeutralizeFeeWarningFlag('wallet'),
                  }}
                >
                  {(walletTokenBalance && walletTokenBalance.WBTC) || 0}
                </span>
              </p>
            </div>
          </div>
          {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
          <div
            className={`item_wrap ${neutralizeType === 'avatar' ? 'active' : ''}`}
            onClick={() => {
              setNeutralizeType('avatar')
            }}
          >
            <div className="item avatar">
              <h4>
                <AvatarIcon />
                Collateral
              </h4>
              <p>
                <span>{token}</span>
                <span
                  style={{
                    color: tokenAmountBalanceWarningFlag('avatar'),
                  }}
                >
                  {(avatarAssetList && avatarAssetList[token]) || 0}
                </span>
              </p>
              <p>
                <span>WBTC</span>
                <span
                  style={{
                    color: walletNeutralizeFeeWarningFlag('wallet'),
                  }}
                >
                  {(avatarAssetList && avatarAssetList.WBTC) || 0}
                </span>
              </p>
            </div>
          </div>
        </div>
      </SelectTypeWrap>

      <TipInfo>
        {estimateNeutralizeFeeWarningFlag() ? <WarnRedIcon /> : <WarnGrayIcon />}

        <p
          style={{
            color: estimateNeutralizeFeeWarningFlag() ? 'red' : '',
          }}
        >
          A 1.0% fee of the token value will be levied after the position is neutralized.
        </p>
      </TipInfo>

      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          gap: 3,
          mt: 1.6,
        }}
      >
        <ActionButton
          variant="contained"
          disableElevation
          onClick={() => {
            clearAllPendingAction()
            clearActive()

            // clearActive()
            setNeutralizeAmount('')
            setActiveTab(1)
          }}
        >
          Cancel
        </ActionButton>
        <ActionButton
          variant="contained"
          disableElevation
          disabled={disableFlag}
          onClick={() => {
            const action = {
              token,
              tokenType: TokenType.NEUTRALIZE,
              amount: Number(neutralizeAmount),
              neutralizeType,
              price: computedPrice(
                {
                  outputToken: {
                    token,
                    decimals,
                  },
                  inputToken: {
                    token,
                    decimals: tokens[token].decimals,
                  },
                },
                Number(neutralizeAmount),
              ).toFixed(decimals),
            }
            if (!approveStatus) {
              onApprove().then((res) => {
                if (res) {
                  addPendingAction(action)
                }
              })
            } else if (isUpdate || haveTokenFlag) {
              updateNeutralizePendingAction(action)
            } else if (neutralizeAmount > 0) {
              addPendingAction(action)
            }
          }}
        >
          {!approveStatus ? 'Approve & Add' : `${isUpdate || haveTokenFlag ? 'Update' : 'Add'} Action`}
        </ActionButton>
      </Box>
      {pendingActionList.length > 0 && (
        <Box>
          <TableBox
            setNeutralizeType={(value) => {
              setNeutralizeType(value)
            }}
          />
        </Box>
      )}

      {pendingActionList.length > 0 && (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 3 }}>
          <ActionButton
            onClick={() => {
              clearAllPendingAction()
              setNeutralizeAmount('')
              onNeutralize()
            }}
            disabled={submitDisable}
            variant="contained"
            disableElevation
          >
            Submit Transaction
          </ActionButton>
        </Box>
      )}
      <NeutralizePendingActionsComp
        neutralizeAmount={neutralizeAmount}
        neutralizeType={neutralizeType}
        params={params}
      />
    </Box>
  )
}

export default NeutralizeRightBox
