import React from 'react'
import { Typography } from '@mui/material/'
import { useWeb3React } from 'web3-react-core'
import usePanel from 'hooks/usePanel'

import { Wrapper, Title, Accordion, AccordionSummary, AccordionDetails } from 'components/util'
import useHashPowerToken from 'hooks/useHashPowerToken'
import AddGuaranteeFromWallet from './AddGuaranteeFromWallet'
import AddCurrencyToAvatar from './AddCurrencyToAvatar'

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export default function BeforeMintAction(props) {
  const { approveStatus, onApprove } = props
  const { walletTokenBalance } = useHashPowerToken()
  const { expanded, handleChange } = usePanel()
  const { active, chainId, account } = useWeb3React()

  return (
    <Wrapper>
      <Title>Add collateral before mint</Title>
      <Accordion id="panel1" expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Add asset from my wallet to Prepaid Interest</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <AddGuaranteeFromWallet
            active={active}
            account={account}
            chainId={chainId}
            walletTokenBalance={walletTokenBalance}
            onApprove={onApprove}
            approveStatus={approveStatus}
          />
        </AccordionDetails>
      </Accordion>
      <Accordion id="panel2" expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 2. Add asset from my wallet to Collateral</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <AddCurrencyToAvatar
            active={active}
            account={account}
            chainId={chainId}
            walletTokenBalance={walletTokenBalance}
            onApprove={onApprove}
            approveStatus={approveStatus}
          />
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
