import React, { useCallback, useEffect, useState } from 'react'
import { IconButton, Typography } from '@mui/material/'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'
import AddIcon from '@mui/icons-material/Add'
import uuid from 'uuid/dist/v4'
import Methods from 'components/Methods'
import { Accordion, AccordionDetails, AccordionSummary, Approve, Title, Wrapper } from 'components/util'
import DataWrapper from 'components/DataWrapper'
import usePanel from 'hooks/usePanel'
import { useWeb3React } from 'web3-react-core'
import { BigNumber } from '@ethersproject/bignumber'
import tokens from 'config/constants/tokens'
import { styled } from '@mui/material/styles'
import PositionABI from 'config/abi/Position.json'
import { AvatarIcon, Button, DeleteIcon, DroppableIcon } from 'components/uikit'
import { getContract } from 'utils/web3React'
import { utils } from 'ethers'
import { useAppSelector } from 'state'
import useHashPowerToken from 'hooks/useHashPowerToken'
import useSystemParams from 'hooks/useSystemParams'
import contracts from 'config/constants/contracts'
import useToast from '../../../hooks/useToast'
import { tokensSymbol } from '../../../config/constants/types'
import { formatInfinity, formatSnNumber, toFixed } from '../../../utils/formatTheValue'
import useComputedPrice from '../../../hooks/useComputedPrice'

const AddBtn = styled(Button)`
  box-shadow: none !important;
  width: 34px !important;
  height: 28px !important;
  padding: 0 !important;
  background: linear-gradient(90deg, #62c3f7 0%, #7ae9f1 100%) !important;
  border-radius: 6px !important;
  svg {
    color: #000;
    width: 16px;
  }
`

const { fbtc20mint, multiCall } = contracts
const onDragEnd = (result, columns, setColumns, setIsSubmit) => {
  if (!result.destination) return
  const { source, destination } = result
  if (source.droppableId !== destination.droppableId) {
    const sourceColumn = columns[source.droppableId]
    const destColumn = columns[destination.droppableId]
    const sourceItems = [...sourceColumn.items]
    const destItems = [...destColumn.items]
    const [removed] = sourceItems.splice(source.index, 1)
    destItems.splice(destination.index, 0, removed)
    setColumns([...destColumn, destItems])
  } else {
    const copiedItems = [...columns]
    const [removed] = copiedItems.splice(source.index, 1)
    copiedItems.splice(destination.index, 0, removed)
    if (copiedItems.map((it) => it.input > 0).includes(false)) {
      setIsSubmit(true)
    } else {
      setIsSubmit(false)
    }
    setColumns(copiedItems)
  }
}

export default function CustomizedAccordions(props) {
  const { onApprove, approveStatus } = props
  const { active, chainId } = useWeb3React()
  const { toastInfo, toastError, toastSuccess } = useToast()
  const { setRefresh } = useHashPowerToken()
  const { signer, pairWBTCAndFBTC, pairWBTCAndUSDT, getWBTC, setRefreshParams } = useSystemParams()
  // const getTokenBalance = useGetTokenBalance(library, active, chainId)
  const currentCpt = useAppSelector((state) => state.collateralManage.currentCPT)
  const { position, avatarAssetList, positionInfoValues } = currentCpt
  const [tokensList, setTokensList] = useState([])
  const [columns, setColumns] = useState([])
  const { expanded, handleChange } = usePanel()
  const [isSubmitStatus, setIsSubmit] = useState(true)
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)

  const computedPrice = useComputedPrice()

  const [params, setParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%',
  })
  const ABI = [
    'function swapUniV2_swapExactTokensForTokens(uint amountIn, uint amountOutMin, address[] calldata path, uint deadline)',
  ]
  const ABIMulti = ['function multicall(tuple(address target, bytes data)[]  calls)']
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const ifaceMulti = new utils.Interface(ABIMulti)
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const interf = new utils.Interface(ABI)

  const addMethods = useCallback(() => {
    setColumns((state) => {
      const data = [
        ...state,
        {
          id: uuid(),
          input: 0,
          inputToken: tokensList[0],
          outputToken: tokensList[0].rightOptionList[0],
          oracleHub: computedPrice({
            inputToken: tokensList[0],
            outputToken: tokensList[0].rightOptionList[0],
          }),
        },
      ]
      if (data.map((it) => it.input > 0).includes(false)) {
        setIsSubmit(true)
      } else {
        setIsSubmit(false)
      }
      return data
    })
  }, [computedPrice, tokensList])
  const onSubmit = useCallback(async () => {
    if (signer && Number(position)) {
      const posContract = getContract(signer, PositionABI, position)
      try {
        if (posContract) {
          const isSubmit = columns.filter((it) => it.input <= 0)
          if (isSubmit.length) {
            return
          }

          const _params = columns.map((it) => {
            // @ts-ignore
            const inputAmount = formatSnNumber(Math.floor(it.input * BigNumber.from(10).pow(it.inputToken.decimals)))
            const path = []
            if (
              (it.inputToken.token === 'USDT' || it.outputToken.token === 'USDT') &&
              (it.inputToken.token === 'F(BTC,20)' || it.outputToken.token === 'F(BTC,20)')
            ) {
              path.push(it.inputToken.address[chainId])
              path.push(tokens.WBTC.address[chainId])
              path.push(it.outputToken.address[chainId])
            } else {
              path.push(it.inputToken.address[chainId])
              path.push(it.outputToken.address[chainId])
            }
            return [
              fbtc20mint.address[chainId],
              interf.encodeFunctionData(
                'swapUniV2_swapExactTokensForTokens',
                // @ts-ignore
                [inputAmount, '0', path, '999999999999999'],
              ),
            ]
          })
          const dataMulti = ifaceMulti.encodeFunctionData('multicall', [_params])
          const info = await posContract.delegateCall(multiCall.address[chainId], dataMulti)
          if (info.hash) {
            setIsSubmit(true)
            toastInfo('Submitted')
            const result = await info.wait()
            if (result) {
              setIsSubmit(false)
              setRefreshParams((state) => !state)
              setRefresh((state) => !state)
              toastSuccess('Success')
            }
          }
        }
      } catch (e) {
        setIsSubmit(false)
        toastError('Fail', e.message)
      }
    }
  }, [
    signer,
    position,
    columns,
    ifaceMulti,
    chainId,
    interf,
    toastInfo,
    setRefreshParams,
    setRefresh,
    toastSuccess,
    toastError,
  ])

  useEffect(() => {
    if (active && avatarAssetList) {
      const list = []
      // eslint-disable-next-line no-restricted-syntax,guard-for-in
      for (const tokensKey in tokens) {
        const item = tokens[tokensKey]
        const tokenItem = {
          token: tokensKey,
          address: item.address,
          allBalance: formatSnNumber(avatarAssetList[tokensKey]),
          balance: toFixed(avatarAssetList[tokensKey], tokensKey),
          icon: item.icon,
          decimals: item.decimals,
          rightOptionList: [],
        }
        // eslint-disable-next-line no-restricted-syntax
        for (const tokensKeyB in tokens) {
          if (tokensKey !== tokensKeyB) {
            tokenItem.rightOptionList.push({
              token: tokensKeyB,
              address: tokens[tokensKeyB].address,
              icon: tokens[tokensKeyB].icon,
              allBalance: formatSnNumber(avatarAssetList[tokensKeyB]),
              balance: toFixed(avatarAssetList[tokensKeyB], tokensKeyB),
              decimals: tokens[tokensKeyB].decimals,
            })
          }
        }
        list.push(tokenItem)
      }

      setTokensList(list)
      let price = 0
      if (list) {
        price = computedPrice({
          inputToken: list[0],
          outputToken: list[0].rightOptionList[0],
        })
      }
      setColumns(() => {
        return [
          {
            id: uuid(),
            input: 0,
            output: 0,
            inputToken: list[0],
            outputToken: list[0].rightOptionList[0],
            oracleHub: price,
          },
        ]
      })
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [active, avatarAssetList])

  const computed = useCallback(
    (data) => {
      if (positionInfoValues) {
        if (data) {
          let balance = 0
          data.forEach((item) => {
            balance +=
              toFixed(
                getWBTC({ amount: item.output, symbol: item.outputToken.token, decimals: item.outputToken.decimals }),
                'WBTC',
              ) * 1
            balance -=
              toFixed(
                getWBTC({ amount: item.input, symbol: item.inputToken.token, decimals: item.inputToken.decimals }),
                'WBTC',
              ) * 1
          })
          // @ts-ignore
          const totalAssetsValues =
            positionInfoValues.totalAssetsValues * 1 +
            toFixed(balance, 'WBTC') * (BigNumber.from(10) as any).pow(tokens.WBTC.decimals) * 1
          // @ts-ignore
          const { interestGuaranteed } = positionInfoValues
          /*
           * totalAssetsValues + value / shortBalance
           *
           * */
          setParams((state) => ({
            ...state,
            // @ts-ignore
            futureCRate: ((totalAssetsValues / BigNumber.from(positionInfoValues.shortBalanceValue)) * 100).toFixed(2),
            // @ts-ignore
            futureGDays: Math.floor(
              (interestGuaranteed - (BigNumber.from(positionInfoValues.totalObligation) as any)) /
                (BigNumber.from(positionInfoValues.estimatedDailyDebt) as any),
            ),
          }))
        }
      }
    },
    [getWBTC, positionInfoValues],
  )
  const onChangeInputAmount = useCallback(
    (e, item) => {
      const value = formatSnNumber(e.target.value)
      setColumns((state) => {
        const data = state.map((it) => {
          const _item = {
            ...it,
            input: it.id === item.id ? e.target.value : it.input,
            // eslint-disable-next-line no-nested-ternary
            output:
              it.id === item.id ? toFixed(computedPrice(item, e.target.value), item.outputToken.token) : it.output,
          }
          if (_item.inputToken.token !== tokensSymbol.FBTC20 && _item.outputToken.token !== tokensSymbol.FBTC20) {
            _item.pairWBTCAndUSDT = {
              [_item.inputToken.token]: pairWBTCAndUSDT[_item.inputToken.token] * 1 + e.target.value * 1 * 0.997,
              [_item.outputToken.token]: pairWBTCAndUSDT[_item.outputToken.token] * 1 - _item.output * 1,
            }
          }
          if (_item.inputToken.token === tokensSymbol.FBTC20 || _item.outputToken.token === tokensSymbol.FBTC20) {
            if (_item.inputToken.token === 'WBTC' || _item.outputToken.token === 'WBTC') {
              _item.pairWBTCAndFBTC = {
                [_item.inputToken.token]: pairWBTCAndFBTC[_item.inputToken.token] * 1 + e.target.value * 1 * 0.997,
                [_item.outputToken.token]: pairWBTCAndFBTC[_item.outputToken.token] * 1 - _item.output * 1,
              }
            }
          }
          // eslint-disable-next-line no-unused-expressions
          // e.target.value<=0 ? _item.output = 0 : ''
          // setP
          // _item.oracleHub = computedPrice()
          return _item
        })

        if (data.map((it) => it.input > 0 && it.output > 0).includes(false)) {
          setIsSubmit(true)
        } else if (
          value.indexOf('.') !== -1 &&
          String(value).slice(value.indexOf('.') + 1).length > item.inputToken.decimals
        ) {
          setIsSubmit(true)
        } else {
          setIsSubmit(false)
        }
        computed(data)
        return data
      })
    },
    [computed, computedPrice, pairWBTCAndUSDT, pairWBTCAndFBTC],
  )
  const onChangeToken = useCallback(
    (selected, id, type) => {
      setColumns((state) => {
        return state.map((item) => {
          if (item.id === id) {
            const data = { ...item, [type]: selected }
            data.oracleHub = computedPrice(data, 1)
            data.output = toFixed(computedPrice(data, data.input), data.outputToken.token)
            return data
          }
          return item
        })
      })
    },
    [computedPrice],
  )
  const onExchange = useCallback(
    (record, index) => {
      setColumns((state) => {
        return state.map((item, i) => {
          if (i === index) {
            const data = item.outputToken
            data.rightOptionList = [...item.inputToken.rightOptionList]
            data.rightOptionList.push(item.inputToken)
            data.rightOptionList = data.rightOptionList.filter((it) => it.token !== item.outputToken.token)
            const target = { ...item, outputToken: item.inputToken, inputToken: data, input: 0, output: 0 }
            return { ...target, oracleHub: computedPrice(target, 1) }
          }
          return item
        })
      })
    },
    [computedPrice],
  )
  const onMax = useCallback(
    (record, index) => {
      setColumns((state) => {
        const data = state.map((item, i) => ({
          ...item,
          input: i === index ? record.inputToken.allBalance : item.input,
          // eslint-disable-next-line no-nested-ternary,no-self-compare
          output:
            item.id === record.id
              ? toFixed(computedPrice(item, record.inputToken.allBalance), record.outputToken.token)
              : item.output,
        }))
        const value = String(record.inputToken.balance)
        if (data.map((it) => it.input > 0 && it.output > 0).includes(false)) {
          setIsSubmit(true)
        } else if (
          value.indexOf('.') !== -1 &&
          String(value).slice(value.indexOf('.') + 1).length > record.inputToken.decimals
        ) {
          setIsSubmit(true)
        } else {
          setIsSubmit(false)
        }
        computed(data)
        return data
      })
    },
    [computed, computedPrice],
  )

  useEffect(() => {
    if (active && Number(position) && positionInfoValues) {
      setParams({
        nowGDays: Number(positionInfoValues.gDays) === Infinity ? '+' : positionInfoValues.gDays,
        nowCRate: Number(positionInfoValues.cRate) === Infinity ? '+' : `${positionInfoValues.cRate}%`,
      })
    }
  }, [active, position, positionInfoValues])

  return (
    <Wrapper>
      <Title />
      <Accordion id="panel12" expanded={expanded === 'panel12'} onChange={handleChange('panel12')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Trading through Collateral</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <DragDropContext onDragEnd={(result) => onDragEnd(result, columns, setColumns, setIsSubmit)}>
            <Droppable droppableId="droppableId">
              {(provided) => {
                return (
                  <div {...provided.droppableProps} ref={provided.innerRef}>
                    {columns.map((item, index) => {
                      return (
                        <Draggable key={item.id} draggableId={item.id} index={index} isDragDisabled={index === 0}>
                          {/* @ts-ignore */}
                          {/* eslint-disable-next-line @typescript-eslint/no-shadow */}
                          {(provided) => {
                            return (
                              <div
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                                style={{
                                  display: 'flex',
                                  alignItems: 'center',
                                  userSelect: 'none',
                                  margin: '0 0 8px 0',
                                  ...provided.draggableProps.style,
                                  position: 'static',
                                }}
                              >
                                <div style={{ width: '100%', position: 'relative', paddingRight: index <= 0 ? 58 : 0 }}>
                                  <Methods
                                    SelectIcon={AvatarIcon}
                                    leftInputValue={item.input}
                                    rightInputValue={item.output}
                                    isExchange
                                    onExchange={() => {
                                      onExchange(item, index)
                                    }}
                                    options={tokensList}
                                    rightOptions={item.inputToken.rightOptionList}
                                    leftSelectValue={item.inputToken.token}
                                    rightSelectValue={item.outputToken.token}
                                    leftBalance={item.inputToken.balance || item.inputToken.allBalance}
                                    rightBalance={item.outputToken.balance || item.outputToken.allBalance}
                                    selectedBalance={100}
                                    leftDisableStatus={[false, false]}
                                    RightIcon={AvatarIcon}
                                    rightDisableStatus={[false, true]}
                                    selectInput={(e) => {
                                      onChangeInputAmount(e, item)
                                    }}
                                    leftChange={(selected) => {
                                      onChangeToken(selected, item.id, 'inputToken')
                                    }}
                                    rightChange={(selected) => {
                                      onChangeToken(selected, item.id, 'outputToken')
                                    }}
                                    onMax={() => {
                                      onMax(item, index)
                                    }}
                                    leftSymbol={item.inputToken.token}
                                    rightSymbol={item.outputToken.token}
                                  />
                                  <p
                                    style={{
                                      position: 'absolute',
                                      bottom: 0,
                                      right: index <= 0 ? 73 : 15,
                                      color: 'rgba(0, 0, 0, 0.5)',
                                      fontSize: 12,
                                    }}
                                  >
                                    1{item.inputToken.token}  {computedPrice(item, 1)}
                                    {item.outputToken.token}
                                  </p>
                                </div>
                                {index > 0 ? (
                                  <>
                                    <IconButton
                                      onClick={() => {
                                        const data = [...columns]
                                        data.splice(index, 1)
                                        if (data.map((it) => it.input > 0).includes(false)) {
                                          setIsSubmit(true)
                                        } else {
                                          setIsSubmit(false)
                                        }
                                        setColumns(data)
                                      }}
                                      style={{ cursor: 'pointer', marginTop: 8, marginLeft: 10, padding: 0 }}
                                    >
                                      <DeleteIcon />
                                    </IconButton>
                                    <IconButton style={{ marginTop: 8, marginLeft: 8, padding: 0 }}>
                                      <DroppableIcon style={{ fill: 'none' }} />
                                    </IconButton>
                                  </>
                                ) : null}
                              </div>
                            )
                          }}
                        </Draggable>
                      )
                    })}
                    {provided.placeholder}
                  </div>
                )
              }}
            </Droppable>
          </DragDropContext>
          <AddBtn onClick={addMethods}>
            <AddIcon />
          </AddBtn>
          <DataWrapper
            data={[
              // eslint-disable-next-line no-sequences,no-nested-ternary
              [
                'Collateral Ratio',
                // eslint-disable-next-line no-nested-ternary
                params.nowCRate.includes('NaN') ? '0%' : params.nowCRate === 'Infinity' ? '+' : params.nowCRate,
                // eslint-disable-next-line no-nested-ternary
                params.futureCRate
                  ? params.futureCRate && formatInfinity(params.futureCRate).toString().includes('')
                    ? formatInfinity(params.futureCRate)
                    : `${params.futureCRate}%`
                  : '0%',
              ],

              // eslint-disable-next-line no-sequences,no-nested-ternary
              [
                'Prepaid Interest Coverage',
                `${params.nowGDays ? formatInfinity(params.nowGDays) : '0'} days`,
                `${params.futureGDays ? formatInfinity(params.futureGDays) : '0'} days`,
              ],
            ]}
          />
          {approveStatus ? (
            <Approve style={{ boxShadow: 'none' }} disabled={isSubmitStatus} onClick={onSubmit}>
              Submit
            </Approve>
          ) : (
            <Approve
              style={{ boxShadow: 'none' }}
              disabled={approveBtnStatus}
              onClick={() => {
                onApprove((state) => {
                  setApproveBtnStatus(state)
                })
              }}
            >
              Approve
            </Approve>
          )}
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
