import React, { useCallback, useEffect, useState } from 'react'
import tokens from 'config/constants/tokens'
import Methods from 'components/Methods'
import Wallet from 'components/uikit/widgets/Menu/icons/Wallet'
import { AvatarIcon } from 'components/uikit'
import { Approve, IconWrap, TipWrap } from 'components/util'
import { BigNumber } from '@ethersproject/bignumber'
import { useTransfer } from 'hooks/useManager'
import DataWrapper from 'components/DataWrapper'
import { useAppSelector } from '../../../state'
import { formatInfinity, formatSnNumber, formatWBTC } from '../../../utils/formatTheValue'
import useToast from '../../../hooks/useToast'
import useHashPowerToken from '../../../hooks/useHashPowerToken'

function AddCurrencyToAvatar(props) {
  const { onApprove, approveStatus, active, account, chainId, walletTokenBalance } = props
  const { setRefresh } = useHashPowerToken()
  const currentCpt = useAppSelector((state) => state.collateralManage.currentCPT)
  const { position, avatarAssetList, positionInfoValues, allowedAssets, tokensValues } = currentCpt
  const [submitStatus, setSubmitStatus] = useState(true)
  const { toastError, toastSuccess } = useToast()
  const [tradingList, setTradingList] = useState([])
  const [leftSelectedToken, setLeftSelectedToken] = useState<any>({ token: 'WBTC' })
  const [rightSelectedToken, setRightSelectedToken] = useState<any>({ token: 'WBTC' })
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)
  const [params, setParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%',
  })

  const [transferVal, setTransferVal] = useState(0)
  const transfer = useTransfer()

  const computed = useCallback(
    (value) => {
      // @ts-ignore
      const totalAssetsValues =
        (BigNumber.from(positionInfoValues.totalAssetsValues) as any) * 1 +
        value * (BigNumber.from(10) as any).pow(tokens.WBTC.decimals) * 1
      // @ts-ignore
      const interestGuaranteed = BigNumber.from(positionInfoValues.interestGuaranteed)

      let cRate = 0
      if (totalAssetsValues === 0) {
        cRate = 0
      } else if (formatWBTC(positionInfoValues.shortBalanceValue) === '0') {
        cRate = Infinity
      } else {
        // @ts-ignore
        cRate = (totalAssetsValues / BigNumber.from(positionInfoValues.shortBalanceValue)) * 100
      }

      // @ts-ignore
      const gDays = Math.floor(
        ((interestGuaranteed as any) - (BigNumber.from(positionInfoValues.totalObligation) as any)) /
          (BigNumber.from(positionInfoValues.estimatedDailyDebt) as any),
      )
      setParams((state) => ({
        ...state,
        futureCRate: formatInfinity(cRate).toString().includes('')
          ? formatInfinity(cRate)
          : `${formatInfinity(cRate).toFixed(2)}%`,
        // eslint-disable-next-line no-restricted-globals
        futureGDays: isNaN(gDays) ? 0 : formatInfinity(gDays),
      }))
    },
    [positionInfoValues],
  )

  const onTransfer = useCallback(async () => {
    if (active && account && Number(position)) {
      try {
        setSubmitStatus(true)
        // @ts-ignore
        const tx = await transfer(
          account,
          tokens[leftSelectedToken.token].address[chainId],
          position,
          formatSnNumber(transferVal * 10 ** tokens[leftSelectedToken.token].decimals),
        )
        if (tx) {
          const transferInfo = await tx.wait()
          if (transferInfo) {
            setRefresh((state) => !state)
            toastSuccess('Success')
            setSubmitStatus(false)
          }
        }
      } catch (e: any) {
        toastError('Fail', e.message)
        setSubmitStatus(false)
      }
    }
  }, [
    active,
    account,
    position,
    transferVal,
    transfer,
    leftSelectedToken,
    chainId,
    setRefresh,
    toastSuccess,
    toastError,
  ])

  useEffect(() => {
    if (active && Number(position) && positionInfoValues) {
      setParams({
        nowGDays: Number(positionInfoValues.gDays) === Infinity ? '+' : positionInfoValues.gDays,
        nowCRate: Number(positionInfoValues.cRate) === Infinity ? '+' : `${positionInfoValues.cRate}%`,
      })
      computed(0)
    }
  }, [active, computed, position, positionInfoValues])

  useEffect(() => {
    if (active && Number(position) && walletTokenBalance && chainId) {
      const list = []
      // eslint-disable-next-line no-restricted-syntax
      for (const [token, data] of Object.entries(tokens)) {
        const item: any = { ...data }
        if (allowedAssets.includes(data.address[chainId].toLocaleUpperCase())) {
          item.balance = walletTokenBalance[token]
          item.avatarBalance = avatarAssetList[token]
          item.token = token
          item.rightOptionList = []
          item.rightOptionList.push({
            address: item.address,
            balance: avatarAssetList[token],
            token,
          })
          list.push(item)
        }
      }
      setTradingList(list)
      setLeftSelectedToken(list[0])
      setRightSelectedToken(list[0].rightOptionList[0])
    }
  }, [active, allowedAssets, avatarAssetList, position, walletTokenBalance, chainId])

  return (
    <>
      <Methods
        SelectIcon={Wallet}
        options={tradingList}
        rightOptions={tradingList}
        leftSelectValue={leftSelectedToken ? leftSelectedToken.token : ''}
        rightSelectValue={rightSelectedToken ? rightSelectedToken.token : ''}
        rightBalance={rightSelectedToken ? rightSelectedToken.balance : 0}
        leftBalance={leftSelectedToken ? leftSelectedToken.balance : 0}
        leftInputValue={transferVal}
        rightInputValue={transferVal}
        selectInput={({ target }) => {
          // @ts-ignore
          setTransferVal(target.value)
          if (target.value > 0) {
            setSubmitStatus(false)
          } else {
            setSubmitStatus(true)
          }
          // @ts-ignore
          computed(
            target.value *
              ((BigNumber.from(tokensValues[leftSelectedToken.token] as any) as any) /
                (BigNumber.from(10) as any).pow(tokens.WBTC.decimals)),
          )
        }}
        leftChange={(selected) => {
          setLeftSelectedToken(selected)
          setRightSelectedToken(selected.rightOptionList[0])
          // @ts-ignore
          computed(
            leftSelectedToken.balance *
              ((BigNumber.from(tokensValues[leftSelectedToken.token] as any) as any) /
                (BigNumber.from(10) as any).pow(tokens.WBTC.decimals)),
          )
        }}
        onMax={() => {
          if (walletTokenBalance) {
            setTransferVal(leftSelectedToken.balance)
            // @ts-ignore
            computed(
              leftSelectedToken.balance *
                ((BigNumber.from(tokensValues[leftSelectedToken.token] as any) as any) /
                  (BigNumber.from(10) as any).pow(tokens.WBTC.decimals)),
            )
            if (leftSelectedToken.balance > 0) {
              setSubmitStatus(false)
            }
          }
        }}
        RightIcon={AvatarIcon}
      />
      <DataWrapper
        data={[
          ['Collateral Ratio', params.nowCRate.includes('NaN') ? '0%' : params.nowCRate, params.futureCRate],
          ['Prepaid Interest Coverage', `${params.nowGDays || 0} days`, `${params.futureGDays} days`],
        ]}
      />
      <TipWrap>
        <IconWrap style={{ background: '#FF7D00', color: '#fff' }}>!</IconWrap>
        <p>
          Add asset to Collateral wont effect the Prepaid Interest Coverage, so you might need to add the specific
          asset in method 1 first.
        </p>
      </TipWrap>
      {approveStatus ? (
        <Approve style={{ boxShadow: 'none' }} disabled={submitStatus} onClick={onTransfer}>
          Submit
        </Approve>
      ) : (
        <Approve
          style={{ boxShadow: 'none' }}
          disabled={approveBtnStatus}
          onClick={() => {
            onApprove((state) => {
              setApproveBtnStatus(state)
            })
          }}
        >
          Approve
        </Approve>
      )}
    </>
  )
}

export default AddCurrencyToAvatar
