import { createAction } from '@reduxjs/toolkit'
import type { PendingAction, TokenType } from './reducer'

// eslint-disable-next-line import/prefer-default-export
export const updataShortActiveToken = createAction<{ activeToken: string }>('short/updataShortActiveToken')

export const setShortPendingAction =
  createAction<{ actions: Map<string, PendingAction[]> }>('short/setShortPendingAction')

export const updataShortRateInfo = createAction<{
  rateInfo: {
    rate: any
    coverage: any
  }
}>('short/updataShortRateInfo')

export const updateActionInfo = createAction<{
  activeInfo: {
    token?: string
    tokenType?: TokenType
  }
}>('short/updateActionInfo')

export const updateActiveAmount = createAction<{
  amount: any
}>('short/updateActiveAmount')
export const updateActiveActionIndex = createAction<{
  index: number
}>('short/updateActiveActionIndex')

export const updateActiveTab = createAction<{
  index: number
}>('short/updateActiveTab')
