import React from 'react'
import { styled } from '@mui/material/styles'
import {
  Box,
  Table,
  TableRow,
  TableSortLabel,
  TableBody,
  TableContainer,
  TableHead,
  TablePagination,
} from '@mui/material'
import TableCell, { tableCellClasses } from '@mui/material/TableCell'
import SortIcon from './icon/SortIcon'

export const StyledTableCell = styled(TableCell)(() => ({
  [`&.${tableCellClasses.head}`]: {
    border: 'none',
    color: 'rgba(0, 0, 0, 0.5)',
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
    borderBottom: '1px solid #E2E8EE',
  },
}))
export const StyledTableRow = styled(TableRow)(() => ({
  cursor: 'pointer',
  // hide last border
  '&:last-child td, &:last-child th': {
    fontSize: 14,
    borderBottom: '1px solid rgba(226, 232, 238, 1)',
  },
}))

const BoxWrapper = styled(Box)`
  height: 80%;
  overflow: scroll;
  &::-webkit-scrollbar,
  .MuiTableContainer-root::-webkit-scrollbar {
    display: none;
  }
`

function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
  if (b[orderBy] < a[orderBy]) {
    return -1
  }
  if (b[orderBy] > a[orderBy]) {
    return 1
  }
  return 0
}

type Order = 'asc' | 'desc'

function getComparator<Key extends keyof any>(
  order: Order,
  orderBy: Key,
): (a: { [key in Key]: number | string }, b: { [key in Key]: number | string }) => number {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy)
}

function stableSort<T>(array: T[], comparator: (a: T, b: T) => number) {
  const stabilizedThis = array.map((el, index) => [el, index] as [T, number])
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0])
    if (order !== 0) {
      return order
    }
    return a[1] - b[1]
  })
  return stabilizedThis.map((el) => el[0])
}

function EnhancedTableHead(props) {
  const { order, orderBy, onRequestSort, columns, isMyPage = false } = props
  const createSortHandler = (property) => (event: React.MouseEvent<unknown>) => {
    onRequestSort(event, property)
  }

  return (
    <TableHead>
      <TableRow>
        {columns.map((headCell, index) => (
          <StyledTableCell
            style={{ paddingLeft: index === 0 && isMyPage && 0 }}
            key={headCell.id}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            {!headCell.isSort ? (
              <TableSortLabel
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : 'asc'}
                IconComponent={null}
                style={{
                  color: 'rgba(0,0,0,.5)',
                  fontFamily: 'PingFangSC-Regular, sans-serif',
                }}
              >
                {headCell.label}
                {orderBy === headCell.id ? (
                  <Box component="span" style={{ marginLeft: 4 }}>
                    {/* {order === 'desc' ? 'sorted descending' : 'sorted ascending'} */}
                    <SortIcon order={order} />
                  </Box>
                ) : null}
              </TableSortLabel>
            ) : (
              <TableSortLabel
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : 'asc'}
                onClick={createSortHandler(headCell.id)}
                IconComponent={null}
                style={{
                  color: 'rgba(0,0,0,.5)',
                  fontFamily: 'PingFangSC-Regular, sans-serif',
                }}
              >
                {headCell.label}
                {orderBy === headCell.id ? (
                  <Box component="span" style={{ marginLeft: 4 }}>
                    {/* {order === 'desc' ? 'sorted descending' : 'sorted ascending'} */}
                    <SortIcon order={order} />
                  </Box>
                ) : null}
              </TableSortLabel>
            )}
          </StyledTableCell>
        ))}
      </TableRow>
    </TableHead>
  )
}

export default function EnhancedTable(props) {
  const { dataSource, columns, content, pagination, rowClick, isMyPage } = props
  const [order, setOrder] = React.useState<Order>('desc')
  const [orderBy, setOrderBy] = React.useState(columns[0].id)
  const [selected, setSelected] = React.useState<readonly string[]>([])
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(10)

  const handleRequestSort = (event: React.MouseEvent<unknown>, property) => {
    const isAsc = orderBy === property && order === 'asc'
    setOrder(isAsc ? 'desc' : 'asc')
    setOrderBy(property)
  }
  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name)
    let newSelected: readonly string[] = []

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name)
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1))
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1))
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1))
    }

    setSelected(newSelected)

    // eslint-disable-next-line no-unused-expressions
    rowClick && rowClick(event, name)
  }

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  // Avoid a layout jump when reaching the last page with empty rows.
  return (
    <BoxWrapper>
      <TableContainer>
        <Table sx={{ minWidth: 1400 }} aria-labelledby="tableTitle">
          <EnhancedTableHead
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
            columns={columns}
            isMyPage={isMyPage}
          />
          <TableBody>
            {/* eslint-disable-next-line no-nested-ternary */}
            {dataSource && dataSource.length && pagination ? (
              stableSort(dataSource, getComparator(order, orderBy))
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, index) => {
                  const labelId = `enhanced-table-checkbox-${index}`
                  // @ts-ignore
                  return (
                    <StyledTableRow
                      hover
                      onClick={(event) => handleClick(event, row.ticker)}
                      role="checkbox"
                      tabIndex={-1}
                      key={row.ticker}
                    >
                      {content(row, index, labelId)}
                      {/* <StyledTableCell component="th" id={labelId} scope="row">{row.ticker}</StyledTableCell>
                    <StyledTableCell>{row.markPrice}</StyledTableCell>
                    <StyledTableCell>{row.holdings}</StyledTableCell>
                    <StyledTableCell>{row.outstandingDebt}</StyledTableCell>
                    <StyledTableCell>{row.isHoldingStatus}</StyledTableCell>
                    <StyledTableCell>
                      <Action row={row}/>
                    </StyledTableCell>  */}
                    </StyledTableRow>
                  )
                })
            ) : dataSource && dataSource.length && !pagination ? (
              stableSort(dataSource, getComparator(order, orderBy)).map((row, index) => {
                const labelId = `enhanced-table-checkbox-${index}`
                // @ts-ignore

                return (
                  <StyledTableRow
                    hover
                    onClick={(event) => handleClick(event, row.ticker)}
                    role="checkbox"
                    tabIndex={-1}
                    key={row.ticker}
                  >
                    {content(row, index, labelId)}
                    {/* <StyledTableCell component="th" id={labelId} scope="row">{row.ticker}</StyledTableCell>
                    <StyledTableCell>{row.markPrice}</StyledTableCell>
                    <StyledTableCell>{row.holdings}</StyledTableCell>
                    <StyledTableCell>{row.outstandingDebt}</StyledTableCell>
                    <StyledTableCell>{row.isHoldingStatus}</StyledTableCell>
                    <StyledTableCell>
                      <Action row={row}/>
                    </StyledTableCell>  */}
                  </StyledTableRow>
                )
              })
            ) : (
              <StyledTableRow>
                <StyledTableCell style={{ textAlign: 'center' }} colSpan={columns.length} rowSpan={3}>
                  No data
                </StyledTableCell>
              </StyledTableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
      {pagination ? (
        <TablePagination
          rowsPerPageOptions={[5, 10, 20]}
          component="div"
          count={(dataSource && dataSource.length) || 0}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      ) : null}
    </BoxWrapper>
  )
}
