import {
  Box,
  Typography,
  IconButton,
  Button,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Paper,
  Badge,
} from '@mui/material'
import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Fbtc20Icon, SetIcon } from 'components/uikit'
import AddIcon from '@mui/icons-material/Add'
import RemoveIcon from '@mui/icons-material/Remove'
import ClearIcon from '@mui/icons-material/Clear'
import { useCurrentCpt, useShortActiveAction, useShortPendingAction } from 'state/hooks'
import tokens from 'config/constants/tokens'
import useComputedPrice from 'hooks/useComputedPrice'
import useSystemParams from 'hooks/useSystemParams'
import { useWeb3React } from 'web3-react-core'
import contracts from 'config/constants/contracts'
import { utils } from 'ethers'
import usePosContract from 'hooks/usePosContract'
import IconMap from 'components/IconMap'
import { ActionType, TokenType } from 'state/Short/reducer'
import { ChainTxUrl } from 'config/ChainInfo'
import useToast from 'hooks/useToast'
import { AlertButton, FlexWrap } from 'components/util'
import { useAppSelector } from 'state'
import formatTheValue, { toFixed } from 'utils/formatTheValue'
import { tokensSymbol } from 'config/constants/types'
import { ShortThresholdValue } from 'config/ThresholdValue'
import SetMenu from './SetMenu'
import useHashPowerToken from '../../../hooks/useHashPowerToken'
import PendingActionsComponents from './PendingActionsComponents'
import { ActionButton, InputBox, pendingStyle } from './styledComp'

const TableBox: React.FC = () => {
  const [hoveredRow, setHoveredRow] = useState<number>(-1)

  const { token } = useCurrentCpt()

  const { clearAllPendingAction, pendingActionList, deletePendingAction } = useShortPendingAction()
  const { setActionInfo, setActiveAmount, setActionIndex } = useShortActiveAction()
  const handleMouseEnter = (index: number) => {
    setHoveredRow(index)
  }

  const handleMouseLeave = () => {
    setHoveredRow(-1)
  }
  return (
    <TableContainer
      component={Paper}
      sx={{
        backgroundColor: 'transparent',
        border: 'none',
        boxShadow: 'none',
      }}
    >
      <Table aria-label="simple table">
        <TableHead>
          <TableRow sx={pendingStyle.tableHeader}>
            <TableCell sx={pendingStyle.tableHeadCell}>{pendingActionList.length} Pending Actions</TableCell>
            <TableCell align="right" sx={pendingStyle.tableHeadCell}>
              <Button
                size="small"
                sx={pendingStyle.clearAll}
                onClick={() => {
                  clearAllPendingAction()
                }}
              >
                Clear All
              </Button>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody sx={pendingStyle.tableBody}>
          {pendingActionList.map((row, index) => (
            <TableRow
              key={row.tokenType + row.token}
              sx={pendingStyle.tableBodyRow}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
              onClick={() => {
                setActionInfo({ token: row.token, tokenType: row.tokenType })
                setActiveAmount(row.amount)
                setActionIndex(index)
              }}
            >
              <TableCell sx={pendingStyle.tableBodyCell}>
                <Badge
                  color="error"
                  overlap="circular"
                  badgeContent={
                    row.type === ActionType.ADD ? (
                      <AddIcon sx={{ width: 7, height: 7, color: '#000' }} />
                    ) : (
                      <RemoveIcon sx={{ width: 7, height: 7, color: '#000' }} />
                    )
                  }
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  sx={{
                    '.MuiBadge-badge': {
                      padding: 0,
                      minWidth: 10,
                      minHeight: 10,
                      height: 10,
                      position: 'absolute',
                      bottom: '20%',
                      right: '24%',
                      backgroundColor: 'rgba(110, 214, 243, 1)',
                      borderWidth: 1,
                      borderColor: 'rgba(235, 247, 255, 1)',
                      borderStyle: 'solid',
                    },
                  }}
                >
                  {IconMap[row.token]()}
                </Badge>
                <Typography sx={{ fontWeight: 600, marginLeft: '5px' }}>
                  {row.tokenType} {row.token}
                </Typography>
              </TableCell>
              <TableCell align="right" sx={{ p: 0, height: 66 }}>
                <Box sx={{ display: 'flex', justifyContent: 'end' }}>
                  <Box>
                    <Typography sx={{ fontSize: 14 }}>{row.amount}</Typography>
                    <Typography sx={{ fontSize: 12, color: 'rgba(0, 0, 0, 0.5)' }}>
                      {row.price} {token}
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      transition: 'all 0.3s',
                      width: hoveredRow === index ? 32 : 0,
                      display: 'flex',
                      alignItems: 'center',
                    }}
                  >
                    <IconButton
                      onClick={(e) => {
                        e.stopPropagation()
                        deletePendingAction(row.token, row.tokenType)
                        // dispatch(deleteShortPendingActionsList({ index }))
                      }}
                      sx={{
                        transition: 'all 0.3s',
                        opacity: hoveredRow === index ? 1 : 0,
                        color: 'rgba(0, 0, 0, 1)',
                      }}
                    >
                      <ClearIcon sx={{ width: 16, height: 16 }} />
                    </IconButton>
                  </Box>
                </Box>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const WalletBalance: React.FC = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const [maxSlippageValue, setMaxSlippageValue] = useState<any>(localStorage.getItem('slippage') ?? '0.5')
  const [deadline, setDeadlineValue] = useState<any>(10)
  const open = Boolean(anchorEl)

  const computedPrice = useComputedPrice()

  const { hashPowerTokenList, walletTokenBalance, setRefresh, computed } = useHashPowerToken()
  const { position, decimals, token, avatarAssetList } = useCurrentCpt()

  const { pendingActionList, shortAmount } = useShortPendingAction()
  const { activeInfo, setActiveAmount, clearActive, onApprove } = useShortActiveAction()
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget)
  }
  const handleClose = () => {
    setAnchorEl(null)
  }
  const currentCpt = useAppSelector((state) => state.collateralManage.currentCPT)
  const { positionInfoValues } = currentCpt
  const [forecastGuaranteeDays, setForecastGuaranteeDays] = useState(0)

  // eslint-disable-next-line consistent-return
  const onMax = () => {
    if (activeInfo.tokenType !== 'Short') {
      return setActiveAmount(avatarAssetList[activeInfo.token])
    }

    if (positionInfoValues && activeInfo.tokenType === 'Short') {
      const { rewards } = currentCpt
      const { totalAssetsValues, interestGuaranteed, totalObligation } = positionInfoValues
      const allBalance = interestGuaranteed - totalObligation
      const maxMintAmountG = (() => {
        if (allBalance === 0) {
          return 0
        }
        // @ts-ignore
        if ((rewards / 10) * ShortThresholdValue.days === 0) {
          return Infinity
        }
        // @ts-ignore
        return toFixed(allBalance / ((rewards / 10) * ShortThresholdValue.days), token)
      })()
      const maxMintAmountC = toFixed(
        totalAssetsValues / (ShortThresholdValue.ratio / 100) / currentCpt.tokensValues[currentCpt.token] -
          formatTheValue(currentCpt.balance, currentCpt.decimals),
        currentCpt.decimals,
      )

      // eslint-disable-next-line no-nested-ternary
      // @ts-ignore
      // eslint-disable-next-line no-nested-ternary
      const maxAmount =
        // eslint-disable-next-line no-nested-ternary
        maxMintAmountC > 0 && maxMintAmountG > 0
          ? maxMintAmountG * 1 > maxMintAmountC * 1
            ? maxMintAmountC
            : maxMintAmountG
          : 0

      if (maxAmount > 0) {
        computed(maxAmount, currentCpt, (ratio, days) => {
          setActiveAmount(maxAmount)
          setNewRate(ratio * 100)
          setForecastGuaranteeDays(days)
        })
      }
    }
  }

  const { toastError, toastSuccess, toastInfo } = useToast()
  const { mintSellHelpContract, signer } = useSystemParams()
  const { active, chainId } = useWeb3React()

  const computedOldValue = useMemo(() => {
    const _value =
      hashPowerTokenList &&
      hashPowerTokenList[0] &&
      hashPowerTokenList[0].positionInfoValues &&
      hashPowerTokenList[0].positionInfoValues.cRate
    if (_value === 0 || !_value) {
      return 0
    }
    if (_value === 'Infinity' || _value === Infinity) {
      return '+'
    }
    return `${_value}%`
  }, [hashPowerTokenList])

  const [newRate, setNewRate] = useState<any>(computedOldValue)

  const posContract = usePosContract(active, signer)

  const _tokenList = useCallback(() => {
    return pendingActionList
      .filter((item) => item.tokenType !== TokenType.SHORT && item.tokenType !== TokenType.NEUTRALIZE)
      .map((item) => tokens[item.token].address[chainId])
  }, [chainId, pendingActionList])

  const _tokenAmounts = useCallback(() => {
    return pendingActionList
      .filter((item) => item.tokenType !== TokenType.SHORT && item.tokenType !== TokenType.NEUTRALIZE)
      .map((item) => utils.parseUnits(item.amount.toString(), tokens[item.token].decimals))
  }, [pendingActionList])

  const { addPendingAction, updatePendingAction, clearAllPendingAction, pendingTokenTypes } = useShortPendingAction()

  const computedRate = useMemo(async () => {
    if (mintSellHelpContract && mintSellHelpContract.previewPositionInfoAfterShort) {
      if (!shortAmount) {
        setNewRate(computedOldValue)
        return
      }

      if (shortAmount) {
        const result = await mintSellHelpContract.previewPositionInfoAfterShort(
          position,
          utils.parseUnits(shortAmount.toString(), decimals),
          _tokenList(),
          _tokenAmounts(),
        )
        setNewRate(result.targetCollateralRatio)
        // eslint-disable-next-line consistent-return
        return result
      }
    }
    // eslint-disable-next-line consistent-return
    return { targetCollateralRatio: 0 }
  }, [_tokenAmounts, _tokenList, computedOldValue, decimals, mintSellHelpContract, position, shortAmount])

  const submintShort = async () => {
    const result = await computedRate
    console.log('result', result)
    console.log('first', [
      utils.parseUnits(shortAmount.toString(), decimals),
      utils.parseUnits((shortAmount - maxSlippageValue / 100).toString(), decimals),
      _tokenList(),
      _tokenAmounts(),
      result.targetCollateralRatio,
      Number((Date.now() / 1000).toFixed()) + deadline * 60,
    ])
    const inter = new utils.Interface(contracts.actionMintSell.abi)

    const data = inter.encodeFunctionData('short', [
      utils.parseUnits(shortAmount.toString(), decimals),
      utils.parseUnits((shortAmount - maxSlippageValue / 100).toString(), decimals),
      _tokenList(),
      _tokenAmounts(),
      result.targetCollateralRatio,
      Number((Date.now() / 1000).toFixed()) + deadline * 60,
    ])
    try {
      const txInfo = await posContract.delegateCall(contracts.actionMintSell.address[chainId], data)
      try {
        if (txInfo !== undefined && txInfo.hash) {
          toastInfo(
            'Submitted',
            <>
              <p style={{ marginBottom: 24 }}>Waiting for confirmation</p>
              <FlexWrap style={{ flexDirection: 'row-reverse' }}>
                <AlertButton
                  onClick={() => {
                    window.open(`${ChainTxUrl}/${txInfo.hash}`)
                  }}
                >
                  View on Explorer
                </AlertButton>
              </FlexWrap>
            </>,
          )
          const info = await txInfo.wait()

          if (info.confirmations !== 0) {
            toastSuccess('Success')
            setRefresh((state) => !state)
            clearActive()
            clearAllPendingAction()
          }
        }
      } catch (error) {
        if (error) {
          toastError(
            'Failed',
            <FlexWrap>
              <p style={{ whiteSpace: 'pre-wrap' }}>{(error as Error).message}</p>
            </FlexWrap>,
          )
        }
      }
    } catch (error) {
      if (error) {
        toastError(
          'Failed',
          <FlexWrap style={{ flexDirection: 'column' }}>
            <p style={{ whiteSpace: 'pre-wrap' }}>{(error as Error).message}</p>
            <p style={{ whiteSpace: 'pre-wrap' }}>{(error as any)?.data?.message}</p>
          </FlexWrap>,
        )
      }
    }
  }

  const disabledAddAction = useMemo(() => {
    const _balance = walletTokenBalance && walletTokenBalance[activeInfo.token]
    const condition1 = activeInfo.amount && activeInfo.amount > _balance
    const condition2 = Number(activeInfo.amount) <= 0
    // c rate < 120% || g days < 20
    return (activeInfo.tokenType !== TokenType.SHORT && condition1) || condition2
  }, [activeInfo.amount, activeInfo.token, activeInfo.tokenType, walletTokenBalance])
  const { supplyAmount } = useShortPendingAction()

  useEffect(() => {
    if (active && hashPowerTokenList && hashPowerTokenList.length) {
      if (hashPowerTokenList[0].positionInfoValues && hashPowerTokenList[0].positionInfoValues.cRate) {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        computed(supplyAmount.get(tokensSymbol.FBTC20) || 0, currentCpt, (ratio, gDays, _AllAmount) => {
          setForecastGuaranteeDays(gDays)
        })
      }
    }
  }, [hashPowerTokenList, active, supplyAmount, currentCpt, computed])

  const disabledSubmit = useMemo(() => {
    return (
      (newRate && newRate < ShortThresholdValue.ratio * 100) ||
      (forecastGuaranteeDays && forecastGuaranteeDays < ShortThresholdValue.days)
    )
  }, [forecastGuaranteeDays, newRate])
  const isUpdate = useMemo(() => activeInfo.activeIndex !== -1, [activeInfo.activeIndex])

  const [availableShowFlag, setAvailableShowFlag] = useState(false)
  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Typography sx={{ fontSize: 16, fontWeight: 500 }}>
          {activeInfo?.token ? `${activeInfo.tokenType} ${activeInfo.token}` : `${token} Wallet Balance`}
        </Typography>

        <IconButton
          aria-controls={open ? 'basic-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}
          onClick={handleClick}
          sx={{
            p: 0,
            width: 24,
            height: 24,
          }}
        >
          <SetIcon />
        </IconButton>

        <SetMenu
          handleClose={handleClose}
          anchorEl={anchorEl}
          maxSlippageValue={maxSlippageValue}
          open={open}
          deadline={deadline}
          setMaxSlippageValue={(value) => {
            localStorage.setItem('slippage', value)
            setMaxSlippageValue(value)
          }}
          setDeadlineValue={(value) => setDeadlineValue(value)}
        />
      </Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 1.6 }}>
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: 1.2,
            width: '100%',
            height: activeInfo?.token && activeInfo.tokenType !== 'Short' && availableShowFlag ? '52px' : '45px',
            transition: '300ms all',
          }}
        >
          {activeInfo?.token ? (
            IconMap[activeInfo?.token]({ style: { width: 42, height: 42 } })
          ) : (
            <Fbtc20Icon style={{ width: 42, height: 42 }} />
          )}
          {/* {JSON.stringify(activeInfo)} */}
          <div
            style={{
              transition: 'all 300ms',
            }}
          >
            <InputBox>
              <input
                readOnly={!activeInfo?.token}
                type="number"
                placeholder={!activeInfo?.token ? walletTokenBalance[token] : 0}
                value={activeInfo?.amount.toString() ? activeInfo?.amount : ''}
                onFocus={() => setAvailableShowFlag(true)}
                onBlur={() => {
                  if (activeInfo?.amount.toString() === '' || activeInfo?.amount < 0) {
                    setAvailableShowFlag(false)
                  }
                }}
                onChange={(event) => {
                  const inputValue = event.target.value
                  let formattedValue = ''
                  const cleanedValue = inputValue.replace(/[^\d.]/g, '')
                  if (cleanedValue.includes('.')) {
                    const parts = cleanedValue.split('.')
                    parts[1] = parts[1]?.slice(0, 8)
                    formattedValue = parts.join('.')
                  } else {
                    formattedValue = cleanedValue
                  }
                  setActiveAmount(formattedValue || '')
                }}
              />
            </InputBox>

            {activeInfo?.token && availableShowFlag && activeInfo.tokenType !== 'Short' ? (
              <Box
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: 1,
                  width: '100%',
                  color: 'rgba(0,0,0,.5)',
                  paddingLeft: '4px',
                }}
              >
                {avatarAssetList[activeInfo?.token ?? token]} Available
              </Box>
            ) : (
              <></>
            )}
          </div>
        </Box>
        {activeInfo?.token && activeInfo.tokenType !== 'Short' && (
          <Box>
            <Button
              size="small"
              sx={{
                textTransform: 'none',
                borderRadius: 1.8,
                color: 'rgba(110, 214, 243, 1)',
              }}
              onClick={() => onMax()}
            >
              Max
            </Button>
          </Box>
        )}
      </Box>
      {activeInfo?.token && (
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            gap: 3,
            mt: 1.6,
          }}
        >
          <ActionButton
            variant="contained"
            disableElevation
            onClick={() => {
              clearActive()
            }}
          >
            Cancel
          </ActionButton>
          <ActionButton
            disabled={disabledAddAction}
            variant="contained"
            disableElevation
            onClick={() => {
              if (disabledAddAction) return
              const action = {
                token: activeInfo.token,
                tokenType: activeInfo.tokenType,
                amount: Number(activeInfo.amount),
                price:
                  token !== activeInfo.token
                    ? computedPrice(
                        {
                          outputToken: {
                            token,
                            decimals,
                          },
                          inputToken: {
                            token: activeInfo.token,
                            decimals: tokens[activeInfo.token].decimals,
                          },
                        },
                        Number(activeInfo.amount),
                      ).toFixed(decimals)
                    : activeInfo.amount,
              }
              if (!activeInfo.approveStatus) {
                onApprove().then((res) => {
                  if (res) {
                    addPendingAction(action)
                    clearActive()
                  }
                })
              } else if (isUpdate) {
                updatePendingAction(action)
                clearActive()
              } else {
                addPendingAction(action)
                clearActive()
              }
            }}
          >
            {!activeInfo.approveStatus ? 'Approve & Add' : `${isUpdate ? 'Update' : 'Add'} Action`}
          </ActionButton>
        </Box>
      )}
      {pendingActionList.length > 0 && (
        <Box>
          <TableBox />
        </Box>
      )}
      {token && pendingTokenTypes.get(token)?.includes(TokenType.SHORT) && (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 3 }}>
          <ActionButton onClick={() => submintShort()} variant="contained" disableElevation disabled={disabledSubmit}>
            Submit Transaction
          </ActionButton>
        </Box>
      )}

      <PendingActionsComponents newRate={newRate} forecastGuaranteeDay={forecastGuaranteeDays} />
    </Box>
  )
}
export default WalletBalance
