import React, { lazy } from 'react'
import { Route, Switch } from 'react-router-dom'
import { ResetCSS } from 'components/uikit'
import BigNumber from 'bignumber.js'
import useEagerConnect from 'hooks/useEagerConnect'

import Collateral from 'views/Collateral'
import CollateralManage from 'views/Collateral/ManagerPage'
import ShortNeutralize from 'views/ShortNeutralize'
// import Mint from 'views/Mint'
import MyPage from 'views/MyPage'
import Faucet from 'views/Faucet'
import Liquidation from 'views/Liquidation'
import LiquidationDetail from 'views/Liquidation/detail'
import Trade from 'views/Trade'
import ClaimedHistory from 'views/History/Claimed'
import Obligation from 'views/History/Obligation'
import Dashboard from 'views/Dashboard'
// import Onboarding from 'views/Onboarding'
// import QuickMode from 'views/QuickMode'

import GlobalStyle from './style/Global'
import Menu from './components/Menu'
import SuspenseWithChunkError from './components/SuspenseWithChunkError'
import ToastListener from './components/ToastListener'
import PageLoader from './components/PageLoader'

const NotFound = lazy(() => import('./views/NotFound'))

// This config is required for number formatting
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
})

const App: React.FC = () => {
  useEagerConnect()

  return (
    <>
      <ResetCSS />
      <GlobalStyle />
      <Menu>
        <SuspenseWithChunkError fallback={<PageLoader />}>
          <Switch>
            <Route path="/mypage" exact>
              <MyPage />
            </Route>

            <Route path="/collateral" exact component={Collateral} />
            <Route path="/collateral/manage" exact component={CollateralManage} />
            <Route path="/shortneutralize" exact component={ShortNeutralize} />

            {/* <Route path="/mint" exact component={Mint} />
            <Route path="/mint/:id(0|1)" exact component={Mint} /> */}
            <Route path="/faucet" exact component={Faucet} />

            <Route path="/liquidation" exact component={Liquidation} />
            <Route path="/liquidation/detail" exact component={LiquidationDetail} />
            <Route path="/trade" exact component={Trade} />

            <Route path="/history/claimed/:id(0|1|2)" component={ClaimedHistory} />
            <Route path="/history/obligation" component={Obligation} />

            <Route path="/dashboard" component={Dashboard} />
            {/* <Route path="/onboarding" component={Onboarding} /> */}
            {/* <Route path="/quickMode" exact component={QuickMode} /> */}
            {/* <Route path="/quickMode/:name(mint|neutralize|withdraw)" exact component={QuickMode} /> */}
            {/* 404 */}
            <Route component={NotFound} />
          </Switch>
        </SuspenseWithChunkError>
      </Menu>
      <ToastListener />
    </>
  )
}

export default React.memo(App)
