import { configureStore } from '@reduxjs/toolkit'
import { useDispatch, TypedUseSelectorHook, useSelector } from 'react-redux'
import { load, save } from 'redux-localstorage-simple'
import { setupListeners } from '@reduxjs/toolkit/query/react'

import header from './header/reducer'
import collateralManage from './CollateralManage/reducer'
import holdings from './Hodlings/reducer'
import liquidation from './Liquidation/reducer'
import short from './Short/reducer'

const PERSISTED_KEYS: string[] = ['collateralManage', 'holdings']

const store = configureStore({
  devTools: process.env.NODE_ENV !== 'production',
  reducer: {
    header,
    collateralManage,
    holdings,
    liquidation,
    short,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({ thunk: true, serializableCheck: false }).concat(
      save({ states: PERSISTED_KEYS, debounce: 1000 }),
    ),
  preloadedState: load({ states: PERSISTED_KEYS, disableWarnings: true }),
})

setupListeners(store.dispatch)

/**
 * @see https://redux-toolkit.js.org/usage/usage-with-typescript#getting-the-dispatch-type
 */
export type AppDispatch = typeof store.dispatch
export const useAppDispatch = () => useDispatch<AppDispatch>()

export type AppState = ReturnType<typeof store.getState>
export const useAppSelector: TypedUseSelectorHook<AppState> = useSelector

export default store
