import Table, { StyledTableCell } from 'components/Table'
import SearchIcon from '@mui/icons-material/Search'
import useSystemParams from 'hooks/useSystemParams'
import { formatWBTC } from 'utils/formatTheValue'
import addressShort from 'utils/addressShort'
import useHashPowerToken from 'hooks/useHashPowerToken'
import MainTitle from 'components/util/MainTitle'
import tokens, { tokenType } from 'config/constants/tokens'
import { Search, StyledInputBase, SearchIconWrapper } from '../Collateral'

const Wrapper = styled('div')`
  padding-right: 44px;
`

const CardWrapper = styled(Card)`
  .pagination {
  }
`
const headCells = [
  {
    id: 'avatar',
    numeric: false,
    disablePadding: true,
    label: 'Collateral',
  },
  {
    id: 'ticker',
    numeric: true,
    disablePadding: false,
    label: 'Ticker',
  },
  {
    id: 'markPrice',
    numeric: true,
    disablePadding: false,
    label: 'Mark Price',
  },
  /*  {
    id: 'collateralMarkPrice',
    numeric: true,
    disablePadding: false,
    label: 'Collateral Mark Value',
  },
  {
    id: 'collateralMarketValue',
    numeric: true,
    disablePadding: false,
    label: 'Collateral Market Value',
  }, */
  {
    id: 'outstandingDebtPosition',
    numeric: true,
    disablePadding: false,
    label: ' Outstanding Debt Position',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    label: 'Collateral Ratio',
  },
  {
    id: 'guaranteeDays',
    numeric: true,
    disablePadding: false,
    label: 'Prepaid Interest Coverage',
  },
]

function Liquidation(props) {
  const { history } = props
  const [holderText, setPlaceholder] = useState('Ticker')
  const [searchVal, setSearchVal] = useState()
  const onSearch = useCallback(({ target }) => {
    const { value } = target
    setSearchVal(value)
  }, [])
  const { hashPowerTokenList } = useHashPowerToken()
  const { accountContract } = useSystemParams()

  const getLiqList = useCallback(async () => {
    if (accountContract) {
      // eslint-disable-next-line no-restricted-syntax
      for (const [, tokenData] of Object.entries(tokens)) {
        if (tokenData.type === tokenType.CPT) {
          // eslint-disable-next-line no-await-in-loop
          // const validActors = await accountContract.validActors(tokenData.address[chainId])
        }
      }
    }
  }, [accountContract])

  useEffect(() => {
    getLiqList()
  }, [getLiqList])

  return (
    <Wrapper>
      <MainTitle style={{ marginBottom: 14, fontFamily: 'Heebo Medium' }}>Liquidation</MainTitle>
      <CardWrapper>
        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase
            type="search"
            placeholder={holderText}
            inputProps={{ 'aria-label': 'search' }}
            value={searchVal}
            onInput={onSearch}
            onFocus={() => {
              setPlaceholder('')
            }}
            onBlur={() => {
              setPlaceholder('Ticker')
            }}
          />
        </Search>
        <Table
          rowClick={() => {
            history.push('/liquidation/detail')
          }}
          pagination
          dataSource={hashPowerTokenList || []}
          columns={headCells}
          content={(row, index, labelId) => (
            <>
              <StyledTableCell component="th" id={labelId} scope="row">
                <span style={{ color: '#2AA1FF' }}>{addressShort(row.avatar)}</span>
              </StyledTableCell>
              <StyledTableCell>{row.ticker}</StyledTableCell>
              <StyledTableCell>
                {Number(row.position) ? formatWBTC(row.tokensValues[row.token]) : 0} WBTC
              </StyledTableCell>
              {/*  <StyledTableCell>{row.collateralMarkPrice}</StyledTableCell>
              <StyledTableCell>{row.collateralMarketValue}</StyledTableCell> */}
              {/*  @ts-ignore */}
              <StyledTableCell>
                {Number(row.position) ? row.balance / (BigNumber.from(10) as any).pow(row.decimals) : 0}
              </StyledTableCell>
              {/* eslint-disable-next-line no-nested-ternary */}
              <StyledTableCell>
                {row.positionInfoValues && Object.keys(row.positionInfoValues).length
                  ? row.positionInfoValues.cRate.includes('NaN')
                    ? 0
                    : row.positionInfoValues.cRate === 'Infinity'
                    ? '+'
                    : `${row.positionInfoValues.cRate}%`
                  : 0}
              </StyledTableCell>
              {/* eslint-disable-next-line no-nested-ternary */}
              <StyledTableCell>
                {row.positionInfoValues && Object.keys(row.positionInfoValues).length && row.positionInfoValues.gDays
                  ? row.positionInfoValues.gDays === 'Infinity'
                    ? '+'
                    : row.positionInfoValues.gDays
                  : 0}{' '}
                days
              </StyledTableCell>
            </>
          )}
        />
      </CardWrapper>
    </Wrapper>
  )
}

export default Liquidation
