export function useShortPendingAction() {
  const dispatch = useAppDispatch()
  const { pendingActions } = useAppSelector((state) => state.short)
  const _pendingActions = useMemo(() => new Map(pendingActions), [pendingActions])
  // add action
  const addPendingAction = useCallback(
    (data: Pick<PendingAction, 'amount' | 'price' | 'token' | 'tokenType'>) => {
      const _actions = _pendingActions.get(data.token) || []
      _pendingActions.set(data.token, [..._actions, { ...data, type: ActionType.ADD }])

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [dispatch, _pendingActions],
  )
  // delete action
  const deletePendingAction = useCallback(
    (token: string, tokenType: TokenType) => {
      const _actions = [...(_pendingActions.get(token) || [])]
      const _nextList = _actions.filter((item) => item.tokenType !== tokenType)
      _pendingActions.set(token, _nextList)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [dispatch, _pendingActions],
  )
  // clear all action
  const clearAllPendingAction = useCallback(() => {
    dispatch(setShortPendingAction({ actions: new Map() }))
  }, [dispatch])

  // update action
  const updatePendingAction = useCallback(
    (data: Pick<PendingAction, 'amount' | 'price' | 'token' | 'tokenType'>) => {
      const _actions = [...(_pendingActions.get(data.token) || [])]
      const _index = _actions.findIndex((item) => item.tokenType === data.tokenType)
      _actions.splice(_index, 1, { ...data, type: ActionType.ADD })
      _pendingActions.set(data.token, _actions)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [_pendingActions, dispatch],
  )

  const updateNeutralizePendingAction = useCallback(
    (data: Pick<NeetralizePendingAction, 'amount' | 'price' | 'token' | 'tokenType' | 'neutralizeType'>) => {
      const _actions = [...(_pendingActions.get(data.token) || [])]
      const _index = _actions.findIndex((item) => item.neutralizeType === data.neutralizeType)
      _actions.splice(_index, 1, { ...data, type: ActionType.ADD })
      _pendingActions.set(data.token, _actions)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [_pendingActions, dispatch],
  )

  const deleteNeutralizePendingAction = useCallback(
    (token: string, tokenType: any) => {
      const _actions = [...(_pendingActions.get(token) || [])]
      const _nextList = _actions.filter((item) => item.neutralizeType !== tokenType)
      _pendingActions.set(token, _nextList)

      dispatch(setShortPendingAction({ actions: _pendingActions }))
    },
    [dispatch, _pendingActions],
  )

  // get token type
  const pendingTokenTypes = useMemo(() => {
    const _map = new Map<string, TokenType[]>()
    pendingActions.forEach((item, index) => {
      _map.set(
        index,
        item.map((i) => i.tokenType),
      )
    })
    return _map
  }, [pendingActions])

  const pendingActionList = useMemo(() => {
    return Array.from(pendingActions.values()).flat()
  }, [pendingActions])

  const tokenAmount = useMemo(() => {
    const _obg = {}
    pendingActionList.forEach((item) => {
      if (item.tokenType === TokenType.SUPPLY || item.tokenType === TokenType.WithDraw) {
        _obg[item.token] = item.amount
      }
    })
    return _obg
  }, [pendingActionList])

  const shortAmount = useMemo(() => {
    let _amount = 0
    pendingActionList.forEach((item) => {
      if (item.tokenType === TokenType.SHORT) {
        _amount = item.amount
      }
    })
    return _amount
  }, [pendingActionList])

  const supplyAmount = useMemo(() => {
    const _amount = new Map<string, number>()
    pendingActions.forEach((items) => {
      const item = items.filter((i) => i.tokenType === TokenType.SUPPLY)[0]
      if (item) {
        _amount.set(item.token, item.amount)
      }
    })
    return _amount
  }, [pendingActions])
  return {
    addPendingAction,
    clearAllPendingAction,
    deletePendingAction,
    updatePendingAction,
    deleteNeutralizePendingAction,
    updateNeutralizePendingAction,
    pendingTokenTypes,
    pendingActions,
    pendingActionList,
    tokenAmount,
    shortAmount,
    supplyAmount,
  }
}

export function useCurrentAllowedAssets() {
  const { chainId } = useWeb3React()
  const { allowedAssets, avatarAssetList, position } = useCurrentCpt()
  let allowedAssetInfo: {
    symbol: string
    address: {
      [key: string]: string
    }
    icon: (props?: any) => JSX.Element
    decimals: number
    balance: number
  }[] = []
  if (Number(position) !== 0) {
    let _allowedAssets: string[] = []
    if (allowedAssets && allowedAssets.length) {
      _allowedAssets = allowedAssets.map((item) => item.toLowerCase()) as string[]
    }
    Object.keys(tokens).forEach((key) => {
      const token = tokens[key]
      if (chainId && token.address[chainId] && _allowedAssets.includes(token.address[chainId].toLowerCase())) {
        allowedAssetInfo.push({ ...token, balance: avatarAssetList[key] })
      }
    })
  } else {
    allowedAssetInfo = Object.values(tokens).map((item) => ({ ...item, balance: 0 }))
  }

  return allowedAssetInfo
}

export function useShortActiveTokenApprove() {
  const { account, chainId, active } = useWeb3React()
  const activeInfo = useAppSelector((state) => state.short.activeInfo)
  const { position } = useCurrentCpt()
  const { onCheckApprove } = useCheckApprove()
  const { handleApprove } = useApprove(account, activeInfo.token ? tokens[activeInfo.token].address[chainId] : '0')
  const [approveStatus, setApproveStatus] = useState(false)
  const { toastSuccess, toastError } = useToast()

  useEffect(() => {
    if (account && chainId && activeInfo.token) {
      onCheckApprove(account, tokens[activeInfo.token].address[chainId], position)
        .then((res) => {
          if (res && res._hex > 0) {
            setApproveStatus(true)
          } else {
            setApproveStatus(false)
          }
        })
        .catch(() => {
          setApproveStatus(false)
        })
    }
  }, [account, activeInfo, activeInfo.token, chainId, onCheckApprove, position, setApproveStatus])
  const onApprove = async () => {
    if (active && chainId && Number(position)) {
      try {
        const info = await handleApprove(position)
        setApproveStatus(true)
        if (info.hash) {
          const result = await info.wait()
          if (result) {
            toastSuccess('Approve Success')
          }
          return result
        }
      } catch ({ message }: any) {
        setApproveStatus(false)
        toastError('Failed', `${message}`)
        return message
      }
    }
    return null
  }

  return { approveStatus, onApprove }
}

// short active action and info
export function useShortActiveAction() {
  const dispatch = useAppDispatch()
  const { activeAmount, activeInfo, activeIndex } = useAppSelector((state) => state.short)
  const { pendingActionList } = useShortPendingAction()
  const setActionInfo = useCallback(
    (data: { token?: string; tokenType?: TokenType } = {}) => {
      dispatch(updateActionInfo({ activeInfo: data }))
      const _index = pendingActionList.findIndex(
        (item) => item.token === data.token && item.tokenType === data.tokenType,
      )
      if (_index >= 0) {
        dispatch(updateActiveActionIndex({ index: _index }))
      }
    },
    [dispatch, pendingActionList],
  )
  const setActiveTab = useCallback(
    (index: number) => {
      dispatch(updateActiveTab({ index }))
    },
    [dispatch],
  )
  const setActionIndex = useCallback(
    (index: number) => {
      dispatch(updateActiveActionIndex({ index }))
    },
    [dispatch],
  )
  const setActiveAmount = useCallback(
    (amount: any) => {
      dispatch(updateActiveAmount({ amount }))
    },
    [dispatch],
  )
  const clearActive = useCallback(() => {
    setActionInfo()
    setActiveAmount('')
    setActionIndex(-1)
  }, [setActionIndex, setActionInfo, setActiveAmount])

  const { approveStatus, onApprove } = useShortActiveTokenApprove()
  return {
    setActionInfo,
    setActiveAmount,
    clearActive,
    setActiveTab,
    activeInfo: activeInfo?.token
      ? {
          ...activeInfo,
          amount: activeAmount,
          activeIndex,
          approveStatus,
        }
      : { token: null, amount: '', tokenType: null, activeIndex: -1, approveStatus: false },
    setActionIndex,
    onApprove,
  }
}
