import React, { useMemo } from 'react'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { IconButton, Typography, Box } from '@mui/material'
import AddIcon from '@mui/icons-material/Add'
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward'
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward'
import CloseIcon from '@mui/icons-material/Close'
import useComputedPrice from 'hooks/useComputedPrice'
import { useShortActiveAction, useCurrentAllowedAssets, useCurrentCpt, useShortPendingAction } from 'state/hooks'
import { TokenType } from 'state/Short/reducer'
import useHashPowerToken from 'hooks/useHashPowerToken'
import tokens from 'config/constants/tokens'
import { ActionPanelStyle } from './styledComp'

const BalanceDisplay: React.FC<{ state: 'up' | 'down' | 'close'; val: number | string; token: string }> = ({
  state,
  val,
  token,
}) => {
  const { clearActive, setActionInfo, setActiveAmount } = useShortActiveAction()
  return (
    <Box
      sx={{
        width: 74,
        height: 32,
        borderRadius: 10,
        border: '1px solid rgba(70, 79, 96, 0.2)',
        color: 'rgba(134, 143, 160, 1)',
        display: 'flex',
        alignItems: 'center',
        background: 'rgba(247, 249, 252, 1)',
        justifyContent: 'space-between',
        p: 1,
        cursor: 'pointer',
        transition: 'all 0.3s',
      }}
      onClick={() => {
        if (state === 'close') {
          clearActive()
        }
        if (state === 'up') {
          setActionInfo({ token, tokenType: TokenType.SUPPLY })
          setActiveAmount(Number(val))
        }
        if (state === 'down') {
          setActionInfo({ token, tokenType: TokenType.WithDraw })
          setActiveAmount(Number(val))
        }
      }}
    >
      {state === 'up' && <ArrowUpwardIcon sx={{ width: 12, height: 12 }} />}
      {state === 'close' && <CloseIcon sx={{ width: 12, height: 12 }} />}
      {state === 'down' && <ArrowDownwardIcon sx={{ width: 12, height: 12 }} />}
      <Typography
        sx={{
          fontSize: 10,
          mt: 0.1,
          width: '40px',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
          textAlign: 'right',
        }}
      >
        {val}
      </Typography>
    </Box>
  )
}

const ActionPanel: React.FC<{
  pendingTokenTypes: Map<string, TokenType[]>
  symbol: string
  tokenAmount: { [key: string]: number }
}> = ({ pendingTokenTypes, symbol, tokenAmount }) => {
  const { activeInfo, setActionInfo } = useShortActiveAction()
  const { walletTokenBalance } = useHashPowerToken()

  const disabeldAction = useMemo(
    () => walletTokenBalance && walletTokenBalance[symbol] === 0,
    [walletTokenBalance, symbol],
  )
  if (
    activeInfo.token === symbol &&
    (activeInfo.tokenType === TokenType.SUPPLY || activeInfo.tokenType === TokenType.WithDraw)
  ) {
    return <BalanceDisplay token={symbol} state="close" val={activeInfo.tokenType} />
  }
  if (pendingTokenTypes.get(symbol)?.includes(TokenType.SUPPLY)) {
    return <BalanceDisplay token={symbol} state="up" val={tokenAmount[symbol]} />
  }
  if (pendingTokenTypes.get(symbol)?.includes(TokenType.WithDraw)) {
    return <BalanceDisplay token={symbol} state="down" val={tokenAmount[symbol]} />
  }
  return (
    <>
      <IconButton
        disabled={disabeldAction}
        onClick={() => {
          if (disabeldAction) return
          setActionInfo({ token: symbol, tokenType: TokenType.SUPPLY })
        }}
        sx={ActionPanelStyle.addAdnRemoveBtn}
      >
        <AddIcon sx={{ fontSize: 16 }} />
      </IconButton>
      {/* <IconButton
                      disabled={false}
                      sx={ActionPanelStyle.addAdnRemoveBtn}
                    >
                      <RemoveIcon sx={{ fontSize: 16 }} />
                    </IconButton> */}
    </>
  )
}
const CollateralList: React.FC = () => {
  const allowedAssets = useCurrentAllowedAssets()
  const { decimals } = useCurrentCpt()
  const { walletTokenBalance } = useHashPowerToken()
  // useEffect(() => {
  //   console.log(walletTokenBalance, avatarAssetList, 'avatarAssetListavatarAssetListavatarAssetListavatarAssetList')
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [])
  const { pendingActionList, tokenAmount } = useShortPendingAction()

  const computedPrice = useComputedPrice()

  const pendingTokenTypes = useMemo(() => {
    const _map = new Map<string, TokenType[]>()
    pendingActionList.forEach((item) => {
      const _tokenType = _map.get(item.token) || []
      _tokenType.push(item.tokenType)
      _map.set(item.token, _tokenType)
    })
    return _map
  }, [pendingActionList])

  return (
    <TableContainer
      component={Paper}
      sx={{
        backgroundColor: 'transparent',
        border: 'none',
        boxShadow: 'none',
      }}
    >
      <Table sx={{}} aria-label="simple table">
        <TableHead>
          <TableRow sx={{ fontSize: 12, fontWeight: 500, color: 'rgba(0, 0, 0, 0.6)' }}>
            <TableCell sx={{ pl: 0, pr: 0 }}>Collateral Asset</TableCell>
            <TableCell align="right" sx={{ pl: 0, pr: 0 }}>
              Collateral Balance
            </TableCell>
            <TableCell align="right" sx={{ pl: 0, pr: 0 }}>
              {null}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {allowedAssets.map((row) => {
            return (
              <TableRow
                key={row.symbol}
                sx={{ '&:last-child td, &:last-child th': { border: 0, transition: 'all 0.3s' } }}
              >
                <TableCell sx={{ display: 'flex', alignItems: 'center', pl: 0, pr: 0, height: 66 }}>
                  {row.icon()}
                  <div
                    style={{
                      marginLeft: '10px',
                    }}
                  >
                    <Typography sx={{ fontWeight: 600 }}>{row.symbol}</Typography>
                    <Typography sx={{ fontSize: 12, color: 'rgba(0, 0, 0, 0.5)' }}>
                      {row.symbol} {walletTokenBalance[row.symbol]} in wallet
                    </Typography>
                  </div>
                </TableCell>
                <TableCell align="right" sx={{ p: 0, height: 66, transition: 'all 0.3s' }}>
                  <Typography sx={{ fontSize: 14 }}>{row.balance}</Typography>
                  {walletTokenBalance[row.symbol] !== 0 && (
                    <Typography sx={{ fontSize: 12, color: 'rgba(53, 5, 5, 0.5)' }}>
                      {row.symbol === tokens.WBTC.symbol
                        ? walletTokenBalance[row.symbol]
                        : computedPrice(
                            {
                              outputToken: {
                                token: tokens.WBTC.symbol,
                                decimals: tokens.WBTC.decimals,
                              },
                              inputToken: {
                                token: row.symbol,
                                decimals: row.decimals,
                              },
                            },
                            walletTokenBalance[row.symbol],
                          ).toFixed(decimals)}{' '}
                      {tokens.WBTC.symbol}
                    </Typography>
                  )}
                </TableCell>
                <TableCell sx={{ pl: 0, pr: 0, height: 66 }}>
                  <Box
                    sx={{
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'flex-end',
                    }}
                  >
                    <ActionPanel pendingTokenTypes={pendingTokenTypes} symbol={row.symbol} tokenAmount={tokenAmount} />
                  </Box>
                </TableCell>
              </TableRow>
            )
          })}
        </TableBody>
      </Table>
    </TableContainer>
  )
}
export default CollateralList
