import contracts from '../config/constants/contracts'
import { kovanAddresses } from '../config/constants/types'

const { positionManagerFBTC20 } = contracts

export function useManagerList() {
  const [managerList, setManagerList] = useState([])
  const { active, account, library, chainId } = useWeb3React()
  const generateData = useCallback(() => {
    const list = []
    if (active && account) {
      // find manager contracts
      Object.keys(contracts).forEach((it) => {
        if (it.includes('Manager')) {
          const signer = getSigner(library, account)
          const data = {
            ...contracts[it],
            signer,
            contract: getContract(signer, contracts[it].abi, contracts[it].address[chainId]),
          }
          list.push(data)
        }
      })
    }
    setManagerList(list)
  }, [account, active, chainId, library])
  useEffect(() => {
    generateData()
    return () => {
      setManagerList([])
    }
  }, [generateData, account])
  return managerList
}

