export function useGetPosition(active, account, library) {
  // eslint-disable-next-line consistent-return
  const callback = useCallback(
    // eslint-disable-next-line consistent-return
    async (abi, managerAddress) => {
      if (active && library) {
        const signer = getSigner(library, account)
        const contract = getContract(signer, abi, managerAddress)
        if (contract) {
          return contract.positions(account)
        }
      }
    },
    [active, library, account],
  )

  return { getPosition: callback }
}

export function useCreatePosition(abi, managerAddress) {
  const { account, library, active } = useWeb3React()
  const [tx, setTx] = useState<any>()
  // eslint-disable-next-line consistent-return
  const callback = useCallback(async () => {
    if (active) {
      const signer = getSigner(library, account)
      const contract = getContract(signer, abi, managerAddress)
      if (contract) {
        setTx('waiting')
        return contract.createPosition()
      }
    }
  }, [active, library, account, abi, managerAddress])

  return { createPosition: callback, tx }
}

export function useGetTokenBalance(library, active, chainId) {
  // eslint-disable-next-line consistent-return
  return useCallback(
    // eslint-disable-next-line consistent-return
    async (address) => {
      if (active && address && isSupportedChainId(chainId)) {
        const signer = getSigner(library, address)
        // const balance = {};
        const balance = {}
        // eslint-disable-next-line no-restricted-syntax,guard-for-in
        for (const token in tokens) {
          balance[token] = 0
          // eslint-disable-next-line no-continue
          // if (token === 'F(BTC,20)') continue;
          const contract = getContract(signer, ERC20, tokens[token].address[chainId])
          if (contract) {
            try {
              // eslint-disable-next-line no-await-in-loop
              const _balance = await contract.balanceOf(address)
              // eslint-disable-next-line no-prototype-builtins
              if (_balance) {
                // @ts-ignore
                balance[token] = _balance / BigNumber.from(10).pow(tokens[token].decimals)
              }
            } catch (e) {
              // eslint-disable-next-line no-console
              console.error(e)
              window.location.reload()
            }
          }
        }
        return balance
      }
    },
    [active, chainId, library],
  )
}

export function useGetGuaranteesInfo(managerData, position, active) {
  const [positionInfo, setGuaranteeInfo] = useState()

  useEffect(() => {
    // eslint-disable-next-line @typescript-eslint/no-extra-semi
    ;(async () => {
      if (active && managerData) {
        try {
          const info = await managerData.contract.positionInfo(position)
          if (info) {
            setGuaranteeInfo(info)
          }
        } catch (e) {
          //
        }
      }
    })()
    return () => {
      setGuaranteeInfo(null)
    }
  }, [active, managerData, position])

  return positionInfo
}

/**
 * @desc Add asset from my wallet to guarantee pool
 * */
export function useAddGuarantee(abi, managerAddress) {
  const { library, active, account } = useWeb3React()
  // eslint-disable-next-line consistent-return
  const callback = useCallback(
    // eslint-disable-next-line consistent-return
    async (position, amount) => {
      if (active && abi) {
        const signer = getSigner(library, account)
        const contract = getContract(signer, abi, managerAddress)
        if (contract) {
          return contract.addGuarantee(position, amount)
        }
      }
    },
    [active, library, account, abi, managerAddress],
  )
  return { addGuarantee: callback }
}

/**
 * @desc Withdraw asset from guarantee to my wallet
 * */
export function useRemoveGuarantee(abi, managerAddress, returnAddress?) {
  const { library, active, account } = useWeb3React()
  // eslint-disable-next-line consistent-return
  const callback = useCallback(
    // eslint-disable-next-line consistent-return
    async (position, amount) => {
      if (active) {
        const signer = getSigner(library, account)
        const contract = getContract(signer, abi, managerAddress)
        if (contract) {
          return contract.moveGuarantee(position, returnAddress || account, amount)
        }
      }
    },
    [active, library, account, abi, managerAddress, returnAddress],
  )
  return { removeGuarantee: callback }
}

/**
 * @desc transfer
 * */
export function useTransfer() {
  const { library } = useWeb3React()
  // eslint-disable-next-line consistent-return
  return useCallback(
    // eslint-disable-next-line consistent-return
    async (owner, from, to, amount) => {
      if (library) {
        const signer = getSigner(library, owner)
        const contract = getContract(signer, ERC20, from)
        if (contract) {
          return contract.transfer(to, amount)
        }
      }
    },
    [library],
  )
}

/**
 * @dec Get the specified token balance
 * */
export function useGetSpecifiedTokenBalance(library: Web3Provider, active: boolean, chainId: number) {
  // eslint-disable-next-line consistent-return
  return useCallback(
    // eslint-disable-next-line consistent-return
    async (signerAddress, tokenSymbol) => {
      if (active && signerAddress) {
        const signer = getSigner(library, signerAddress)
        const contract = getContract(signer, ERC20, tokens[tokenSymbol].address[chainId])
        if (contract) {
          return contract.balanceOf(signerAddress)
        }
      }
    },
    [active, library, chainId],
  )
}

