/**
 *
 * */
export function usePositionFBTC20Mint(library, active, account) {
  // eslint-disable-next-line consistent-return
  return useCallback(
    // eslint-disable-next-line consistent-return
    (amount) => {
      if (active) {
        const signer = getSigner(library, account)
        const Contract = getContract(signer, positionManagerFBTC20.abi, kovanAddresses.FBTC20Manager)
        if (Contract) {
          return Contract.mint(amount)
        }
      }
    },
    [library, active, account],
  )
}

export function useGetPositionInfo(library, active, account) {
  // eslint-disable-next-line consistent-return
  return useCallback(
    // eslint-disable-next-line consistent-return
    (position, newBalance?) => {
      if (active) {
        const signer = getSigner(library, account)
        const Contract = getContract(signer, positionManagerFBTC20.abi, kovanAddresses.FBTC20Manager)
        if (Contract) {
          return Contract.getPositionValueInfo(position, newBalance || 0)
        }
      }
    },
    [library, account, active],
  )
}
