import React, { useCallback, useEffect, useState } from 'react'
import { Typography, Input } from '@mui/material/'
import Wallet from 'components/uikit/widgets/Menu/icons/Wallet'
import { BigNumber } from '@ethersproject/bignumber'
import {
  Wrapper,
  Title,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Approve,
  FlexWrap,
  AlertButton,
} from 'components/util'
import usePanel from 'hooks/usePanel'
// eslint-disable-next-line import/named
import tokens from 'config/constants/tokens'
import { styled } from '@mui/material/styles'
import { AvatarIcon, ExchangeArrow, GuaranteeIcon } from 'components/uikit'
import ERC20ABI from 'config/abi/erc20.json'
import { useWeb3React } from 'web3-react-core'
import Select from 'components/Select'
import { useAppSelector } from 'state'
import { utils } from 'ethers'
import DataWrapper from 'components/DataWrapper'
import { getContract, isSupportedChainId } from 'utils/web3React'
import useSystemParams from '../../../hooks/useSystemParams'
import usePosContract from '../../../hooks/usePosContract'
import contracts from '../../../config/constants/contracts'
import { formatInfinity, formatSnNumber, formatWBTC, toFixed } from '../../../utils/formatTheValue'
import { useTransfer } from '../../../hooks/useManager'
import useToast from '../../../hooks/useToast'
import useHashPowerToken from '../../../hooks/useHashPowerToken'
import { toSmallUnits } from '../../../utils/transformHelper'
import { ChainTxUrl } from '../../../config/ChainInfo'

const MethodsWrap = styled('div')`
  .balance_info {
    display: flex;
    align-content: center;
    padding: 0 12px;
    margin-bottom: 6px;
    .label {
      color: #000;
      font-size: 14px;
      font-family: PingFangSC-Medium, sans-serif;
    }
    span {
      font-family: PingFangSC-Medium, sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: rgba(0, 0, 0, 0.5);
      margin-left: 7px;
    }
  }
  .content {
    display: flex;
    height: 50px;
    background: rgba(255, 255, 255, 0.4);
    border: 1px solid rgba(207, 216, 226, 0.5);
    box-sizing: border-box;
    border-radius: 11px;
    padding: 0 12px;
    .select_custom {
      margin: 0;
      padding: 0;
      border: none;
      background: unset;
      width: 200px;
      flex-shrink: 0;
    }
    .select_custom:nth-of-type(2) {
      width: 130px;
    }
    .MuiInput-root,
    .MuiInput-input {
      text-align: right;
      width: 100%;
      border: none;
      &:before,
      &:after {
        display: none;
      }
    }
    .MuiInput-input {
      padding: 0 8px;
      border-left: 1px solid rgba(207, 216, 226, 0.5) !important;

      &::-webkit-inner-spin-button {
        display: none;
      }
    }
  }
  .func {
    font-size: 12px;
    line-height: 17px;
    color: #6ed6f3;
    text-align: right;
    padding: 0 20px;
    margin-top: 3px;
    cursor: pointer;
    user-select: none;
  }
`

enum AccountType {
  'MyWallet' = 'My Wallet',
  'Standard' = 'Collateral',
  'Settlement' = 'Prepaid Interest',
}

const accountCategoryList = [
  {
    type: AccountType.MyWallet,
    token: 'My Wallet',
    id: 'd04155c3-eb41-4c44-b28d-67bb2d515904',
    icon: Wallet,
  },
  {
    type: AccountType.Settlement,
    token: 'Prepaid Interest',
    icon: GuaranteeIcon,
    id: 'edebd20e-18a1-4bc8-a869-9b44537d810c',
  },
  {
    type: AccountType.Standard,
    token: 'Collateral',
    icon: AvatarIcon,
    id: '763c1484-3fd3-4044-b8b5-3c261947f414',
  },
]

const { fbtc20mint } = contracts

export default function GeneralTransfer(props) {
  const { approveStatus, onApprove } = props
  const [gDisStatus, setGDisStatus] = useState(true)
  const [approveBtnStatus, setApproveBtnStatus] = useState(false)

  const currentCPT = useAppSelector((s) => s.collateralManage.currentCPT)
  const { allowedAssets, token, position, avatarAssetList, positionInfoValues, WBTCRouter, tokensValues } = currentCPT
  const { signer } = useSystemParams()
  const { walletTokenBalance, setRefresh } = useHashPowerToken()
  const [tokenList, setTokenList] = useState([])
  const [actionData, setActionData] = useState([
    {
      accountType: AccountType.MyWallet,
      tokenType: token,
      transferVal: 0,
      id: '1fee1a14-b8b3-4b73-b2be-1817a6d228ec',
      tokenSelectDisabled: false,
      list: [...accountCategoryList],
    },
    {
      accountType: AccountType.Standard,
      tokenType: token,
      transferVal: 0,
      id: '25897dcf-e843-4630-a5e1-309dc10cb1e5',
      tokenSelectDisabled: true,
      list: [...accountCategoryList],
    },
  ])
  const [params, setParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%',
  })

  const { toastInfo, toastSuccess, toastError } = useToast()
  const transfer = useTransfer()
  const { expanded, handleChange } = usePanel()
  const { active, chainId, library, account } = useWeb3React()
  const posContract = usePosContract(active, signer)

  const computed = useCallback(
    (value, type) => {
      /**
       * 0 null
       * 1 G->A --->>>  GDays   -interestGuaranteed -t
       * 2 G->W --->>>  GDays  CRate   -interestGuaranteed & -totalAssetsValues
       * 3 A->W --->>>  CRate   -totalAssetsValues
       * 4 A->G --->>>  GDays   +interestGuaranteed
       * 5 W->A --->>>  CRate   +totalAssetsValues
       * 6 W->G --->>>  CRate  GDays  +t  +interestGuaranteed
       *
       * */
      let { totalAssetsValues } = positionInfoValues
      let { interestGuaranteed } = positionInfoValues
      if (positionInfoValues) {
        if (type === 1) {
          interestGuaranteed = positionInfoValues.interestGuaranteed * 1 - value
        }
        if (type === 2) {
          totalAssetsValues = positionInfoValues.totalAssetsValues * 1 - value
          interestGuaranteed = positionInfoValues.interestGuaranteed * 1 - value
        }
        if (type === 3) {
          totalAssetsValues = positionInfoValues.totalAssetsValues * 1 - value
        }
        if (type === 4) {
          interestGuaranteed = positionInfoValues.interestGuaranteed * 1 + value
        }
        if (type === 5) {
          totalAssetsValues = positionInfoValues.totalAssetsValues * 1 + value * 1
        }
        if (type === 6) {
          totalAssetsValues = positionInfoValues.totalAssetsValues * 1 + value
          interestGuaranteed = positionInfoValues.interestGuaranteed * 1 + value
        }
        let cRate = 0
        if (totalAssetsValues === 0) {
          cRate = 0
        } else if (formatWBTC(positionInfoValues.shortBalanceValue) === '0') {
          cRate = Infinity
        } else {
          // @ts-ignore
          cRate = (totalAssetsValues / positionInfoValues.shortBalanceValue) * 100
        }

        // @ts-ignore
        const gDays = Math.floor(
          (interestGuaranteed - positionInfoValues.totalObligation) / positionInfoValues.estimatedDailyDebt,
        )
        setParams((state) => ({
          ...state,
          futureCRate: formatInfinity(cRate).toString().includes('')
            ? formatInfinity(cRate)
            : `${formatInfinity(cRate)}%`,
          // eslint-disable-next-line no-restricted-globals
          futureGDays: isNaN(gDays) ? 0 : formatInfinity(gDays),
        }))
      }
    },
    [positionInfoValues],
  )

  const onSubmit = useCallback(async () => {
    if (posContract) {
      const inter = new utils.Interface(fbtc20mint.abi)
      let data = ''
      let isTransfer = false
      if (actionData[0].accountType === AccountType.MyWallet) {
        if (actionData[1].accountType.includes('Settlement')) {
          // @ts-ignore
          data = inter.encodeFunctionData('addGuarantee', [
            Math.floor(
              ((BigNumber.from(10) as any).pow(tokens[actionData[0].tokenType].decimals) as any).toString() *
                actionData[0].transferVal,
            ) || '0',
            false,
          ])
          isTransfer = false
        } else if (actionData[1].accountType.includes('Standard')) {
          // @ts-ignore
          await transfer(
            account,
            tokens[actionData[0].tokenType].address[chainId],
            position,
            formatSnNumber(
              (
                ((BigNumber.from(10) as any).pow(tokens[actionData[0].tokenType].decimals) as any) *
                actionData[0].transferVal
              ).toFixed(0),
            ),
          )
          isTransfer = true
        }
      }
      if (actionData[0].accountType === AccountType.Standard) {
        isTransfer = false
        if (actionData[1].accountType === AccountType.MyWallet) {
          // @ts-ignore
          data = inter.encodeFunctionData('withdrawAsset', [
            tokens[actionData[0].tokenType].address[chainId],
            account,
            formatSnNumber(
              (
                (((BigNumber.from(10) as any).pow(tokens[actionData[0].tokenType].decimals) as any).toString() *
                  actionData[0].transferVal) as any
              ).toFixed() * 1,
            ),
          ])
        }
        if (actionData[1].accountType === AccountType.Settlement) {
          // @ts-ignore
          data = inter.encodeFunctionData('addGuarantee', [
            toSmallUnits(
              actionData[0].transferVal.toFixed(tokens[actionData[0].tokenType].decimals),
              tokens[actionData[0].tokenType].decimals,
            ),
            true,
          ])
        }
      }
      if (actionData[0].accountType === AccountType.Settlement) {
        isTransfer = false
        if (actionData[1].accountType === 'My Wallet') {
          // @ts-ignore
          data = inter.encodeFunctionData('moveGuarantee', [
            account,
            ((BigNumber.from(10) as any).pow(tokens.WBTC.decimals) * actionData[0].transferVal).toFixed(0),
          ])
        }
        if (actionData[1].accountType.includes('Standard')) {
          // @ts-ignore
          data = inter.encodeFunctionData('moveGuarantee', [
            position,
            ((BigNumber.from(10) as any).pow(tokens.WBTC.decimals) * actionData[0].transferVal).toFixed(0),
          ])
        }
      }

      // @ts-ignore
      // const data = inter.encodeFunctionData('addGuarantee', [(BigNumber.from(10) as any).pow(tokens.WBTC.decimals).toString() || '0']);
      if (!isTransfer && data) {
        // @ts-ignore
        try {
          setGDisStatus(true)
          const txInfo = await posContract.delegateCall(fbtc20mint.address[chainId], data)
          if (txInfo) {
            toastInfo(
              'Submitted ',
              <>
                <p style={{ marginBottom: 24 }}>Waiting for confirmation</p>
                <FlexWrap style={{ flexDirection: 'row-reverse' }}>
                  <AlertButton
                    onClick={() => {
                      window.open(`${ChainTxUrl}/${txInfo.hash}`)
                    }}
                  >
                    View on Explorer
                  </AlertButton>
                </FlexWrap>
              </>,
            )
            const result = await txInfo.wait()
            if (result) {
              toastSuccess(
                'Success',
                <>
                  <FlexWrap style={{ flexDirection: 'row-reverse' }}>
                    <AlertButton
                      onClick={() => {
                        window.open(`${ChainTxUrl}/${txInfo.hash}`)
                      }}
                    >
                      View on Explorer
                    </AlertButton>
                  </FlexWrap>
                </>,
              )
              setRefresh((state) => !state)
              // setDisabled(false)

              setGDisStatus(false)
            }
          }
        } catch ({ message }: any) {
          toastError('Failed', `${message}`)
          setGDisStatus(false)
        }
      }
    }
  }, [posContract, actionData, transfer, account, chainId, position, toastInfo, toastSuccess, setRefresh, toastError])

  const onChangeVal = useCallback(
    ({ target }, record) => {
      const { value } = target
      setActionData((state) => {
        const data = state.map((item) => ({ ...item, transferVal: value }))
        if (tokensValues) {
          const wbtc = value * tokensValues[record.tokenType]

          if (data[0].accountType === AccountType.Settlement) {
            if (data[1].accountType === AccountType.Standard) {
              computed(wbtc, 1)
            }
            if (data[1].accountType === AccountType.MyWallet) {
              computed(wbtc, 2)
            }
          }
          if (data[0].accountType === AccountType.Standard) {
            if (data[1].accountType === AccountType.MyWallet) {
              computed(wbtc, 3)
            }
            if (data[1].accountType === AccountType.Settlement) {
              computed(wbtc, 4)
            }
          }
          if (data[0].accountType === AccountType.MyWallet) {
            if (data[1].accountType === AccountType.Standard) {
              computed(wbtc, 5)
            }
            if (data[1].accountType === AccountType.Settlement) {
              computed(wbtc, 6)
            }
          }
        }
        return data
      })
    },
    [tokensValues, computed],
  )

  const onChangeAccount = useCallback((val, record, i) => {
    setActionData((state) => {
      const data = state.map((item) => ({
        ...item,
        accountType: item.id === record.id ? val : item.accountType,
        tokenType: val.includes('Settlement') ? 'WBTC' : item.tokenType,
      }))
      if (i === 0) {
        data[1].list = accountCategoryList.filter((it) => it.type !== data[0].accountType)
        data[0].tokenSelectDisabled = data[0].accountType.includes('Settlement')
      }
      return data
    })
  }, [])

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const onChangeToken = useCallback((val, index) => {
    setActionData((state) => {
      const nextState = state.map((it) => ({ ...it, tokenType: val }))
      if (val !== 'WBTC') {
        nextState[1].list = nextState[1].list.filter((it) => it.type !== AccountType.Settlement)
      } else {
        const settlement = accountCategoryList.find((it) => it.type === AccountType.Settlement)
        const _index = nextState[1].list.findIndex((it) => it.type === AccountType.Settlement)
        if (!(_index > -1)) {
          nextState[1].list.push(settlement)
        }
        // nextState[1].list =
      }
      return nextState
    })
  }, [])

  const onMax = useCallback(
    (record) => {
      setActionData((state) => {
        const data = state
        if (tokensValues && positionInfoValues) {
          const maxMoveAmount = formatWBTC(
            positionInfoValues.totalAssetsValues * 1 - positionInfoValues.shortBalanceValue * 1.5,
          )

          let value = 0
          const { collateralValue, interestGuaranteed } = positionInfoValues
          if (data[0].accountType === AccountType.Settlement && positionInfoValues) {
            if (maxMoveAmount > formatWBTC(collateralValue)) {
              value = maxMoveAmount - formatWBTC(collateralValue)
            }
            if (maxMoveAmount > formatWBTC(interestGuaranteed)) {
              value = formatWBTC(interestGuaranteed)
            }
            if (maxMoveAmount < formatWBTC(interestGuaranteed)) {
              value = toFixed(maxMoveAmount)
            }
            if (data[1].accountType === AccountType.Standard) {
              computed(value, 1)
            }
            if (data[1].accountType === AccountType.MyWallet) {
              computed(value, 2)
            }
          }
          if (data[0].accountType === AccountType.Standard && avatarAssetList) {
            const wbtc = formatWBTC(avatarAssetList[record.tokenType] * tokensValues[record.tokenType])
            if (maxMoveAmount > wbtc) {
              value = wbtc * WBTCRouter[record.tokenType]
            }
            if (maxMoveAmount < wbtc) {
              value = maxMoveAmount * WBTCRouter[record.tokenType]
            }
            if (data[1].accountType === AccountType.MyWallet) {
              computed(wbtc * WBTCRouter[record.tokenType], 3)
            }
            if (data[1].accountType === AccountType.Settlement) {
              computed(wbtc, 4)
            }
          }
          if (data[0].accountType === AccountType.MyWallet) {
            value = walletTokenBalance[record.tokenType]
            if (data[1].accountType === AccountType.Standard) {
              computed(value, 5)
            }
            if (data[1].accountType === AccountType.Settlement) {
              computed(value, 6)
            }
          }

          data[0].transferVal = value
          data[1].transferVal = value
        }
        return data
      })
    },
    [WBTCRouter, avatarAssetList, computed, positionInfoValues, tokensValues, walletTokenBalance],
  )

  useEffect(() => {
    if (active && isSupportedChainId(chainId)) {
      // eslint-disable-next-line @typescript-eslint/no-extra-semi
      ;(async () => {
        let _tokenList = []
        if (allowedAssets) {
          // eslint-disable-next-line no-restricted-syntax
          for (const address of allowedAssets) {
            const erc20 = getContract(null, ERC20ABI, address.toLocaleLowerCase(), library)
            if (erc20) {
              _tokenList.push(Promise.all([erc20.symbol(), erc20.decimals(), address]))
            }
          }

          _tokenList = (await Promise.all(_tokenList)).map((item) => {
            return {
              token: item[0],
              decimals: item[1],
              address: item[2].toLocaleLowerCase(),
              icon: tokens[item[0]]?.icon,
            }
          })
          if (_tokenList.length > 0) {
            setTokenList(_tokenList)
          }
          setActionData((state) => {
            return [
              state[0],
              {
                ...state[1],
                list: accountCategoryList.filter((it) => it.type !== state[0].accountType),
              },
            ]
          })
        }
      })()
    }
  }, [active, allowedAssets, chainId, library])

  useEffect(() => {
    if (active && Number(position) && positionInfoValues) {
      setParams({
        nowGDays: Number(positionInfoValues.gDays) === Infinity ? '+' : positionInfoValues.gDays,
        nowCRate: Number(positionInfoValues.cRate) === Infinity ? '+' : `${positionInfoValues.cRate}%`,
      })
      computed(0, 0)
    }
  }, [active, computed, position, positionInfoValues])

  useEffect(() => {
    if (actionData[0].transferVal <= 0) {
      setGDisStatus(true)
    } else {
      setGDisStatus(false)
    }
  }, [actionData])

  return (
    <Wrapper>
      <Title>General Transfer</Title>
      <Accordion id="panel15" expanded={expanded === 'panel15'} onChange={handleChange('panel15')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Freestyle Transfer</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {actionData.map((item, index) => (
            <div key={item.id}>
              <MethodsWrap>
                <div className="balance_info">
                  <span className="label">Balance: </span>
                  {item.accountType === AccountType.Standard && (
                    <span>
                      {avatarAssetList[item.tokenType] || 0} {item.tokenType}
                    </span>
                  )}
                  {item.accountType === AccountType.MyWallet && (
                    <span>
                      {walletTokenBalance[item.tokenType] || 0} {item.tokenType}
                    </span>
                  )}
                  {item.accountType === AccountType.Settlement && (
                    <span>
                      {formatWBTC(positionInfoValues.interestGuaranteed)} {item.tokenType}
                    </span>
                  )}
                </div>
                <div className="content">
                  <Select
                    options={item.list}
                    onChange={(val) => {
                      onChangeAccount(val, item, index)
                    }}
                    selectValue={item.accountType}
                  />
                  <Select
                    options={tokenList}
                    onChange={(val) => {
                      onChangeToken(val, index)
                    }}
                    selectValue={item.tokenType}
                    selectDisabled={index === 1 ? true : item.tokenSelectDisabled}
                  />
                  <Input
                    value={item.transferVal}
                    type="number"
                    onInput={(e) => {
                      onChangeVal(e, item)
                    }}
                    disabled={index === 1}
                  />
                </div>

                <div hidden={index === 1} className="func">
                  {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events */}
                  <span
                    onClick={() => {
                      onMax(item)
                    }}
                  >
                    Max
                  </span>
                </div>
              </MethodsWrap>

              {index === 0 ? (
                <div style={{ display: 'flex', alignItems: 'center', height: 30, justifyContent: 'center' }}>
                  <ExchangeArrow
                    onClick={() => {
                      setActionData((state) => {
                        const list1 = state[0]
                        const list2 = state[1]
                        const data = state.reverse()
                        return data.map((it, i) => ({
                          ...it,
                          list:
                            i === 0 ? list1.list : accountCategoryList.filter((ele) => ele.type !== list2.accountType),
                          tokenSelectDisabled: i === 0 ? list1.tokenSelectDisabled : list2.tokenSelectDisabled,
                        }))
                      })
                    }}
                    style={{ fill: 'none', transform: 'rotate(90deg)', cursor: 'pointer', width: 22 }}
                  />
                </div>
              ) : null}
            </div>
          ))}
          <DataWrapper
            data={[
              ['Collateral Ratio', params.nowCRate.includes('NaN') ? '0%' : params.nowCRate, params.futureCRate],
              ['Prepaid Interest Coverage ', `${params.nowGDays || 0} days`, `${params.futureGDays} days`],
            ]}
          />
          {approveStatus ? (
            <Approve style={{ boxShadow: 'none' }} disabled={gDisStatus} onClick={onSubmit}>
              Submit
            </Approve>
          ) : (
            <Approve
              style={{ boxShadow: 'none' }}
              disabled={approveBtnStatus}
              onClick={() => {
                onApprove((state) => {
                  setApproveBtnStatus(state)
                })
              }}
            >
              Approve
            </Approve>
          )}
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  )
}
