import { Box, Typography, Button, TextField, InputAdornment } from '@mui/material'
import React, { useState } from 'react'
import Menu from '@mui/material/Menu'
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp'

const SetMenu: React.FC<{
  handleClose: () => void
  anchorEl: null | HTMLElement
  open: boolean
  deadline: any
  maxSlippageValue: any

  setMaxSlippageValue: (value: any) => void
  setDeadlineValue: (value: string) => void
}> = ({ handleClose, anchorEl, open, deadline, maxSlippageValue, setMaxSlippageValue, setDeadlineValue }) => {
  const [focusFlag, setFocusFlag] = useState(false)
  return (
    <Menu
      id="basic-menu"
      anchorEl={anchorEl}
      open={open}
      onClose={handleClose}
      MenuListProps={{
        'aria-labelledby': 'basic-button',
      }}
      transformOrigin={{ horizontal: 'right', vertical: 'top' }}
      anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
      PaperProps={{
        sx: {
          width: '288px',
          boxShadow: 'none',
          mt: 1.5,
          borderRadius: 2,
          border: '1px solid rgba(207, 216, 226, 0.5)',
        },
      }}
    >
      <Box
        sx={{
          pl: 1.2,
          pr: 1.2,
          mb: 1,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <Typography sx={{ fontSize: 14, fontWeight: 400, color: 'rgba(0, 0, 0, 0.5)' }}>Max.slippage</Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <Typography sx={{ fontSize: 14, fontWeight: 500 }}>Auto</Typography>
          <KeyboardArrowUpIcon sx={{ width: 16, height: 16 }} />
        </Box>
      </Box>
      <Box
        sx={{
          display: 'flex',
          gap: 1,
          pl: 1.2,
          pr: 1.2,
          height: 50,
        }}
      >
        {/* <Box
          sx={{
            flex: 1,
            display: 'flex',
            alignItems: 'center',
            borderRadius: 2,
           
            border: '1px solid rgba(207, 216, 226, 0.5)',
          }}
        >
          <Button
            size="small"
            sx={{
              height: 42,
              p: 0,
              borderRadius: 2,
              textTransform: 'none',
              bgcolor: 'rgba(110, 214, 243, 1)',
              color: '#000',
            }}
          >
            Auto
          </Button>
          <Button
            size="small"
            sx={{
              height: 42,
              p: 0,
              borderRadius: 2,
              textTransform: 'none',
              bgcolor: 'rgba(110, 214, 243, 0)',
              color: '#000',
            }}
          >
            Custom
          </Button>
        </Box> */}

        <Box
          onClick={() => setMaxSlippageValue('0.1')}
          sx={{
            flex: 1,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: 2,

            bgcolor: maxSlippageValue === '0.1' ? 'rgba(110, 214, 243, 1)' : '',
            border: '1px solid rgba(207, 216, 226, 0.5)',
            cursor: 'pointer',
          }}
        >
          0.1%
        </Box>
        <Box
          onClick={() => setMaxSlippageValue('0.5')}
          sx={{
            flex: 1,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            bgcolor: maxSlippageValue === '0.5' ? 'rgba(110, 214, 243, 1)' : '',
            cursor: 'pointer',
            borderRadius: 2,

            border: '1px solid rgba(207, 216, 226, 0.5)',
          }}
        >
          0.5%
        </Box>
        <Box
          onClick={() => setMaxSlippageValue('1')}
          sx={{
            flex: 1,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: 2,

            bgcolor: maxSlippageValue === '1' ? 'rgba(110, 214, 243, 1)' : '',
            cursor: 'pointer',
            border: '1px solid rgba(207, 216, 226, 0.5)',
          }}
        >
          1%
        </Box>
        <Box
          sx={{
            flex: 1.2,
            display: 'flex',
            alignItems: 'center',
            borderRadius: 2,
            border:
              focusFlag ||
              (maxSlippageValue > 0 &&
                maxSlippageValue !== '0.5' &&
                maxSlippageValue !== '0.1' &&
                maxSlippageValue !== '1')
                ? '1px solid rgba(110, 214, 243, 1)'
                : '1px solid rgba(207, 216, 226, 0.5)',
            p: 1,
          }}
        >
          <TextField
            placeholder="1"
            multiline
            maxRows={5}
            size="small"
            onBlur={() => setFocusFlag(false)}
            onFocus={() => setFocusFlag(true)}
            value={maxSlippageValue}
            onChange={(e) => {
              const inputValue = e.target.value
              let formattedValue = ''
              const cleanedValue = inputValue.replace(/[^\d.]/g, '')
              if (cleanedValue.includes('.')) {
                const parts = cleanedValue.split('.')
                parts[1] = parts[1]?.slice(0, 2)
                formattedValue = parts.join('.')
              } else {
                formattedValue = cleanedValue
              }

              if (Number(e.target.value) < 0 || e.target.value === '') {
                setMaxSlippageValue('0')
                return
              }
              if (Number(e.target.value) > 49.9) {
                setMaxSlippageValue('49.9')
                return
              }
              setMaxSlippageValue(
                formattedValue.includes('.') ? formattedValue.toString() : parseFloat(formattedValue).toString(),
              )
            }}
            sx={{
              '& .MuiInputBase-input': {
                textAlign: 'left',
              },
              '& .MuiInputBase-input::placeholder': {
                textAlign: 'left',
              },
            }}
            variant="standard"
            fullWidth
            InputProps={{
              disableUnderline: true,
              sx: {
                padding: 0,
              },
              endAdornment: (
                <InputAdornment position="end">
                  <Typography sx={{ textAlign: 'left', fontSize: 14, fontWeight: 400, color: 'rgba(0,0,0,1)' }}>
                    %
                  </Typography>
                </InputAdornment>
              ),
            }}
          />
        </Box>
      </Box>
      <Box
        sx={{
          pl: 1.2,
          pr: 1.2,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          mt: 2,
          mb: 1,
        }}
      >
        <Typography sx={{ fontSize: 14, fontWeight: 400, color: 'rgba(0, 0, 0, 0.5)' }}>
          Transaction deadline
        </Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <Typography sx={{ fontSize: 14, fontWeight: 500 }}>10m</Typography>
          <KeyboardArrowUpIcon sx={{ width: 16, height: 16 }} />
        </Box>
      </Box>
      <Box
        sx={{
          height: 50,
          flex: 1,
          display: 'flex',
          alignItems: 'center',
          borderRadius: 2,
          border: '1px solid rgba(207, 216, 226, 0.5)',
          p: 1,
          ml: 1.2,
          mr: 1.2,
        }}
      >
        <TextField
          placeholder="10"
          multiline
          maxRows={5}
          size="small"
          value={deadline}
          onChange={(e) => {
            setDeadlineValue(e.target.value)
          }}
          sx={{
            '& .MuiInputBase-input': {
              textAlign: 'right',
            },
            '& .MuiInputBase-input::placeholder': {
              textAlign: 'right',
            },
          }}
          variant="standard"
          fullWidth
          InputProps={{
            disableUnderline: true,
            sx: {
              padding: 0,
            },
            endAdornment: (
              <InputAdornment position="end">
                <Typography sx={{ fontSize: 14, fontWeight: 400, color: 'rgba(0, 0, 0, 1)' }}>minutes</Typography>
              </InputAdornment>
            ),
          }}
        />
      </Box>
    </Menu>
  )
}

export default SetMenu
