const Title = styled('h1')`
  font-family: "Heebo Medium";
  font-style: normal;
  font-weight: 500; 
  font-size: 32px;
  line-height: 48px;
  color: #000;
`

function MainTitle(props) {
  const {children} = props;
  return (
      <Title {...props}>
        {children}
      </Title>
  );
}

export default MainTitle;
