export default function oracleHub(data) {
  const { inputBalance, outputBalance, inputAmount, inputDecimals, outputDecimals } = data
  const x = inputBalance * 10 ** inputDecimals
  const y = outputBalance * 10 ** outputDecimals

  const k = x * y
  const I = inputAmount * 0.997 * 10 ** inputDecimals
  const O = y - k / (x + I)
  const nextX = (x * 1 + I * 1) / 10 ** inputDecimals
  const nextY = (y * 1 - O * 1) / 10 ** outputDecimals

  return { output: O / 10 ** outputDecimals, nextX, nextY }
}
