import {useWeb3React} from "web3-react-core";
import {useEffect, useState} from "react";
import BlockUpdater from "config/abi/BlockUpdater.json"
import moment from "moment";
import {BigNumber} from "@ethersproject/bignumber";
import {getContract, getSigner, isSupportedChainId} from "../utils/web3React";
import tokens from "../config/constants/tokens";
import {toLargeUnits} from "../utils/transformHelper";


export default function useEpoch(){
  const {active, chainId, library, account} = useWeb3React();
  const [currentEpoch,setCurrentEpoch] = useState(0);
  const [epochList, setEpochList] = useState([])

  useEffect(()=>{
    (async ()=>{
      // @ts-ignore
      if (active && chainId && library && account && isSupportedChainId(chainId)) {
        const signer = getSigner(library, account);
        const list = [];
        const updater = getContract(signer, BlockUpdater, tokens['F(BTC,20)'].blockUpdater.address[chainId])
        if (updater) {
          try {
            // @ts-ignore
            // eslint-disable-next-line @typescript-eslint/no-shadow

            const _currentEpoch = await updater.getCurrentEpoch();
            if (_currentEpoch >= 0) {
              // @ts-ignore
              for (let i = 0; i <= (_currentEpoch /1); i++) {
                list.push(updater.epochs(i))
              }
              const __epochList = await Promise.all(list)
              setCurrentEpoch(_currentEpoch / 1)
              setEpochList(__epochList.map((it,index)=> {
                const date = it[0].mul(1000).toNumber()
                // n * 10**6 * 60*60*24  / 10**18 / 10**8
                const factor = it[1].toNumber() > 0 ? `${Number(toLargeUnits(it[1].mul(BigNumber.from(10).pow(6)).mul(60*60*24).div(BigNumber.from(10).pow(8)), 18)).toFixed(tokens.WBTC.decimals)}WBTC`: 0
                return  {
                  epoch: index,
                  start: date ?
                    moment(date).format('YYYY-MM-DD HH:ss'): date,
                  factor,
                  acc: it[2]
                }
              }))
            }

          } catch (e) {
            console.error('error', (e as Error).message )
          }
        }

      }
    })();
  },[account, active, chainId, library])
  return [currentEpoch, epochList];
}
