/* eslint-disable @typescript-eslint/no-extra-semi */
import React, { Dispatch, SetStateAction, useCallback, useEffect, useState } from 'react'
import tokens, { tokenType } from 'config/constants/tokens'
import PositionABI from 'config/abi/Position.json'
import { useWeb3React } from 'web3-react-core'
import { BigNumber } from '@ethersproject/bignumber'
import { getContract, isSupportedChainId } from '../utils/web3React'
import useSystemParams from '../hooks/useSystemParams'
import { OpenStatus, tokensSymbol } from '../config/constants/types'
import { useUpdataCurrentCPT } from '../state/hooks'
import { useGetTokenBalance } from '../hooks/useManager'
import { formatWBTC, toFixed } from '../utils/formatTheValue'
import { toLargeUnits } from '../utils/transformHelper'

const HashPowerTokenContext = React.createContext<{
  hashPowerTokenList: any[],
  refresh: boolean,
  setRefresh: Dispatch<SetStateAction<boolean>>,
  setHashPowerTokenList: Dispatch<SetStateAction<any[]>>,
  computed: (_mintAmount: any, cptInfo: any, callback: (ratio: any, gDays: any, amount: any)=> any) => void,
  walletTokenBalance: any,
  mintComputed: (_radio: number, cptInfo: any, callback: (currentMintAmount: any, gDays: any, amount: any)=> any)=> any
}>(null)

function HashPowerTokenProvider({ children }) {
  const { active, chainId, account, library } = useWeb3React()
  const [hashPowerTokenList, setHashPowerTokenList] = useState([])
  const [walletTokenBalance, setWalletBalance] = useState({})
  const { accountContract, signer, helperContract, matrixContract } = useSystemParams()
  const [refresh, setRefresh] = useState(false)
  const { changeCPT } = useUpdataCurrentCPT()
  const getTokenBalance = useGetTokenBalance(library, active, chainId)
  // eslint-disable-next-line consistent-return
  const getPositionValues = useCallback(
    // eslint-disable-next-line consistent-return
    async (position, cptBalance) => {
      if (active && Number(position)) {
        if (helperContract) {
          const positionValues = await helperContract.getPositionStatus(position, cptBalance)
          const data = Object.fromEntries(Object.entries(positionValues))
          const {
            interestGuaranteed,
            collateralValue,
            shortBalanceValue,
            estimatedDailyDebt,
            estimatedObligationToday,
          } = data

          // @ts-ignore
          const totalObligation = data.obligation.add(data.epochDebt).add(estimatedObligationToday)
          let cRate: null
          let totalAssetsValues: null
          if (totalObligation.toString() * 1) {
            // @ts-ignore
            totalAssetsValues = collateralValue.add(interestGuaranteed).sub(totalObligation)
            // @ts-ignore
            cRate = ((totalAssetsValues / shortBalanceValue) * 100).toFixed(2)
          } else {
            // @ts-ignore
            totalAssetsValues = collateralValue.add(interestGuaranteed)
            // @ts-ignore
            cRate = ((totalAssetsValues / shortBalanceValue) * 100).toFixed(2)
          }
          // @ts-ignore
          data.guaranteeValues = interestGuaranteed.sub(totalObligation)
          data.totalAssetsValues = totalAssetsValues
          data.totalObligation = totalObligation
          if (formatWBTC(totalAssetsValues) === '0') {
            data.cRate = '0'
          } else if (formatWBTC(shortBalanceValue) === '0') {
            data.cRate = 'Infinity'
          } else {
            data.cRate = cRate
          }
          // @ts-ignore
          if (interestGuaranteed.sub(totalObligation).toNumber() === 0) {
            // @ts-ignore
            data.gDays = '0'
            // @ts-ignore
          } else if (estimatedDailyDebt.isZero()) {
            data.gDays = 'Infinity'
          } else {
            // @ts-ignore
            data.gDays = interestGuaranteed.sub(totalObligation).div(estimatedDailyDebt).toString()
          }
          return data
        }
      } else {
        const zero = BigNumber.from(0)
        return {
          cRate: '0',
          collateralValue: zero,
          epochDebt: zero,
          estimatedDailyDebt: zero,
          estimatedObligationToday: zero,
          gDays: '0',
          guaranteeValues: zero,
          interestGuaranteed: zero,
          obligation: zero,
          shortBalanceValue: zero,
          totalAssetsValues: zero,
          totalObligation: zero,
        }
      }
    },
    [active, helperContract],
  )
  // eslint-disable-next-line consistent-return
  const queryValues = useCallback(
    // eslint-disable-next-line consistent-return
    async (position, token, mark) => {
      if (active && position) {
        let result
        if (mark === 'WBTC') {
          result = await matrixContract.queryValues(
            [
              [tokens[token].address[chainId], BigNumber.from(10).pow(tokens[token].decimals), true],
              [tokens.USDT.address[chainId], BigNumber.from(10).pow(tokens.USDT.decimals), true],
            ],
            tokens.WBTC.address[chainId],
            position,
          )
        }
        if (result) {
          return {
            [token]: result[0],
            [mark === 'WBTC' ? 'USDT' : 'WBTC']: result[1],
            WBTC: BigNumber.from(10).pow(tokens.WBTC.decimals), // 1wbtc ~= ?USDT
          }
        }
        return {
          [token]: null,
          [tokensSymbol.USDT]: null,
          [tokensSymbol.WBTC]: 0,
        }
      }
    },
    [active, chainId, matrixContract],
  )
  // eslint-disable-next-line consistent-return
  const queryAssets = useCallback(
    // eslint-disable-next-line consistent-return
    async (position, token) => {
      if (active && matrixContract && position) {
        // return Promise.all([
        //   matrixContract.queryAssetsEff(tokens[token].address[chainId], tokens.WBTC.address[chainId], position),
        //   matrixContract.queryAllowedAssets(tokens[token].address[chainId])
        // ])
        return matrixContract.queryAllowedAssets(tokens[token].address[chainId])
      }
    },
    [active, chainId, matrixContract],
  )

  // eslint-disable-next-line consistent-return
  const estimateDailyReward = useCallback(
    // eslint-disable-next-line consistent-return
    async (abi, address, balance) => {
      if (signer) {
        const updater = getContract(signer, abi, address)
        if (updater) {
          return updater.estimatedDailyReward(balance)
        }
      }
    },
    [signer],
  )
  // eslint-disable-next-line consistent-return
  const initData = useCallback(async () => {
    // eslint-disable-next-line no-console
    console.time('init Data time')
    const positions = []
    if (accountContract && signer) {
      // eslint-disable-next-line no-restricted-syntax
      for (const [token, tokenData] of Object.entries(tokens)) {
        if (tokenData.type === tokenType.CPT) {
          let current = {}
          // @ts-ignore
          const { blockUpdater } = tokenData
          // eslint-disable-next-line no-await-in-loop
          const position = await accountContract.positions(account, tokenData.address[chainId])
          const posContract = getContract(signer, PositionABI, position)
          if (posContract && Number(position)) {
            try {
              // eslint-disable-next-line no-await-in-loop
              const [_balance, tokensValues, _allowedAssets, avatarAssetList, rewards] = await Promise.all([
                posContract.balance(),
                queryValues(position, token, 'WBTC'),
                queryAssets(position, token),
                getTokenBalance(position),
                estimateDailyReward(
                  blockUpdater.abi,
                  blockUpdater.address[chainId],
                  BigNumber.from(10).mul(BigNumber.from(10).pow(tokens[tokensSymbol.FBTC20].decimals)),
                ),
              ])
              const allowedAssets = _allowedAssets && _allowedAssets.map((item) => item.toLocaleUpperCase())
              const WBTCRouter = {}
              // eslint-disable-next-line no-restricted-syntax
              for (const t in tokensValues) {
                if (Object.hasOwnProperty.call(tokensValues, token)) {
                  const wbtc = tokensValues[t] as unknown as BigNumber | number
                  const decimals = tokens.WBTC.decimals as number
                  WBTCRouter[t] = 1 / Number(toLargeUnits(wbtc, decimals))
                }
              }
              current = {
                ...current,
                ...tokenData,
                allowedAssets,
                avatarAssetList,
                position,
                status: OpenStatus.opened,
                rewards,
                token,
                avatar: position,
                WBTCRouter,
                ticker: token,
                balance: _balance,
                tokensValues,
                positionInfoValues: null,
              }
              // eslint-disable-next-line no-await-in-loop
              const positionInfoValues = await getPositionValues(position, _balance)
              current = {
                ...current,
                positionInfoValues,
              }
            } catch (e) {
              // eslint-disable-next-line no-console
              console.error(e)
            }
          } else {
            // eslint-disable-next-line no-await-in-loop
            const tokensValues = await queryValues(account, token, 'WBTC')
            const WBTCRouter = {}
            // eslint-disable-next-line no-restricted-syntax
            for (const t in tokensValues) {
              if (Object.hasOwnProperty.call(tokensValues, token)) {
                const wbtc = tokensValues[t] as unknown as BigNumber | number
                const decimals = tokens.WBTC.decimals as number
                WBTCRouter[t] = 1 / Number(toLargeUnits(wbtc, decimals))
              }
            }

            // eslint-disable-next-line @typescript-eslint/no-shadow
            current = {
              ...current,
              ...tokenData,
              token,
              avatar: position,
              position,
              ticker: token,
              rewards: 0,
              positionInfoValues: null,
              avatarAssetList: null,
              allowedAssets: null,
              balance: 0,
              tokensValues,
              WBTCRouter,
              status: OpenStatus.notOpen,
            }
          }

          positions.push({
            ...current,
            ...tokenData,
          })
        }
      }
      changeCPT(positions[0])
      // eslint-disable-next-line no-console
      console.timeEnd('init Data time')
      setHashPowerTokenList(positions)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, accountContract, chainId, getPositionValues, queryValues, signer, refresh])

  // knowing mint
  const computed = useCallback((_mintAmount, cptInfo, callback) => {
    const { positionInfoValues, token, tokensValues, balance, position, rewards } = cptInfo
    if (cptInfo && Number(position) && positionInfoValues && positionInfoValues.interestGuaranteed) {
      const allBalance = positionInfoValues.interestGuaranteed - positionInfoValues.totalObligation
      // @ts-ignore
      const mintAllAmount = Number(toLargeUnits(balance, cptInfo.decimals)) + Number(_mintAmount)
      // @ts-ignore
      const estimatedDailyDebtPerDebt = rewards / 10
      // @ts-ignore
      // eslint-disable-next-line consistent-return
      const gDays = (() => {
        if (allBalance === 0) {
          return 0
        }
        // @ts-ignore
        if (estimatedDailyDebtPerDebt * mintAllAmount === 0) {
          return Infinity
        }
        return toFixed(allBalance / (estimatedDailyDebtPerDebt * mintAllAmount), 0)
      })()
      // @ts-ignore
      const shortBalance = tokensValues[token] * mintAllAmount
      // @ts-ignore
      const ratio = ((positionInfoValues.totalAssetsValues / shortBalance) * 100).toFixed(2)

      // eslint-disable-next-line no-unused-expressions
      callback && callback(ratio, gDays, Number(mintAllAmount.toFixed(cptInfo.decimals)))
    }
  }, [])

  // knowing radio
  const mintComputed = useCallback((_radio: number, cptInfo, callback) => {
    const { positionInfoValues, token, tokensValues, balance, position, rewards } = cptInfo
    if (cptInfo && Number(position)) {
      const dividend = BigNumber.from(10).pow(cptInfo.decimals) as unknown as number
      const allBalance = positionInfoValues.interestGuaranteed - positionInfoValues.totalObligation
      //  
      const hasAmount = balance / dividend
      const mintAllAmount = positionInfoValues.totalAssetsValues / ((_radio / 100) * tokensValues[token])
      const currentMintAmount = Number((mintAllAmount - hasAmount).toFixed(6))
      /*
                1000000 / (10 x) = 3
                3 * 10 x = 1000000 ;  x = 1000000 / 3 * 10
      * (positionInfoValues.totalAssetsValues / (tokensValues[token] * x) * 100 = 300
      *
      *
      *
      * */
      const estimatedDailyDebtPerDebt = rewards / 10
      // @ts-ignore
      // eslint-disable-next-line consistent-return
      const gDays = (() => {
        if (allBalance === 0) {
          return 0
        }
        // @ts-ignore
        if (estimatedDailyDebtPerDebt * mintAllAmount === 0) {
          return Infinity
        }
        // @ts-ignore
        return toFixed(allBalance / (estimatedDailyDebtPerDebt * mintAllAmount), 0)
      })()
      // @ts-ignore
      // @ts-ignore

      // eslint-disable-next-line no-unused-expressions
      callback && callback(currentMintAmount, gDays, Number(mintAllAmount.toFixed(cptInfo.decimals)))
    }
  }, [])

  useEffect(() => {
    if (isSupportedChainId(chainId)) {
      initData()
    }
  }, [chainId, initData, refresh, account])

  useEffect(() => {
    (async () => {
      if (account && isSupportedChainId(chainId)) {
        const balance = await getTokenBalance(account)
        setWalletBalance(balance)
      }
    })()
  }, [getTokenBalance, account, refresh, chainId])
  return (
    <HashPowerTokenContext.Provider
      value={{
        hashPowerTokenList,
        refresh,
        setRefresh,
        setHashPowerTokenList,
        computed,
        walletTokenBalance,
        mintComputed,
      }}
    >{children}
    </HashPowerTokenContext.Provider>
  )
}

export { HashPowerTokenContext, HashPowerTokenProvider }
