import React  from 'react'
import { styled } from '@mui/material/styles'
import { BTitle } from 'components/util'
import Card from 'components/Card'
import Table, {StyledTableCell} from 'components/Table'
import useEpoch from "hooks/useEpoch";

const Wrapper = styled('div')`
  padding-right: 40px;
  height: 80vh;
  overflow: scroll;
`

const TableCard = styled(Card)`
  margin-bottom: 20px;
`


const columns =  [
  {
    id: 'epoch',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Epoch',
  },
  {
    id: 'start',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Start',
  },
  {
    id: 'factor',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Daily Mining Output',
  },
/*  {
    id: 'acc',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Acc ',
  }, */
];


function DashboardPage() {
  const [,epochList] = useEpoch()

  return (
    <>
      <BTitle>Dashboard</BTitle>
      <Wrapper>
        <TableCard>
          <Table
            dataSource={epochList}
            columns={columns}
            pagination
            content={(row,index, labelId)=>(
              <>
                <StyledTableCell component="th" id={labelId} scope="row">{row.epoch}</StyledTableCell>
                <StyledTableCell>{row.start}</StyledTableCell>
                <StyledTableCell>{row.factor} </StyledTableCell>
             {/*   <StyledTableCell>{row.acc}</StyledTableCell>
             */} </>
            )}
          />
        </TableCard>
      </Wrapper>
    </>

  )
}

export default DashboardPage

