import { Box, Typography } from '@mui/material'
import React, { useEffect, useMemo, useState } from 'react'
import SubdirectoryArrowRightIcon from '@mui/icons-material/SubdirectoryArrowRight'
import useHashPowerToken from 'hooks/useHashPowerToken'
import { useAppDispatch, useAppSelector } from 'state'
import { useWeb3React } from 'web3-react-core'
import { formatWBTC } from 'utils/formatTheValue'
import { BigNumber } from 'ethers'
import tokens from 'config/constants/tokens'
import oracleHub from 'utils/oracleHub'
import useSystemParams from 'hooks/useSystemParams'
import { tokensSymbol } from 'config/constants/types'
import { updataShortRateInfo } from 'state/Short/action'
import { useShortPendingAction } from 'state/hooks'
import { ShortThresholdValue } from 'config/ThresholdValue'

const PendingActionsComponents: React.FC<{
  newRate: number | string
  forecastGuaranteeDay: any
}> = ({ newRate, forecastGuaranteeDay }) => {
  const { hashPowerTokenList, computed } = useHashPowerToken()

  const currentCpt = useAppSelector((state) => state.collateralManage.currentCPT)
  const [forecastGuaranteeDays, setForecastGuaranteeDays] = useState(forecastGuaranteeDay)
  const [, setCpt] = useState(null)
  const { active } = useWeb3React()
  const [dilayReference, setDilayReference] = useState(0)
  const [hashPowerListInfoList, setHashPowerTokenListInfo] = useState<any>(hashPowerTokenList || [])
  const { pairWBTCAndUSDT, pairWBTCAndFBTC } = useSystemParams()
  const { positionInfoValues, avatarAssetList, token } = currentCpt
  const dispatch = useAppDispatch()
  const rateInfo = useAppSelector((state) => state.short.rateInfo)

  const { supplyAmount, shortAmount } = useShortPendingAction()

  const wbtcNew = useMemo(() => {
    const priceInfo = oracleHub({
      inputBalance: pairWBTCAndUSDT.USDT,
      outputBalance: pairWBTCAndUSDT.WBTC,
      inputAmount: avatarAssetList.USDT + Number(supplyAmount.get(tokensSymbol.USDT) || 0),
      inputDecimals: tokens.USDT.decimals,
      outputDecimals: tokens.WBTC.decimals,
    })
    // console.log(
    //   avatarAssetList,
    //   'avatarAssetListavatarAssetListavatarAssetListavatarAssetListavatarAssetListavatarAssetList',
    // )

    const priceInfo1 = oracleHub({
      inputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
      outputBalance: pairWBTCAndFBTC.WBTC,
      inputAmount: avatarAssetList[tokens[token].symbol] + Number(supplyAmount.get(tokensSymbol.FBTC20) || 0),
      inputDecimals: tokens[token].decimals,
      outputDecimals: tokens.WBTC.decimals,
    })
    return (
      Number(priceInfo.output) +
      Number(priceInfo1.output) +
      Number(avatarAssetList.WBTC) +
      Number(supplyAmount.get(tokensSymbol.WBTC) || 0)
    ).toFixed(tokens.WBTC.decimals)
  }, [avatarAssetList, pairWBTCAndFBTC, pairWBTCAndUSDT.USDT, pairWBTCAndUSDT.WBTC, supplyAmount, token])

  const wbtcOld = useMemo(() => {
    const priceInfo = oracleHub({
      inputBalance: pairWBTCAndUSDT.USDT,
      outputBalance: pairWBTCAndUSDT.WBTC,
      inputAmount: avatarAssetList.USDT,
      inputDecimals: tokens.USDT.decimals,
      outputDecimals: tokens.WBTC.decimals,
    })

    const priceInfo1 = oracleHub({
      inputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
      outputBalance: pairWBTCAndFBTC.WBTC,
      inputAmount: avatarAssetList[tokens[token].symbol],
      inputDecimals: tokens[token].decimals,
      outputDecimals: tokens.WBTC.decimals,
    })
    return (priceInfo.output + priceInfo1.output + avatarAssetList.WBTC).toFixed(tokens.WBTC.decimals)
  }, [avatarAssetList, pairWBTCAndFBTC, pairWBTCAndUSDT.USDT, pairWBTCAndUSDT.WBTC, token])

  const [, setRateVal] = useState<any>(0)

  const computedOldValue = useMemo(() => {
    const _value =
      hashPowerTokenList &&
      hashPowerTokenList[0] &&
      hashPowerTokenList[0].positionInfoValues &&
      hashPowerTokenList[0].positionInfoValues.cRate
    if (_value === 0 || !_value) {
      setRateVal(0)

      return 0
    }
    if (_value === 'Infinity' || _value === Infinity) {
      setRateVal('+')
      return '+'
    }
    setRateVal(_value)
    return _value
  }, [hashPowerTokenList])

  const computedNewValue = useMemo(() => {
    // console.log('newRate', newRate)
    const _value = newRate
    if (_value === 'Infinity' || _value === Infinity) {
      setRateVal('+')
      return '+'
    }

    if (_value === 0 || !_value) {
      setRateVal(0)
      return 0
    }

    if (forecastGuaranteeDays > 0) {
      // if (_value.toString().includes('%'))
      // eslint-disable-next-line no-invalid-regexp
      const flag = _value.toString().includes('%')
      const reg1 = new RegExp('%', 'g')
      const _newVal = _value.toString().replace(reg1, '')
      dispatch(
        updataShortRateInfo({
          rateInfo: {
            rate: flag ? _newVal : Number(_newVal) / 100,
            coverage: Number(forecastGuaranteeDays),
          },
        }),
      )
    }

    if (_value.toString().includes('%')) {
      return _value
    }

    return Number(_value) / 100
  }, [dispatch, forecastGuaranteeDays, newRate])

  useEffect(() => {
    if (active && hashPowerTokenList && hashPowerTokenList.length) {
      setCpt(hashPowerTokenList[0].token)
      if (hashPowerTokenList[0].positionInfoValues && hashPowerTokenList[0].positionInfoValues.cRate) {
        computed(supplyAmount.get(tokensSymbol.FBTC20) || 0, currentCpt, (ratio, gDays, _AllAmount) => {
          setForecastGuaranteeDays(gDays)

          setDilayReference(formatWBTC(_AllAmount * (currentCpt.rewards / 10)))
        })
      }
    }
  }, [hashPowerTokenList, currentCpt, computed, active, dispatch, rateInfo, supplyAmount])

  useEffect(() => {
    if (hashPowerTokenList && hashPowerTokenList[0]) {
      setHashPowerTokenListInfo(hashPowerTokenList)
    }
  }, [hashPowerTokenList])

  // const walletDataType = ['']
  // const newDays = useMemo(() => {
  //   console.log(forecastGuaranteeDay, '111111111')
  //   return forecastGuaranteeDay > 0 ? forecastGuaranteeDay : forecastGuaranteeDays
  // }, [forecastGuaranteeDay, forecastGuaranteeDays])

  const walletData = useMemo(
    () => [
      {
        id: 1,
        title: 'Collateral Ratio',
        type: 'ratio',
        value: {
          upVal: `${computedOldValue}%`,
          downVal: `${computedNewValue}%`,
        },
      },
      {
        id: 2,
        title: 'Prepaid Interest Coverage',
        type: 'coverage',
        value: {
          upVal: `${
            // eslint-disable-next-line no-nested-ternary
            currentCpt && Number(currentCpt.avatar) && currentCpt.positionInfoValues
              ? currentCpt.positionInfoValues.gDays === 'Infinity'
                ? '+'
                : currentCpt.positionInfoValues.gDays
              : 0
          } days`,
          downVal: `${(String(forecastGuaranteeDays) === 'Infinity' ? '+' : forecastGuaranteeDays) || 0} days`,
        },
      },
      {
        id: 3,
        title: 'Prepaid Interest',
        type: 'prepaidInterest',
        value: {
          upVal: `${wbtcOld} WBTC`,
          downVal: `${wbtcNew} WBTC`,
        },
      },
      {
        id: 4,
        title: 'Daily Paid Interest Reference',
        type: 'dailyPaid',
        value: {
          upVal: `${(positionInfoValues ? formatWBTC(positionInfoValues.estimatedDailyDebt) : 0) || 0} WBTC`,
          downVal: `${dilayReference} WBTC`,
        },
      },
      {
        id: 5,
        title: 'Outstanding Debt',
        type: 'debt',
        value: {
          upVal: `${
            hashPowerListInfoList[0] && hashPowerListInfoList[0].balance
              ? (BigNumber.from(hashPowerListInfoList[0].balance) as any) /
                (BigNumber.from(10) as any).pow(hashPowerListInfoList[0].decimals)
              : 0
          }`,
          downVal: `${
            hashPowerListInfoList[0] && hashPowerListInfoList[0].balance
              ? (BigNumber.from(hashPowerListInfoList[0].balance) as any) /
                  (BigNumber.from(10) as any).pow(hashPowerListInfoList[0].decimals) +
                Number(supplyAmount.get(tokensSymbol.FBTC20) || 0)
              : 0
          }`,
        },
      },
    ],
    [
      computedNewValue,
      computedOldValue,
      currentCpt,
      dilayReference,
      forecastGuaranteeDays,
      hashPowerListInfoList,
      positionInfoValues,
      supplyAmount,
      wbtcNew,
      wbtcOld,
    ],
  )
  return (
    <Box
      sx={{
        bgcolor: 'rgba(222, 241, 255, 1)',
        borderRadius: 1.8,
        mt: 4,
        p: 2,
        display: 'flex',
        flexDirection: 'column',
        gap: 1.2,
      }}
    >
      {walletData.map((item: any) => {
        return (
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }} key={item.id}>
            <Typography sx={{ fontSize: 12, color: 'rgba(0,0,0,0.5)' }}>{item.title}</Typography>

            <Box>
              <Typography sx={{ fontSize: 12, color: 'rgba(0,0,0,0.6)' }}>{item.value.upVal}</Typography>
              {(shortAmount && (
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <SubdirectoryArrowRightIcon sx={{ color: 'rgba(0,0,0,0.1)', width: 12, height: 12 }} />
                  <Typography
                    sx={{
                      fontSize: 10,
                      // eslint-disable-next-line no-nested-ternary
                      color: ['prepaidInterest', 'dailyPaid', 'debt'].includes(item.type)
                        ? '#2AA1FF'
                        : (item.type === 'ratio' && Number(computedNewValue) < ShortThresholdValue.ratio) ||
                          (item.type === 'coverage' && forecastGuaranteeDays < ShortThresholdValue.days)
                        ? '#FF4D00'
                        : '#2AA1FF',
                    }}
                  >
                    {item.value.downVal}
                  </Typography>
                </Box>
              )) ||
                null}
            </Box>
          </Box>
        )
      })}
    </Box>
  )
}
export default PendingActionsComponents
