import { createReducer } from '@reduxjs/toolkit'
import {
  updataShortActiveToken,
  setShortPendingAction,
  updataShortRateInfo,
  updateActionInfo,
  updateActiveAmount,
  updateActiveActionIndex,
  updateActiveTab,
} from './action'

export enum TokenType {
  SHORT = 'Short',
  NEUTRALIZE = 'Neutralize',
  SUPPLY = 'Supply',
  WithDraw = 'Withdraw',
}
export enum ActionType {
  ADD = 'add',
  REMOVE = 'remove',
}
export interface PendingAction {
  tokenType: TokenType
  token: string
  amount: number
  price: number
  type: ActionType
  neutralizeType?: string
}
export interface NeetralizePendingAction {
  tokenType: TokenType
  token: string
  amount: number
  price: number
  type: ActionType
  neutralizeType: string
}

export interface PendingActionState {
  activeToken: string
  activeAmount: any
  activeInfo: {
    token?: string
    tokenType?: TokenType
  }
  activeTab: number
  activeIndex: number
  pendingActions: Map<string, PendingAction[]>
  rateInfo: {
    rate: any
    coverage: any
  }
}
const initalState: PendingActionState = {
  activeToken: '',
  activeAmount: '',
  activeIndex: -1,
  activeInfo: {},
  pendingActions: new Map(),
  activeTab: 1,
  rateInfo: {
    rate: 0,
    coverage: 0,
  },
}

export default createReducer(initalState, (builder) => {
  return builder
    .addCase(updataShortActiveToken, (state, { payload }) => {
      state.activeToken = payload.activeToken
    })
    .addCase(updateActionInfo, (state, { payload }) => {
      console.log(payload, 'payloadpayloadpayload')
      state.activeInfo = payload.activeInfo
    })

    .addCase(updateActiveActionIndex, (state, { payload }) => {
      state.activeIndex = payload.index
    })
    .addCase(updateActiveAmount, (state, { payload }) => {
      state.activeAmount = payload.amount
    })
    .addCase(setShortPendingAction, (state, { payload }) => {
      state.pendingActions = payload.actions
    })
    .addCase(updataShortRateInfo, (state, { payload }) => {
      state.rateInfo = payload.rateInfo
    })
    .addCase(updateActiveTab, (state, { payload }) => {
      state.activeTab = payload.index
    })
})
