

import {utils, BigNumber, BigNumberish} from 'ethers'


/**
 * @description Convert small units to large units
 */
export function toLargeUnits(val: number | BigNumber, unitName?: BigNumberish | string): string {
  return utils.formatUnits(val, unitName)
}



/**
 * @description Convert large units to small units
 */
export function toSmallUnits(val: string, unitName?: BigNumberish): BigNumber {
  return utils.parseUnits(val, unitName)
}
