import React, { useCallback, useEffect, useState } from 'react'
import { Grid, Divider } from '@mui/material'
import { styled } from '@mui/material/styles'
import { BTitle, Title } from 'components/util'
import { useWeb3React } from 'web3-react-core'
import Tabs from 'components/Tabs'
import Card from 'components/Card'
import { Button } from 'components/uikit'
import { BigNumber } from '@ethersproject/bignumber'
import contracts from 'config/constants/contracts'
import { formatWBTC, formatCurrency, toFixed } from 'utils/formatTheValue'
import { getContract, getSigner } from 'utils/web3React'
import useToast from 'hooks/useToast'
import useHashPowerToken from 'hooks/useHashPowerToken'
import MinerABI from 'config/abi/Miner.json'
import HoldingsWallet from './components/HoldingsWallet'
import OutstandingDebt from './components/OutstandingDebt'
import tokens from '../../config/constants/tokens'
import useSystemParams from '../../hooks/useSystemParams'
import useEpoch from '../../hooks/useEpoch'

const Wrapper = styled('div')`
  width: 100%;
  height: 100%;
  overflow: scroll;
`
const Box = styled('div')`
  box-sizing: border-box;
  margin-top: 26px;
  padding-right: 44px;
  min-width: 1024px;
  padding-bottom: 100px;

  &::-webkit-scrollbar {
    display: none;
  }
`

const CommonCard = styled(Card)`
  padding: 30px;
  height: 100%;

  &.rightPanel {
    h4 {
      line-height: 20px;
    }
  }
  h4 {
    font-family: PingFangSC-Medium, sans-serif;
    font-weight: 480;
    font-size: 16px;
    line-height: 22px;
    color: black;
  }

  p {
    font-family: PingFangSC-Semibold, sans-serif;
    font-size: 40px;
    font-weight: 550;
    line-height: 1;
    color: #6ed6f3;
    margin-top: 12px;
    margin-bottom: 8px;
    letter-spacing: 1px;
    sub {
      font-size: 16px;
    }
  }
  span {
    font-family: PingFangSC-Regular, sans-serif;
    font-size: 14px;
  }
`
const SubTitle = styled(Title)`
  margin-bottom: 14px;
  font-family: PingFangSC-Semibold, sans-serif;
  font-weight: 550;
  font-size: 22px;
  line-height: 31px;
  color: #000000;
`

const ClaimButton = styled(Button)`
  background: linear-gradient(90deg, #61c2f7 0%, #7ae9f0 100%) !important;
  border-radius: 20px !important;
  box-shadow: none !important;
  height: 30px !important;
  line-height: 30px !important;
  color: #000 !important;
  font-size: 14px !important;
  display: block !important;

  &.disabled {
    background: #ddd !important;
    color: #eee !important;
  }
`

// const TableCard = styled(Card)`
//   margin-bottom: 20px;
// `

const { claimHelper } = contracts

function MyPage() {
  const [currentEpoch] = useEpoch()
  const [claimStatus, setClaimStatus] = useState(false)
  const { hashPowerTokenList, walletTokenBalance, setRefresh } = useHashPowerToken()
  const { signer } = useSystemParams()
  const { chainId, account, library } = useWeb3React()
  const { toastError, toastSuccess, toastInfo } = useToast()
  const [walletTotal, setWalletTotal] = useState(0)
  const [avatarTotal, setAvatarTotal] = useState(0)
  const [dailyInterestRewards, setDailyInterestRewards] = useState(0)
  const [dailyObligation, setDailyObligation] = useState(0)
  const [rewardsData, setRewardsData] = useState(null)
  const [allRewards, setAllRewards] = useState([])

  const claimAll = useCallback(async () => {
    if (signer) {
      const claimContract = getContract(signer, claimHelper.abi, claimHelper.address[chainId])
      if (claimContract) {
        try {
          const params = []
          // eslint-disable-next-line no-restricted-syntax
          for (const item of hashPowerTokenList) {
            params.push([item.address[chainId], account])
            if (item.avatar && Number(item.avatar) > 0) {
              params.push([item.address[chainId], item.avatar])
            }
          }
          setClaimStatus(true)
          const txInfo = await claimContract.multipleClaim(params)
          if (txInfo) {
            toastInfo('Submitted')
            await txInfo.wait()
            toastSuccess('Success')
            setRefresh((state) => !state)
            setClaimStatus(false)
          }
        } catch (e) {
          setClaimStatus(false)
          toastError('Fail', e.message)
        }
      }
    }
  }, [account, chainId, hashPowerTokenList, setRefresh, signer, toastError, toastInfo, toastSuccess])

  // eslint-disable-next-line consistent-return
  const pendingReward = useCallback(
    // eslint-disable-next-line consistent-return
    async (abi, updaterAddress, token, address, decimals, balance, lastModifiedEpoch?) => {
      if (library && chainId) {
        const _signer = getSigner(library, address)
        const updater = getContract(_signer, abi, updaterAddress)
        const miner = getContract(_signer, MinerABI, tokens['F(BTC,20)'].address[chainId])

        if (updater && walletTokenBalance && miner) {
          const holderInfos = await miner.holderInfos(address)

          if (lastModifiedEpoch) {
            // @ts-ignore
            return updater.pendingReward(balance, holderInfos[0], holderInfos[1], holderInfos[2])
          }
          // @ts-ignore
          return updater.pendingReward(
            (BigNumber.from(10) as any).pow(decimals) * balance,
            holderInfos[0],
            holderInfos[1],
            holderInfos[2],
          )
        }
      }

      // const contract = getContract(signer, abi, )
    },
    [chainId, library, walletTokenBalance],
  )
  useEffect(() => {
    if (hashPowerTokenList && walletTokenBalance) {
      // eslint-disable-next-line @typescript-eslint/no-extra-semi
      ;(async () => {
        const data = {}
        // eslint-disable-next-line no-restricted-syntax
        for (const item of hashPowerTokenList) {
          // eslint-disable-next-line no-await-in-loop
          const [avatarRewards, walletRewards] = await Promise.all([
            pendingReward(
              item.blockUpdater.abi,
              item.blockUpdater.address[chainId],
              item.token,
              item.position,
              item.decimals,
              (item.avatarAssetList && item.avatarAssetList[item.token]) || 0,
            ),
            pendingReward(
              item.blockUpdater.abi,
              item.blockUpdater.address[chainId],
              item.token,
              account,
              item.decimals,
              walletTokenBalance[item.token] || 0,
            ),
          ])
          data[item.token] = {
            walletRewards,
            avatarRewards,
            walletEstimateDailyReward: (item.rewards / 10) * walletTokenBalance[item.token],
            avatarEstimateDailyReward: item.avatarAssetList
              ? (item.rewards / 10) * item.avatarAssetList[item.token]
              : 0,
          }
        }

        setRewardsData(data)
      })()
    }
  }, [account, chainId, hashPowerTokenList, pendingReward, walletTokenBalance])

  useEffect(() => {
    if (hashPowerTokenList && walletTokenBalance && rewardsData) {
      let _walletTotal = 0
      let _avatarTotal = 0
      let _dailyInterestRewards = 0
      let _dailyObligation = 0
      const _rewardsAll = []
      // eslint-disable-next-line guard-for-in,no-restricted-syntax
      for (const tokenData of hashPowerTokenList) {
        const item = tokenData
        let _walletRewards = 0
        let _walletRewardsUSDT = 0
        if (item.WBTCRouter && Object.keys(item.WBTCRouter).length) {
          // eslint-disable-next-line no-unused-expressions
          // @ts-ignore
          _walletTotal +=
            walletTokenBalance[tokenData.token] * formatWBTC(item.tokensValues[tokenData.token]) * item.WBTCRouter.USDT
        }

        if (item.avatarAssetList) {
          _avatarTotal +=
            item.avatarAssetList[tokenData.token] *
            formatWBTC(item.tokensValues[tokenData.token]) *
            item.WBTCRouter.USDT
        }
        if (rewardsData[item.token]) {
          _dailyInterestRewards +=
            (formatWBTC(rewardsData[item.token].walletEstimateDailyReward) * 1 +
              formatWBTC(rewardsData[item.token].avatarEstimateDailyReward) * 1) *
            item.WBTCRouter.USDT

          // @ts-ignore
          _dailyObligation +=
            ((formatWBTC(item.rewards) / 10) * item.balance * item.WBTCRouter.USDT) /
            (BigNumber.from(10) as any).pow(item.decimals)
          _walletRewards +=
            formatWBTC(rewardsData[item.token].walletRewards[0]) * 1 +
            formatWBTC(rewardsData[item.token].avatarRewards[0]) * 1
          _walletRewardsUSDT +=
            (formatWBTC(rewardsData[item.token].walletRewards[0]) * 1 +
              formatWBTC(rewardsData[item.token].avatarRewards[0]) * 1) *
            item.WBTCRouter.USDT
          _rewardsAll.push({
            rewards: _walletRewards,
            usdt: _walletRewardsUSDT,
          })
        }
        if (Number(item.position)) {
          // @ts-ignore
          // @ts-ignore
          // @ts-ignore
          // @ts-ignore
        }
      }

      setWalletTotal(_walletTotal)
      setAvatarTotal(_avatarTotal)
      setDailyInterestRewards(_dailyInterestRewards)
      setDailyObligation(_dailyObligation)
      setAllRewards(_rewardsAll)
    }
  }, [hashPowerTokenList, rewardsData, walletTokenBalance])

  return (
    <Wrapper>
      <BTitle>My Page</BTitle>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <CommonCard>
                  <SubTitle>Holdings - Synthetic Hashpower Token</SubTitle>
                  <Grid container>
                    <Grid item xs={6}>
                      <h4>Hashpower Token in Wallet </h4>
                      <p>
                        <sub></sub>${formatCurrency(walletTotal)}
                      </p>
                    </Grid>
                    <Grid item xs={6}>
                      <h4>Hashpower Token in Collateral</h4>
                      <p>
                        <sub></sub>${formatCurrency(avatarTotal)}
                      </p>
                    </Grid>
                  </Grid>
                </CommonCard>
              </Grid>
              <Grid item xs={12} style={{ paddingTop: 20 }}>
                <CommonCard style={{ paddingBottom: 33 }}>
                  <SubTitle>Claimable Rewards</SubTitle>
                  <Grid container spacing={2}>
                    <Grid item xs={6}>
                      {/* {
                        allRewards?
                          allRewards.map(item=>{
                            return (
                              <Grid container style={{ marginBottom: 20, lineHeight: '20px', fontSize: '14px' }}>
                                <Grid item xs={6}>
                                  <span>{toFixed(item.rewards, 'WBTC')} WBTC</span>
                                </Grid>
                                <Grid item xs={6} dir="rtl">
                                  <span>${toFixed(item.usdt, '')}</span>
                                </Grid>
                              </Grid>
                            )
                          }): null
                      } */}
                      <Grid container style={{ marginBottom: 18, lineHeight: '20px', fontSize: '14px' }}>
                        <Grid item xs={6}>
                          <span>{allRewards[0] ? toFixed(allRewards[0].rewards, 'WBTC') : 0} WBTC</span>
                        </Grid>
                        <Grid item xs={6} dir="rtl">
                          <span>${allRewards[0] ? toFixed(allRewards[0].usdt, '') : 0}</span>
                        </Grid>
                      </Grid>
                      <Divider style={{ marginBottom: 18 }} />
                      <Grid container style={{ marginBottom: 20, lineHeight: '20px', fontSize: '14px' }}>
                        <Grid item xs={6}>
                          <span>Total</span>
                        </Grid>
                        <Grid item xs={6} dir="rtl">
                          {/* eslint-disable-next-line no-nested-ternary */}
                          <span>
                            $
                            {allRewards.length
                              ? toFixed(
                                  allRewards.length > 1
                                    ? allRewards.reduce((a, b) => a.usdt + b.usdt)
                                    : allRewards.reduce((a, b) => a.usdt + b.usdt).usdt,
                                  '',
                                )
                              : 0}
                            
                          </span>
                        </Grid>
                      </Grid>
                    </Grid>
                    <Grid item xs={6} style={{ paddingTop: 0, paddingLeft: 97 }}>
                      <img
                        src="/images/eth.png"
                        style={{ width: 108, height: 108, margin: '5px 0 0 35px', display: 'block' }}
                        alt="illustration"
                      />
                      <ClaimButton
                        disabled={claimStatus}
                        style={{ margin: '30px 0 0' }}
                        className={`${claimStatus ? 'disabled' : ''}`}
                        onClick={claimAll}
                      >
                        Claim All Rewards
                      </ClaimButton>
                    </Grid>
                  </Grid>
                </CommonCard>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={4}>
            <CommonCard className="rightPanel">
              <SubTitle>Epoch {currentEpoch ? Number(currentEpoch) - 1 : 0} Balance</SubTitle>
              <Grid container>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <h4>Rewards Earned</h4>
                  <p>
                    <sub></sub>${formatCurrency(dailyInterestRewards)}
                  </p>
                </Grid>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <h4>Interest Paid</h4>
                  <p>
                    <sub></sub>${formatCurrency(dailyObligation)}
                  </p>
                </Grid>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <Divider />
                </Grid>

                <Grid item xs={12} style={{ marginBottom: 36 }}>
                  <h4>Net Rewards</h4>
                  <p>
                    <sub></sub>
                    {formatCurrency(dailyInterestRewards - dailyObligation).includes('-')
                      ? formatCurrency(dailyInterestRewards - dailyObligation).replace('-', '-$')
                      : `$${formatCurrency(dailyInterestRewards - dailyObligation)}`}
                  </p>
                </Grid>
              </Grid>
            </CommonCard>
          </Grid>
        </Grid>
        <div style={{ width: '100%' }}>
          <Tabs
            tabsStyle={{
              minHeight: 36,
              paddingLeft: 21,
            }}
            tabStyle={{
              paddingTop: 0,
              paddingBottom: 0,
              minHeight: 32,
              lineHeight: '32px',
            }}
            tab={[
              {
                label: 'All',
                component: (
                  <>
                    <HoldingsWallet rewardsData={rewardsData} />
                    {/* <HoldingsAvatar rewardsData={rewardsData} />    / */}
                    <OutstandingDebt />
                  </>
                ),
              },
              {
                label: 'Holdings',
                component: <HoldingsWallet rewardsData={rewardsData} />,
              },
              // {
              //   label: 'Holdings - Avatar',
              //   component: <HoldingsAvatar rewardsData={rewardsData} />
              // },
              {
                label: 'Outstanding Debt',
                component: <OutstandingDebt />,
              },
            ]}
          />
        </div>
      </Box>
    </Wrapper>
  )
}

export default React.memo(MyPage)
