export const Icon = styled('img')`
  width: 53px;
  height: 45px;
`

const Menu = (props) => {
  const { account, active } = useWeb3React()
  const { login, logout } = useAuth()
  const { onPresentConnectModal } = useWalletModal(login, logout, account)
  const { location, history, style } = props
  const [currentUrl, setCurrentUrl] = useState(location.pathname || '')
  useEffect(() => {
    if (location.pathname) {
      setCurrentUrl(location.pathname)
    }
  }, [location])
  const onChange = useCallback(
    (info) => {
      if (info.href === location.pathname) {
        return
      }
      setCurrentUrl(info.href)
      history.push(info.href)
      // @ts-ignore
      if (!active) {
        onPresentConnectModal()
      }
    },
    [active, history, location.pathname, onPresentConnectModal],
  )
  return (
    <Wrapper style={style}>
      {links.map((it, i) => {
        const children =
          (it.children && (
            // eslint-disable-next-line react/no-array-index-key
            <ul key={`${i}qwe`}>
              {it.children.map((item, index) => {
                return (
                  // eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-noninteractive-element-interactions,react/no-array-index-key
                  <li
                    // eslint-disable-next-line react/no-array-index-key
                    key={index}
                    data-href={item.href}
                    onClick={(e) => {
                      e.stopPropagation()
                      onChange(item)
                    }}
                  >
                    <div
                      className={
                        currentUrl === item.href || (item.label === 'Collateral' && item.href.includes('manage'))
                          ? 'active'
                          : ''
                      }
                    >
                      <span> {item.label}</span>
                    </div>
                  </li>
                )
              })}
            </ul>
          )) ||
          null

        return (
          // eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-noninteractive-element-interactions,react/no-array-index-key
          <li
            // eslint-disable-next-line react/no-array-index-key
            key={i}
            data-href={it.href}
            onClick={() => {
              onChange(it)
            }}
          >
            {/*  @ts-ignore */}
            <div
              className={
                (currentUrl && currentUrl === it.href) ||
                (it.label === 'Collateral' && currentUrl.includes('/manage' || '/collateral')) ||
                (it.label === 'Mint' && currentUrl.startsWith('/mint'))
                  ? 'active'
                  : '' || (it.label === 'Quick Mode' && currentUrl.startsWith('/quickMode') ? 'active' : '')
              }
            >
              <it.icon width="32" height="32" style={{ fill: 'none' }} />
              <span>{it.label}</span>
            </div>
            {children}
          </li>
        )
      })}
    </Wrapper>
  )
}

function Slider(props) {
  return (
    <div>
      <Menu {...props} />
    </div>
  )
}

export default withRouter(Slider)
