import React, { useCallback, useEffect, useState } from 'react'
import Methods from 'components/Methods'
import { BigNumber } from '@ethersproject/bignumber'
import { utils } from 'ethers'

import { AlertButton, Approve, FlexWrap } from 'components/util'
import { formatWBTC, formatSnNumber } from 'utils/formatTheValue'
import tokens, { poolData } from 'config/constants/tokens'
import contracts from 'config/constants/contracts'
import useToast from 'hooks/useToast'
import DataWrapper from 'components/DataWrapper'
import usePosContract from 'hooks/usePosContract'
import { useAppSelector } from 'state'
import useHashPowerToken from '../../../hooks/useHashPowerToken'
import useSystemParams from '../../../hooks/useSystemParams'
import { AvatarIcon, GuaranteeIcon } from '../../../components/uikit'
import { ChainTxUrl } from '../../../config/ChainInfo'

const { fbtc20mint } = contracts

function AddGuaranteeFromAvatar(props) {
  const { active, approveStatus, chainId, onApprove } = props
  const { signer } = useSystemParams()
  const { setRefresh } = useHashPowerToken()
  const currentCpt = useAppSelector((state) => state.collateralManage.currentCPT)
  const { positionInfoValues, position, avatarAssetList } = currentCpt
  const [transferVal, setTransferVal] = useState<number>()
  const [guaranteeBalance, setGuaranteeBalance] = useState(null)
  const [isDisabled, setDisabled] = useState(true)
  const { toastSuccess, toastError, toastInfo } = useToast()
  const posContract = usePosContract(active, signer)
  const [params, setParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%',
  })

  const computed = useCallback(
    (value) => {
      // @ts-ignore
      const totalAssetsValues =
        (BigNumber.from(positionInfoValues.totalAssetsValues) as any) * 1 +
        value * (BigNumber.from(10).pow(tokens.WBTC.decimals) as any) * 1
      // @ts-ignore
      const interestGuaranteed =
        (BigNumber.from(positionInfoValues.interestGuaranteed) as any) * 1 +
        value * (BigNumber.from(10).pow(tokens.WBTC.decimals) as any) * 1
      /*
       * totalAssetsValues + value / shortBalance
       *
       * */
      setParams((state) => ({
        ...state,
        // @ts-ignore
        futureCRate: (totalAssetsValues / BigNumber.from(positionInfoValues.shortBalanceValue)) * 100,
        // @ts-ignore
        futureGDays: Math.floor(
          (interestGuaranteed - (BigNumber.from(positionInfoValues.totalObligation) as any)) /
            (BigNumber.from(positionInfoValues.estimatedDailyDebt) as any),
        ),
      }))
    },
    [positionInfoValues],
  )
  const changeTransferVal = useCallback(
    (e, type) => {
      if (type > 0) {
        setTransferVal(avatarAssetList.WBTC)
        computed(avatarAssetList.WBTC)
      } else {
        setTransferVal(e.target.value)
        computed(e.target.value)
        if (e.target.value && e.target.value > 0) {
          setDisabled(false)
        } else {
          setDisabled(true)
        }
      }
    },
    [avatarAssetList, computed],
  )
  const onSubmit = useCallback(async () => {
    if (active && position) {
      try {
        setDisabled(true)
        const inter = new utils.Interface(fbtc20mint.abi)
        // @ts-ignore
        const data = inter.encodeFunctionData('addGuarantee', [
          (BigNumber.from(10).pow(tokens.WBTC.decimals) as any).toString() * transferVal || '0',
          true,
        ])
        if (posContract) {
          // @ts-ignore
          const txInfo = await posContract.delegateCall(fbtc20mint.address[chainId], data)
          if (txInfo) {
            toastInfo(
              'Submitted ',
              <>
                <p style={{ marginBottom: 24 }}>Waiting for confirmation</p>
                <FlexWrap style={{ flexDirection: 'row-reverse' }}>
                  <AlertButton
                    onClick={() => {
                      window.open(`${ChainTxUrl}/${txInfo.hash}`)
                    }}
                  >
                    View on Explorer
                  </AlertButton>
                </FlexWrap>
              </>,
            )
            const result = await txInfo.wait()
            if (result) {
              toastSuccess(
                'Success',
                <>
                  <p style={{ marginBottom: 24 }}>Add settlement success</p>
                  <FlexWrap style={{ flexDirection: 'row-reverse' }}>
                    <AlertButton
                      onClick={() => {
                        window.open(`${ChainTxUrl}/${txInfo.hash}`)
                      }}
                    >
                      View on Explorer
                    </AlertButton>
                  </FlexWrap>
                </>,
              )
              setRefresh((state) => !state)
              setDisabled(false)
            }
          }
        }
      } catch (error) {
        const { message } = error as Error
        setDisabled(false)
        toastError('Failed', message)
      }
    }
  }, [active, position, transferVal, posContract, chainId, toastInfo, toastSuccess, setRefresh, toastError])

  useEffect(() => {
    if (currentCpt && Number(currentCpt.position) && currentCpt.positionInfoValues) {
      setGuaranteeBalance(formatWBTC(BigNumber.from(currentCpt.positionInfoValues.guaranteeValues)))
    }
  }, [currentCpt])

  useEffect(() => {
    if (active && Number(position) && positionInfoValues && positionInfoValues.gDays) {
      setParams({
        nowGDays:
          Number(positionInfoValues.gDays ?? positionInfoValues.gDays) === Infinity ? '+' : positionInfoValues.gDays,
        nowCRate: Number(positionInfoValues.cRate) === Infinity ? '+' : `${positionInfoValues.cRate}%`,
      })
      computed(0)
    }
  }, [active, computed, position, positionInfoValues])

  useEffect(() => {
    if (transferVal > 0) {
      setDisabled(false)
    }
  }, [transferVal])

  return (
    <>
      <Methods
        SelectIcon={AvatarIcon}
        options={poolData}
        rightBalance={guaranteeBalance || 0}
        rightOptions={poolData}
        leftBalance={avatarAssetList ? formatSnNumber(avatarAssetList.WBTC) : 0}
        rightSelectedValue={42}
        rightSymbol="WBTC"
        selectValue={42}
        selectedBalance={100}
        selectedSymbol="WBTC"
        RightIcon={GuaranteeIcon}
        leftInputValue={transferVal}
        rightInputValue={transferVal}
        selectInput={(e) => {
          changeTransferVal(e, 0)
        }}
        onMax={() => {
          changeTransferVal({}, 1)
        }}
      />
      <DataWrapper
        data={[
          // eslint-disable-next-line no-sequences,no-nested-ternary
          [
            'Collateral Ratio',
            params.nowCRate.includes('NaN') ? '0%' : params.nowCRate,
            params.nowCRate.includes('NaN') ? '0%' : params.nowCRate,
          ],

          // eslint-disable-next-line no-sequences,no-nested-ternary
          [
            'Prepaid Interest Coverage',
            `${params.nowGDays || 0} days`,
            `${
              // eslint-disable-next-line no-nested-ternary
              params.futureGDays
                ? params.futureGDays && params.futureGDays.toString() === 'Infinity'
                  ? '+'
                  : params.futureGDays
                : '0'
            } days`,
          ],
        ]}
      />
      <Approve
        style={{ boxShadow: 'none' }}
        disabled={!approveStatus ? false : isDisabled}
        onClick={approveStatus ? onSubmit : onApprove}
      >
        {approveStatus ? 'Submit' : 'Approve'}
      </Approve>
    </>
  )
}

export default AddGuaranteeFromAvatar
