const headCells = [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  /*  {
      id: 'liquidity',
      numeric: true,
      disablePadding: false,
      isSort: true,
      label: 'Liquidity',
    }, */
  {
    id: 'outstandingDebt',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Outstanding Debt',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Ratio',
  },
  {
    id: 'guaranteeDays',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Prepaid Interest Coverage',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
]

function Collateral() {
  const { hashPowerTokenList, setRefresh, refresh } = useHashPowerToken()
  const [searchVal, setSearchVal] = useState()
  const [holderText, setPlaceholder] = useState<string>('Ticker')
  const [dataSource, setDataSource] = useState([])
  const onSearch = useCallback((event) => {
    setSearchVal(event.target.value)
    setDataSource((state) => {
      if (event.target.value) {
        return state.filter((it) => it.token.includes(event.target.value))
      }
      return hashPowerTokenList
    })
  }, [])
  useEffect(() => {
    if (hashPowerTokenList) {
      setDataSource(hashPowerTokenList)
    }
  }, [hashPowerTokenList])

  const onSwitch = useCallback(
    (event, val) => {
      if (val) {
        setDataSource((state) => {
          return state.filter((it) => Number(it.position) > 0)
        })
      } else {
        setDataSource(hashPowerTokenList)
      }
    },
    [hashPowerTokenList],
  )

  return (
    <div
      style={{
        width: '100%',
        height: '100%',
        paddingRight: 100,
      }}
    >
      <Title>Collateral</Title>
      <Card style={{ height: 'calc(100% - 200px)' }} key={refresh}>
        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase
            type="search"
            placeholder={holderText}
            inputProps={{ 'aria-label': 'search' }}
            value={searchVal}
            onInput={onSearch}
            onFocus={() => {
              setPlaceholder('')
            }}
            onBlur={() => {
              setPlaceholder('Ticker')
            }}
          />
          <SwitchButton onChange={onSwitch} /> <span className="opened">Enabled</span>
        </Search>

        <Table
          key={refresh}
          dataSource={dataSource || []}
          columns={headCells}
          pagination={false}
          content={(row, index, labelId) => (
            <React.Fragment key={index}>
              <StyledTableCell component="th" id={labelId} scope="row">
                <img
                  src={TokensIcon[row.ticker]}
                  style={{ display: 'block', marginRight: 7, float: 'left' }}
                  alt="icon"
                />
                {row.ticker}
              </StyledTableCell>
              {/* @ts-ignore */}
              <StyledTableCell>
                {row.positionInfoValues && Object.keys(row.positionInfoValues).length
                  ? (BigNumber.from(row.balance) as any) / (BigNumber.from(10) as any).pow(row.decimals)
                  : 0}
              </StyledTableCell>
              <StyledTableCell>
                {row.positionInfoValues && Object.keys(row.positionInfoValues).length
                  ? row.positionInfoValues.cRate.includes('NaN')
                    ? 0
                    : row.positionInfoValues.cRate === 'Infinity'
                    ? '+'
                    : row.positionInfoValues.cRate + '%'
                  : 0}
              </StyledTableCell>
              <StyledTableCell>
                {row.positionInfoValues && Object.keys(row.positionInfoValues).length && row.positionInfoValues.gDays
                  ? row.positionInfoValues.gDays === 'Infinity'
                    ? '+'
                    : row.positionInfoValues.gDays
                  : 0}{' '}
                days
              </StyledTableCell>
              <StyledTableCell>
                <Action row={row} refresh={setRefresh} />
              </StyledTableCell>
            </React.Fragment>
          )}
        />
      </Card>
    </div>
  )
}

export default Collateral
