import React, { useCallback, useEffect, useMemo } from 'react'
import { useAppSelector } from 'state'
import { useShortPendingAction } from 'state/hooks'
import { MarkSlider, PrettoSlider, PrettoSlidertwo, SliderWrapper, SlidtRangeWrapper } from './styledComp'

export default function CustomizedSlider(props) {
  const { value } = props

  const [currentInfo, setCurrentInfo] = React.useState<any>({
    rate: 0,
    coverage: 0,
  })
  const rateInfo = useAppSelector((state) => state.short.rateInfo)
  const { pendingActionList, shortAmount } = useShortPendingAction()
  useEffect(() => {
    if (rateInfo) {
      setCurrentInfo(rateInfo)
    }
  }, [pendingActionList, rateInfo, shortAmount])
  const computedValueRatio = useCallback((val: any) => {
    const _value = val
    if (_value === 0 || !_value) {
      return 0
    }
    if (_value === 'Infinity' || _value === Infinity) {
      return '+'
    }
    return `${_value}%`
  }, [])

  const computedValueCoverage = useCallback((val: any) => {
    const _value = val
    if (_value === 0 || !_value) {
      return 0
    }
    if (_value === 'Infinity' || _value === Infinity) {
      return '+'
    }
    return `${_value}`
  }, [])
  const showInfo = useMemo(() => {
    return {
      rate:
        // eslint-disable-next-line no-nested-ternary
        (rateInfo.rate as any) === 'Infinity' || (rateInfo.rate as any) === Infinity || Number(rateInfo.rate) > 300
          ? 300
          : rateInfo.rate,
      coverage:
        (rateInfo.coverage as any) === 'Infinity' ||
        (rateInfo.coverage as any) === Infinity ||
        Number(rateInfo.coverage) > 300
          ? 300
          : Number(rateInfo.coverage),
    }
  }, [rateInfo.coverage, rateInfo.rate])
  return (
    <>
      {currentInfo && (
        <div className="range_wrap">
          <SliderWrapper className="custom-slider-wrapper">
            <MarkSlider
              valueLabelDisplay="off"
              aria-label="pretto slider"
              defaultValue={Number(rateInfo.rate)}
              marks={value.marks1}
              max={300}
            />
          </SliderWrapper>
          {value.marks1.length > 0 ? (
            <SlidtRangeWrapper value={Number(rateInfo.rate)}>
              <PrettoSlider
                disabled
                aria-label="pretto slider"
                defaultValue={120}
                value={showInfo.rate}
                max={300}
                valueLabelDisplay="on"
                marks={value.marks}
                sx={{
                  ".css-1qlnp9f-MuiSlider-root .MuiSlider-markLabel[data-index='1']": {
                    pr: 70,
                  },
                  ".css-1qlnp9f-MuiSlider-root .MuiSlider-markLabel[data-index='2']": {
                    pl: 70,
                  },
                }}
              />
              <div className="custom_range_flag">
                <div className="range_val">{`${computedValueRatio(Number(rateInfo.rate))}`}</div>
              </div>
            </SlidtRangeWrapper>
          ) : (
            <SlidtRangeWrapper className="feature_item" value={Number(rateInfo.coverage)}>
              <PrettoSlidertwo
                disabled
                aria-label="pretto slider"
                defaultValue={20}
                value={showInfo.coverage}
                valueLabelDisplay="on"
                marks={value.marks}
                max={300}
              />
              <div className="custom_range_flag">
                <div className="range_val">{`${computedValueCoverage(rateInfo.coverage)}`}</div>
              </div>
            </SlidtRangeWrapper>
          )}
        </div>
      )}
    </>
  )
}
